export enum ExpandingType {
  AUTOMATIC = 'automatic', // application request to open vim hub based on events (encounterStart)
  VIM_HUB_MANUAL = 'manual', // user click on app at vim hub panel
  VIM_APP_MANUAL = 'vim-app-manual', // application request to open vim hub based on user action
  LAUNCH_BUTTON_CLICK = 'launch-button-click', // Injected launch button of the application was clicked
  VIM_NOTIFICATION_MANUAL = 'vim-notification-manual', // user click on notification
}
