import { isEmpty, map, keyBy, mapValues, cloneDeep } from 'lodash-es';
import { BareboneStorage } from '@getvim-os/barebone/storage';
import { Logger, LoggerFactory } from '@getvim-os/logger';
import { ApplicationManifest } from '@getvim-os/types';
import { inject, injectable } from 'inversify';
import { OsState } from '..';
import { BaseStateManager } from '../lib/baseStateManager.service';
import { InternalApi } from '@/internal-api';
import { Dictionary } from '@reduxjs/toolkit';

export const AUTO_POPUP_DELAY = 1000;

export const APP_OVERRIDES_KEY = 'appOverrides';

@injectable()
export class ApplicationOverridesManager extends BaseStateManager {
  private logger: Logger;
  constructor(
    @inject(OsState) osState: OsState,
    @inject(LoggerFactory) loggerFactory: LoggerFactory,
    @inject(BareboneStorage) private storage: BareboneStorage,
    @inject(InternalApi) private internalApi: InternalApi,
  ) {
    super(osState);
    this.logger = loggerFactory.create('ApplicationOverridesManager');
  }

  public async getApplicationOverrides(
    orgManifests: ApplicationManifest[],
  ): Promise<{ [id: string]: ApplicationManifest | null }> {
    try {
      const orgManifestsMap: Dictionary<ApplicationManifest> = keyBy(orgManifests, 'metadata.id');

      const appOverrides: Record<
        string,
        {
          manifest?: ApplicationManifest;
          url?: string;
          tokenEndpoint?: string;
          launchEndpoint?: string;
        }
      > = await this.getOverrides();

      if (isEmpty(appOverrides)) {
        return {};
      }

      return mapValues(appOverrides, ({ manifest, url, tokenEndpoint, launchEndpoint }, id) => {
        let finalApp: ApplicationManifest | null = orgManifestsMap[id]
          ? cloneDeep<ApplicationManifest>(orgManifestsMap[id]!)
          : null;

        if (manifest) {
          finalApp = cloneDeep(manifest);

          this.logger.debug('Setting manifest override for app.', { appId: id });
        }

        if (url && finalApp) {
          const replaceBaseUrl = (originalUrl: string) => {
            const originalUrlObj = new URL(originalUrl);
            const newLaunch = new URL(url);
            newLaunch.pathname = originalUrlObj.pathname;
            newLaunch.search = originalUrlObj.search;
            return newLaunch.toString();
          };

          finalApp = {
            ...finalApp,
            metadata: {
              ...finalApp.metadata,
              url,
              launchEndpoint: finalApp.metadata.launchEndpoint
                ? replaceBaseUrl(finalApp.metadata.launchEndpoint)
                : undefined,
              tokenEndpoint: finalApp.metadata.tokenEndpoint
                ? replaceBaseUrl(finalApp.metadata.tokenEndpoint)
                : undefined,
            },
          };
          this.logger.debug('Setting url override for app.', { appId: id, url });
        }

        if (tokenEndpoint && finalApp) {
          finalApp.metadata.tokenEndpoint = tokenEndpoint;
        }

        if (launchEndpoint && finalApp) {
          finalApp.metadata.launchEndpoint = launchEndpoint;
        }

        return finalApp;
      });
    } catch (error) {
      this.logger.error('error fetching app overrides!', { error: error });
      return {};
    }
  }

  private async getOverrides(): Promise<
    Record<
      string,
      {
        manifest?: ApplicationManifest;
        url?: string;
        tokenEndpoint?: string;
        launchEndpoint?: string;
      }
    >
  > {
    const appOverrides = await this.storage.safeGet<{
      url: string;
      version: string;
      tokenEndpoint: string;
      launchEndpoint: string;
    }>(APP_OVERRIDES_KEY, this.logger);

    if (isEmpty(appOverrides)) {
      return {};
    }

    this.logger.debug('Fetched application overrides from storage.', { appOverrides });

    const applicationVersionOverrides = map(appOverrides, (value, key) => {
      return { appId: key, version: value.version };
    }).filter((override) => !!override.version);

    const applicationVersionsResponse =
      applicationVersionOverrides.length > 0
        ? await this.internalApi.applicationVersionAPI.getApplicationVersions(
            {
              applicationVersions: applicationVersionOverrides,
            },
            'ApplicationVersionOverrideQuery',
          )
        : undefined;

    const appToManifest = applicationVersionsResponse?.applicationVersions
      ? keyBy(applicationVersionsResponse?.applicationVersions, 'appId')
      : {};

    return mapValues(appOverrides, (value, key) => {
      return {
        manifest: appToManifest[key]?.manifest
          ? { ...appToManifest[key].manifest, version: appToManifest[key].version }
          : undefined,
        url: value.url,
        tokenEndpoint: value.tokenEndpoint,
        launchEndpoint: value.launchEndpoint,
      };
    });
  }
}
