import React, { useCallback, useEffect, useState } from 'react';
import {
  AlignEnum,
  Button,
  ColorNameEnum,
  InputStyle,
  InputStyleWrapper,
  LabeledInput,
  LabeledTextArea,
  Popover,
  PopoverStateReturn,
  Select,
  SizeEnum,
  Text,
  WeightEnum,
} from '@getvim/atomic-ui';
import helpIconRed from '../../assets/help-icon-red.svg';
import './index.less';
import { onContactSupportSubmit, SelectOption, topicOptions } from '@/constants/supportTab';
import { CloseButton } from '@/atomic/atoms';
import SuccessPopover from './success';
import FailPopover from '@/atomic/molecules/support-tab/fail';
import { useEhrUser } from '@/hub/hooks/store/user';
import { useSelectedApplication } from '@/hub/hooks/store/selected-application';
import { ClosingMethod } from '@/analytics-manager/events';
import { usePrevious } from '@/hub/hooks';

const isValidEmail = (email: string): boolean => {
  const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
  return emailRegex.test(email);
};

export enum POPOVER_STATE {
  FORM = 'form',
  SUCCESS = 'success',
  FAIL = 'fail',
}

interface SupportTabProps {
  popover: PopoverStateReturn;
  onSubmit: onContactSupportSubmit;
  onClose: (closingMethod: ClosingMethod) => void;
}

export const SupportTab = ({ popover, onSubmit, onClose }: SupportTabProps) => {
  const user = useEhrUser();
  const [isFocused, setIsFocused] = useState(false);
  const [email, setEmail] = useState(user?.email || '');
  const [topic, setTopic] = useState<SelectOption | undefined>(undefined);
  const [message, setMessage] = useState('');
  const [hasError, setHasError] = useState(false);
  const [loading, setLoading] = useState(false);
  const [popoverState, setPopoverState] = useState(POPOVER_STATE.FORM);
  const { selectedAppId } = useSelectedApplication();
  const previousSelectedAppId = usePrevious(selectedAppId);

  const ANIMATION_DELAY = 5000;

  const handleEmailChange = useCallback((e) => {
    const replyToEmail = e.target.value;
    setEmail(replyToEmail);
    if (isValidEmail(replyToEmail)) {
      setHasError(false);
    }
  }, []);

  const handleTopicChange = useCallback((e) => {
    const topic = e[0];
    setTopic(topic);
  }, []);

  const handleMessageChange = useCallback((e) => {
    const message = e.target.value;
    setMessage(message);
  }, []);

  const resetForm = useCallback(() => {
    setPopoverState(POPOVER_STATE.FORM);
    setEmail(user?.email || '');
    setMessage('');
    setTopic(undefined);
    setIsFocused(false);
  }, [user?.email]);

  const onFocus = useCallback(() => {
    setIsFocused(true);
  }, []);

  const onBlur = useCallback(() => {
    if (!email) setIsFocused(false);
  }, [email]);

  const handleSubmit = useCallback(async () => {
    if (email && topic && message && !isValidEmail(email)) {
      setHasError(true);
    } else {
      setHasError(false);
      setLoading(true);
      try {
        const response = await onSubmit({ topic: topic?.label || '', email, message });
        if (response?.success) {
          setPopoverState(POPOVER_STATE.SUCCESS);
          setTimeout(() => {
            onClose(ClosingMethod.AUTO);
            resetForm();
          }, ANIMATION_DELAY);
        } else {
          setPopoverState(POPOVER_STATE.FAIL);
        }
      } catch {
        setPopoverState(POPOVER_STATE.FAIL);
      } finally {
        setLoading(false);
      }
    }
  }, [email, topic, message, onSubmit, onClose, resetForm]);

  const onCloseButton = useCallback(() => onClose(ClosingMethod.X), [onClose]);

  const onCloseButtonAfterSubmit = useCallback(() => {
    onClose(ClosingMethod.X);
    resetForm();
  }, [onClose, resetForm]);

  const isDisabled = !email || !topic || !message;

  useEffect(() => {
    if (previousSelectedAppId && previousSelectedAppId !== selectedAppId) {
      const closingMethod = selectedAppId
        ? ClosingMethod.VIM_APP_CLOSE_CLICKED
        : ClosingMethod.VIM_HUB_CLOSE_CLICKED;
      onClose(closingMethod);
      resetForm();
    }
  }, [onClose, previousSelectedAppId, resetForm, selectedAppId]);

  return (
    <>
      <Popover
        {...popover}
        className="support-popover"
        aria-label="Support Tab Popover"
        tabIndex={5}
      >
        {popoverState === POPOVER_STATE.FORM && (
          <div>
            <CloseButton onClick={onCloseButton} dataTestId={'support-tab-close-button'} />
            <div className="text-center">
              <img src={helpIconRed} className="help-icon-red" alt="Help icon"></img>
              <Text
                text="Having an issue?"
                colorName={ColorNameEnum.extraDark}
                weight={WeightEnum.bold}
                size={SizeEnum['18px']}
              />
              <Text
                text="We are here to help"
                colorName={ColorNameEnum.extraDark}
                weight={WeightEnum.normal}
                size={SizeEnum['18px']}
              />
              <hr
                className="margin-top margin-bottom-15"
                style={{ borderColor: 'lightgrey', width: '100%', opacity: '30%' }}
              />
              <Text
                text="Help us better understand your issue"
                colorName={ColorNameEnum.extraDark}
                weight={WeightEnum.bold}
                size={SizeEnum['12px']}
              />
              <Text
                text="Please refrain from including any personal health information (PHI). Rest assured, we
            already have the necessary details about the patient and the application in question."
                colorName={ColorNameEnum.extraDark}
                size={SizeEnum['12px']}
              />
              <Text
                text="We’ll reply directly to the email address you provide below."
                size={SizeEnum['12px']}
                className="margin-top margin-bottom-15"
              />
            </div>
            <InputStyleWrapper
              className="margin-top-5"
              inputStyle={InputStyle.smallMedium}
              hasError={hasError}
            >
              <div className="input" data-testId="email-input">
                <LabeledInput
                  label="Your email"
                  type="text"
                  id="email"
                  value={email}
                  name="email"
                  onFocus={onFocus}
                  active={!!email || isFocused}
                  onBlur={onBlur}
                  onChange={handleEmailChange}
                />
              </div>
            </InputStyleWrapper>
            {hasError && (
              <div className="email-validation-error-message" data-testId="email-validation-error">
                <Text className="label" align={AlignEnum.right}>
                  please enter a valid email
                </Text>
              </div>
            )}
            <InputStyleWrapper
              className="input-wrapper margin-top-10"
              inputStyle={InputStyle.smallMedium}
            >
              <div className="input" data-testId="topic-input">
                <Select
                  searchable={false}
                  className="select-topic"
                  name="topic"
                  id="topic"
                  options={topicOptions}
                  onChange={handleTopicChange}
                  showChevronIcon
                  label="Choose Topic"
                  values={topic ? [topic] : []}
                />
              </div>
            </InputStyleWrapper>
            <InputStyleWrapper
              className="email-content-text-area margin-top"
              inputStyle={InputStyle.smallMedium}
            >
              <div data-testId="email-content-input">
                <LabeledTextArea
                  className="email-content"
                  name="message"
                  id="message"
                  placeholder="Please provide details about the issue you're experiencing here"
                  value={message}
                  onChange={handleMessageChange}
                />
              </div>
            </InputStyleWrapper>
            <Button
              className="margin-top-10"
              type="submit"
              width="block"
              buttonType="tiny"
              text="Submit"
              disabled={isDisabled}
              onClick={handleSubmit}
              isLoading={loading}
            />
          </div>
        )}
        {popoverState === POPOVER_STATE.SUCCESS && (
          <SuccessPopover
            email={email}
            onClick={resetForm}
            onClose={onCloseButtonAfterSubmit}
            animation_delay={ANIMATION_DELAY}
          />
        )}
        {popoverState === POPOVER_STATE.FAIL && (
          <FailPopover onClick={resetForm} onClose={onCloseButtonAfterSubmit} />
        )}
      </Popover>
    </>
  );
};
