import { inject, injectable } from 'inversify';
import { Logger, LoggerFactory } from '@getvim-os/logger';
import { VimOsBffGraphQLClient } from '@/internal-api/vimOsBffGqlClient';
import { SendSupportEmailInput, SendSupportEmailResponse } from './types';
import { SEND_SUPPORT_EMAIL } from '@/internal-api/support-email/mutations';

@injectable()
export class SendSupportEmailApi {
  private logger: Logger;

  constructor(
    @inject(VimOsBffGraphQLClient) private gqlClient: VimOsBffGraphQLClient,
    @inject(LoggerFactory) loggerFactory: LoggerFactory,
  ) {
    this.logger = loggerFactory.create('SupportEmailClient');
  }

  formatContent = (content: string) => content.replace(/\n/g, '<br>');

  public async sendSupportEmail(input: SendSupportEmailInput): Promise<SendSupportEmailResponse> {
    input.content = this.formatContent(input.content);
    const result = await this.gqlClient.mutateAuthenticated<{
      ['sendSupportEmail']: SendSupportEmailResponse;
    }>({
      mutation: SEND_SUPPORT_EMAIL,
      variables: { input },
    });
    this.logger.info('SendSupportEmail result', result);
    return result.data?.sendSupportEmail || { success: false };
  }
}
