import { type Logger, LogAttributes, SeverityNumber } from '@opentelemetry/api-logs';
import { inject, injectable, interfaces } from 'inversify';
import { AttributesInjector } from '../attributes/injector';

export const VimTelLoggerFactory = Symbol('Factory<VimTelLoggerFactory>');

export type VimTelLoggerFactoryDynamicArgs = [Logger];
export type VimTelLoggerFactory = interfaces.SimpleFactory<
  VimTelemetryLogger,
  VimTelLoggerFactoryDynamicArgs
>;

const Logger = Symbol('TelemetryLogger');

export const createVimTelLogger = (container: interfaces.Container, logger: Logger) => {
  const childContainer = container.createChild();
  childContainer.bind<Logger>(Logger).toConstantValue(logger);

  return childContainer.get(VimTelemetryLogger);
};

@injectable()
export class VimTelemetryLogger {
  constructor(
    @inject(Logger) private readonly logger: Logger,
    @inject(AttributesInjector) private injector: AttributesInjector,
  ) {}

  alert(error: Error, attributes?: LogAttributes) {
    this.logger.emit({
      severityNumber: SeverityNumber.ERROR,
      severityText: 'ERROR',
      body: error.message,
      attributes: {
        ...this.injector.injectToAttributesObj(attributes),
        error:
          'toJSON' in error
            ? (error as any).toJSON()
            : {
                name: error.name,
                message: error.message,
                stack: error.stack,
              },
      },
    });
  }
}
