import React, { ReactElement, useCallback } from 'react';
import dayjs from 'dayjs';
import { isEmpty } from 'lodash-es';
import { AlignEnum, SizeEnum } from '../../types';
import { SvgQuestionMarkIcon, Text, Tooltip } from '../../atoms';
import './index.less';

export interface ExtendedApplicationTitleProps {
  title: string;
  notifications?: number;
  analyticsClient?: any;
  dataSource?: string;
  customFields?: {
    totalRAF?: number;
    totalSvu?: number;
    patientProfile?: string;
    accruedRaf?: number;
    lastAwvDate?: string;
    lastPmrrDate?: string;
    totalCost?: number;
    highRisk?: boolean;
  };
  toggleAppButton?: ReactElement<any, any>;
}

const formatDateString = (dateStr?: string): string | undefined => {
  if (isEmpty(dateStr)) return;
  try {
    const date = dayjs(dateStr);
    return date.isValid() ? date.format('MM/DD/YYYY') : undefined;
  } catch {
    return;
  }
};

export default function ExtendedApplicationTitle({
  title,
  notifications,
  analyticsClient,
  dataSource,
  customFields = {},
  toggleAppButton,
}: ExtendedApplicationTitleProps) {
  const {
    totalRAF,
    totalSvu,
    patientProfile,
    accruedRaf,
    lastAwvDate,
    lastPmrrDate,
    totalCost,
    highRisk,
  } = customFields;

  const patientProfileText = dataSource
    ? `${dataSource.charAt(0).toUpperCase()}${dataSource.slice(1)} Patient Profile`
    : 'Patient Profile';

  const lastAwvDateFormatted = formatDateString(lastAwvDate);
  const lastPmrrDateFormatted = formatDateString(lastPmrrDate);

  const handleExternalLinkClick = useCallback(() => {
    if (analyticsClient) {
      analyticsClient.track('external_link_clicked');
    }
  }, [analyticsClient]);

  return (
    <div className="application-title__adjusted-container">
      <div className="application-title__left-side">
        <Text size={SizeEnum['14px']} align={AlignEnum.left}>
          <b>{title}</b> {(!!notifications || notifications === 0) && `(${notifications})`}
        </Text>
        {(totalRAF || totalSvu || accruedRaf || lastAwvDate || lastPmrrDate) && (
          <div className="application-title__detail-items-container">
            {totalRAF && (
              <div className="application-title__detail-item">
                <span>Total RAF: {(+totalRAF).toFixed(2)}</span>
                <Tooltip
                  placement="top"
                  className="text-center"
                  width="large"
                  tooltipContent="The accumulated RAF score of the available gaps - as provided by the data source"
                >
                  <div className="application-title__question-mark">
                    <SvgQuestionMarkIcon />
                  </div>
                </Tooltip>
              </div>
            )}
            {totalSvu && (
              <div className="application-title__detail-item">
                <span>Total SVUs: {(+totalSvu).toFixed(0)}</span>
              </div>
            )}
            {accruedRaf && (
              <div className="application-title__detail-item">
                <span>Accrued RAF: {(+accruedRaf).toFixed(2)}</span>
                <Tooltip
                  placement="top"
                  className="text-center"
                  width="large"
                  tooltipContent="The accumulated RAF score of addressed gaps - as provided by the data source"
                >
                  <div className="application-title__question-mark">
                    <SvgQuestionMarkIcon />
                  </div>
                </Tooltip>
              </div>
            )}
          </div>
        )}
      </div>
      <div className="application-title__right-side">
        <div className="theme-title-logo-container">
          <div className="theme-title-logo" />
          {toggleAppButton}
        </div>
        {patientProfile && (
          <div className="application-title__detail-item application-title__detail-item__patient-profile">
            <a
              href={patientProfile}
              target="_blank"
              rel="noopener noreferrer"
              onClick={handleExternalLinkClick}
            >
              {patientProfileText}
            </a>
          </div>
        )}
      </div>

      <div className="application-title__agilon-fields">
        {lastAwvDateFormatted && (
          <div className="application-title__detail-item">
            <span>
              <b>Last AWV Date:</b> {lastAwvDateFormatted}
            </span>
          </div>
        )}
        {lastPmrrDateFormatted && (
          <div className="application-title__detail-item">
            <span>
              <b>Last PMRR Date:</b> {lastPmrrDateFormatted}
            </span>
          </div>
        )}
      </div>

      {(totalCost || highRisk) && (
        <div className="application-title__caap-fields">
          {!Number.isNaN(totalCost) && totalCost! > 0 && (
            <div className="application-title__detail-item">
              <span>
                <strong>Total Cost:</strong> {+totalCost!.toFixed(1)}$
              </span>
            </div>
          )}
          {highRisk && (
            <div className="application-title__detail-item">
              <span>
                <strong>High Risk:</strong> Yes
              </span>
            </div>
          )}
        </div>
      )}
    </div>
  );
}
