import { gql } from '@getvim-os/gql-client';
import { VaultPutInput } from './types';

export const PUT_ENCRYPTED_DATA = (encryptedData: VaultPutInput[]) => gql`
  mutation PutEncryptedData(${encryptedData
    .map(({ entityType }) => `$input_${entityType.toLowerCase()}: EncryptDataInput`)
    .join(' , ')}) {

    ${encryptedData
      .map(
        ({ entityType }) =>
          `${entityType.toLowerCase()}Put: putEncryptedData(input: $input_${entityType.toLowerCase()})`,
      )
      .join('\n')}

  }
`;
