import { injectable } from 'inversify';
import { VimAttributes } from './types';

@injectable()
export class TelemetryScopedAttributes {
  #attributes: VimAttributes = {};

  get attributes(): Readonly<VimAttributes> {
    return this.#attributes;
  }

  updateAttributes(attributes: VimAttributes) {
    this.#attributes = { ...this.#attributes, ...attributes };
  }
}
