import { InitializeOnBootstrap } from '@/bootstrap';
import { InternalApi } from '@/internal-api';
import { EntityType } from '@/internal-api/vault/types';
import { OsState } from '@/store';
import { InternalError } from '@getvim-os/errors';
import { Logger, LoggerFactory } from '@getvim-os/logger';
import { retry } from '@getvim-os/promises';
import { VimSpan } from '@getvim-os/telemetry';
import { Container, inject, injectable } from 'inversify';
import { VaultCryptoHandler } from './types';

@injectable()
export class VaultManager implements InitializeOnBootstrap {
  private vaultCrypto?: VaultCryptoHandler;

  private keyPair?: Awaited<ReturnType<VaultCryptoHandler['createRsaKeyPair']>>;

  private logger: Logger;

  private initializePromise: Promise<void> | undefined;

  constructor(
    @inject(Container) private container: Container,
    @inject(InternalApi) private internalApi: InternalApi,
    @inject(LoggerFactory) loggerFactory: LoggerFactory,
  ) {
    this.logger = loggerFactory.create('VaultManager');
  }

  public initialize() {
    this.initializePromise = (async () => {
      if (window.isSecureContext) {
        const { BrowserCryptoHandler } = await import('./browserCryptoHandler');
        this.vaultCrypto = new BrowserCryptoHandler();
      } else {
        const { WasmCryptoHandler } = await import('./wasmCryptoHandler');
        this.vaultCrypto = new WasmCryptoHandler();
      }

      this.keyPair = await this.vaultCrypto!.createRsaKeyPair();

      this.logger.info('Created keyPair', {
        keyPair: this.keyPair,
        isSecureContext: window.isSecureContext,
      });
    })();
  }

  public async createSessionKey() {
    const baseMessage = 'Failed to update session key!';
    await this.logger.errorIfThrow(
      async () => await this.initializePromise,
      `${baseMessage} - Crypto create keyPair failed`,
    );
    const rawSessionKey = await this.logger.errorIfThrow(
      () =>
        retry(
          () =>
            this.internalApi.vaultApi.getSessionKey({
              publicKey: this.keyPair!.publicKey,
            }),
          {
            retries: 3,
          },
        ),
      `${baseMessage} - API get session key failed`,
    );
    const sessionKeyId = rawSessionKey.sessionKeyId;
    this.logger.info('Created vault sessionKeyId', { sessionKeyId });
    const sessionKey = await this.logger.errorIfThrow(
      () =>
        this.vaultCrypto!.decryptSessionKey(
          rawSessionKey.encryptedSessionKey,
          this.keyPair!.privateKey,
        ),
      `${baseMessage} - Decrypt Session key failed`,
    );

    return {
      sessionKeyId,
      sessionKey,
    };
  }

  public async putSensitiveDataInVault(
    sensitiveData: Array<{ entityType: EntityType; sensitiveData: { id: any } }>,
    abortSignal: AbortSignal | undefined,
    parentSpan: VimSpan,
  ) {
    // Need to resolve OsStore dynamically to prevent circular dependency
    const osState = this.container.get(OsState).store.getState();
    if (!osState.vaultState.sessionKey || !osState.vaultState.sessionKeyId) {
      throw new InternalError('Session key is not set');
    }

    const encryptedData = await parentSpan.startAutoChildSpan(
      'encrypt messages symmetrically',
      {},
      async () =>
        await Promise.all(
          sensitiveData.map(async ({ entityType, sensitiveData }) => {
            const { encryptedValue, iv } = await this.vaultCrypto!.encryptMessageSymmetrically(
              JSON.stringify(sensitiveData),
              osState.vaultState.sessionKey!,
            );

            return {
              entityType,
              sensitiveData: encryptedValue,
              iv,
              sessionKeyId: osState.vaultState.sessionKeyId!,
            };
          }),
        ),
    );

    const result = await parentSpan.startAutoChildSpan(
      'put encrypted data API Call',
      {},
      async () =>
        await retry(() => this.internalApi.vaultApi.putEncryptedData(encryptedData, abortSignal), {
          retries: 3,
        }),
    );
    return result;
  }
}
