import { inject, injectable } from 'inversify';
import { Hub } from './hub';
import { FrameMessenger } from './messaging';
import {
  WindowAttacher,
  WindowInquirer,
  Health,
  AutomationFinder,
  CvChannel,
  WindowProperties,
  FileController,
  Win32Finder,
} from './types';
import { LoggerFactory } from '@getvim-os/logger';

interface WindowsAgentChannels {
  attacher: WindowAttacher;
  queries: WindowInquirer;
  health: Health;
  uiaFinder: AutomationFinder;
  ComputerVision: CvChannel;
  files: FileController;
  win32Finder: Win32Finder;
}

export { UIA_PropertyIds } from './messaging';

export * as WindowsAgentTypes from './types';

@injectable()
export class WindowsAgent extends Hub<WindowsAgentChannels> {
  constructor(
    @inject(FrameMessenger) messenger: FrameMessenger,
    @inject(LoggerFactory) loggerFactory: LoggerFactory,
  ) {
    super(messenger, loggerFactory.create('windows-agent'));
  }

  public static isGetForegroundWindowDetailSsucessfull(
    windowProperties: WindowProperties,
  ): boolean {
    if (
      windowProperties?.processId === 0 ||
      windowProperties?.processId === 4 ||
      windowProperties?.processName === 'Idle' ||
      windowProperties?.processName === 'System'
    ) {
      return false;
    } else {
      return true;
    }
  }

  public get attacher() {
    return this.channel('attacher');
  }

  public get queries() {
    return this.channel('queries');
  }

  public get health() {
    return this.channel('health');
  }

  public get automation() {
    return this.channel('uiaFinder');
  }

  public get files() {
    return this.channel('files');
  }

  public get win32() {
    return this.channel('win32Finder');
  }
}
