import { WebConnectors } from '../../..';
import { WebConnectorMessageTypes } from '../../enum';
import { RemoveEventListenerInput } from '../../types/removeEventListenerInput';

export async function removeEventListener(
  this: WebConnectors,
  { listenerId }: RemoveEventListenerInput,
) {
  return await this.messaging.sendMessage({
    type: WebConnectorMessageTypes.removeEventListener,
    payload: { listenerId },
  });
}
