  /* #region meta scrapers */

  @sentrySpanWrap('Connector')
  async waitForElement(params: WaitForElementParams): Promise<ScrapeResult> {
    const {
      selector,
      frameSelector,
      shadowSelector,
      numbersTries = 25,
      timeoutInterval = 1000,
      predicate,
      scrapeFirst = true,
    } = params;

    const startTime = Date.now();

    const checkElementExistence = async () => {
      if (scrapeFirst) {
        return await this.getFirstScraped({
          selector,
          frameSelector,
          shadowSelector,
          predicate,
        });
      } else {
        return await this.getFirstOfManyScraped({ selector, frameSelector, predicate });
      }
    };

    let attempts = 0;

    const retry = async () => {
      attempts++;
      if (attempts > 1) {
        await new Promise((resolve) => setTimeout(resolve, timeoutInterval));
      }

      try {
        const element = await checkElementExistence();
        if (element) {
          return element;
        }
      } catch (error) {
        throw new InternalError(`Error while fetching element: ${selector}`, {
          attempts,
          duration: Date.now() - startTime,
          error,
        });
      }

      if (attempts >= numbersTries) {
        throw new InternalError(`Exceeded retry attempts for element: ${selector}`, {
          attempts,
          duration: Date.now() - startTime,
        });
      } else {
        return retry();
      }
    };

    return retry();
  }

  @sentrySpanWrap('Connector')
  async waitForElementDoesNotExist({
    selector,
    frameSelector,
    shadowSelector,
    numbersTries = 5,
    timeoutInterval = 200,
    predicate,
    scrapeFirst = true,
  }: WaitForElementParams): Promise<void> {
    await this.waitForElementDoesNotExistRetry({
      selector,
      frameSelector,
      shadowSelector,
      numbersTries,
      timeoutInterval,
      predicate,
      scrapeFirst,
    });
  }

  private async waitForElementDoesNotExistRetry(
    {
      selector,
      frameSelector,
      shadowSelector,
      numbersTries = 5,
      timeoutInterval = 200,
    }: WaitForElementParams,
    attempts = 0,
    startTime = Date.now(),
  ): Promise<void> {
    const elementFound = await this.scrapeFirst({ selector, shadowSelector, frameSelector });
    if (!elementFound) return;

    if (attempts >= numbersTries) {
      throw new StandardError('Element still exists', ErrorCodes.RATE_LIMIT, {
        elementFound,
        duration: Date.now() - startTime,
      });
    }

    await new Promise((resolve) => setTimeout(resolve, timeoutInterval));
    return await this.waitForElementDoesNotExistRetry(
      {
        selector,
        frameSelector,
        shadowSelector,
        numbersTries,
        timeoutInterval,
      },
      attempts,
      startTime,
    );
  }

  @sentrySpanWrap('Connector')
  async getFirstOfManyScraped({
    selector,
    frameSelector,
    predicate,
  }): Promise<ScrapeResult | undefined> {
    const elements = await this.scrape({
      selector,
      frameSelector,
    });
    // bug in eslint
    // eslint-disable-next-line @typescript-eslint/return-await
    return elements?.find((e) => !predicate || predicate(e));
  }

  @sentrySpanWrap('Connector')
  async getFirstScraped({
    selector,
    frameSelector,
    shadowSelector,
    predicate,
  }): Promise<ScrapeResult | undefined> {
    const element = await this.scrapeFirst({
      selector,
      frameSelector,
      shadowSelector,
    });
    if (element) {
      const predicateValidated = !predicate || predicate(element);
      return predicateValidated ? element : undefined;
    }
  }

  @sentrySpanWrap('Connector')
  async focusAndClick({ selector, frameSelector }: Partial<InvokeFunction>): Promise<void> {
    await this.invokeFunction({
      selector,
      frameSelector,
      actionName: 'focus',
    });
    await this.invokeFunction({
      selector,
      frameSelector,
      actionName: 'click',
    });
  }

  vimIdSelector(id: string): string {
    return `[${VIM_ID_ATTRIBUTE_NAME}="${id}"]`;
  }

