import { z } from 'zod';
import { WritebackType } from './writebackType';
import { UpdateEncounterSchema, UpdateReferralSchema } from './entities';

const writebackSchemaMapping: Record<WritebackType, z.ZodSchema<any>> = {
  updateEncounter: UpdateEncounterSchema,
  updateReferral: UpdateReferralSchema,
};

export const createWritebackPayloadSchema = <T extends WritebackType>(type: T) => {
  const schema = writebackSchemaMapping[type];
  return z.object({ type: z.literal(type), data: schema });
};
