import {
  AttributeValue,
  Attributes,
  Exception,
  Span,
  SpanOptions,
  SpanContext,
  SpanStatus,
  TimeInput,
} from '@opentelemetry/api';
import { VimTracer } from './vimTracer';

export class VimSpan implements Span {
  constructor(private span: Span, private tracer: VimTracer) {}

  public startChildSpan(name: string, options?: SpanOptions) {
    return this.tracer.startChildSpan(this, name, options);
  }

  public async startAutoChildSpan<T>(
    name: string,
    options: SpanOptions | undefined,
    cb: (childSpan: VimSpan) => Promise<T>,
  ): Promise<T> {
    return await this.tracer.startAutoChildSpan(this, name, options, cb);
  }
  spanContext(): SpanContext {
    return this.span.spanContext();
  }

  /* istanbul ignore next  -- @preserve */
  setAttribute(key: string, value: AttributeValue): this {
    this.span.setAttribute(key, value);
    return this;
  }

  /* istanbul ignore next  -- @preserve */
  setAttributes(attributes: Attributes): this {
    this.span.setAttributes(attributes);
    return this;
  }
  addEvent(
    name: string,
    attributesOrStartTime?: Attributes | TimeInput,
    startTime?: TimeInput,
  ): this {
    this.span.addEvent(name, attributesOrStartTime, startTime);
    return this;
  }
  /* istanbul ignore next  -- @preserve */
  setStatus(status: SpanStatus): this {
    this.span.setStatus(status);
    return this;
  }
  /* istanbul ignore next  -- @preserve */
  updateName(name: string): this {
    this.span.updateName(name);
    return this;
  }
  end(endTime?: TimeInput): void {
    this.span.end(endTime);
  }
  /* istanbul ignore next  -- @preserve */
  isRecording(): boolean {
    return this.span.isRecording();
  }
  recordException(exception: Exception, time?: TimeInput): void {
    return this.span.recordException(exception, time);
  }
}
