import { UI } from '@deprecated/types';
import { createSlice } from '@reduxjs/toolkit';
import { actions as userActions } from '../user/userState.slice';
import { PayloadAction } from '..';
import { BaseWriteBackConfiguration } from '@/deprecated/types/events';

type Hub = {
  isActive: boolean;
  position: UI.HubPosition;
  writeBackOverlay?: BaseWriteBackConfiguration;
};

const optumLoaderApps = new Set([
  'optum-referral-utility',
  'ae0176b4-4b7e-428a-b00f-e892dd295fd5', // OU dev
  '4db959c1-8437-42d0-b12f-631e330f7621', // OU staging
  '8573ca55-b74f-43ab-8108-9be023037aeb', // OU demo
  'd1f00110-c12b-4286-adca-3d48b9c03e16', // OU prod
]);

export const initialState: Hub = {
  isActive: false,
  position: UI.DEFAULT_HUB_POSITION,
};

export const slice = createSlice({
  name: 'hub',
  initialState,
  reducers: {
    setActive: (state, action: PayloadAction<Hub['isActive']>) => {
      state.isActive = action.payload;
    },
    movePositionInDirection: (state, action: PayloadAction<UI.HubDirection>) => {
      const direction = action.payload;
      const newPosition: Partial<UI.HubPosition> = {};
      if (direction.vertical) {
        newPosition.vertical = verticalMovementToPosition(
          state.position.vertical,
          direction.vertical,
        );
      }
      if (direction.horizontal) {
        newPosition.horizontal =
          direction.horizontal === UI.HorizontalDirection.Right
            ? UI.HorizontalHubPosition.Right
            : UI.HorizontalHubPosition.Left;
      }
      state.position = { ...state.position, ...newPosition };
    },
    setPosition: (state, action: PayloadAction<UI.HubPosition>) => {
      state.position = action.payload;
    },
    displayWriteBackOverlay: (state, action: PayloadAction<Hub['writeBackOverlay']>) => {
      state.writeBackOverlay = {
        displayLoadingWidget: action.payload?.displayLoadingWidget ?? false,
        loadingWidgetId: optumLoaderApps.has(action.payload?.appName ?? '')
          ? 'optum'
          : action.payload?.loadingWidgetId,
      };
    },
    hideWriteBackOverlay: (state) => {
      delete state.writeBackOverlay;
    },
  },
  extraReducers: (builder) => {
    builder.addCase(userActions.logout, (state) => {
      state.position = initialState.position;
      delete state.writeBackOverlay;
    });
  },
});

const verticalMovementToPosition = (
  verticalPosition: UI.VerticalHubPosition = UI.VerticalHubPosition.Middle,
  verticalDirection: UI.VerticalDirection,
) => {
  switch (verticalPosition) {
    case UI.VerticalHubPosition.High:
      if (verticalDirection === UI.VerticalDirection.Up) {
        return verticalPosition;
      }
      return UI.VerticalHubPosition.High_Middle;
    case UI.VerticalHubPosition.High_Middle:
      if (verticalDirection === UI.VerticalDirection.Up) {
        return UI.VerticalHubPosition.High;
      }
      return UI.VerticalHubPosition.Middle;
    case UI.VerticalHubPosition.Middle:
      if (verticalDirection === UI.VerticalDirection.Up) {
        return UI.VerticalHubPosition.High_Middle;
      }
      return UI.VerticalHubPosition.Low_Middle;
    case UI.VerticalHubPosition.Low_Middle:
      if (verticalDirection === UI.VerticalDirection.Up) {
        return UI.VerticalHubPosition.Middle;
      }
      return UI.VerticalHubPosition.Low;
    case UI.VerticalHubPosition.Low:
      if (verticalDirection === UI.VerticalDirection.Down) {
        return verticalPosition;
      }
      return UI.VerticalHubPosition.Low_Middle;
  }
};

export const actions = slice.actions;

export default slice.reducer;
