class HandshakeError extends Error {
}
class MessageTimeoutError extends Error {
  constructor(timeout) {
    super(`Timeout of ${timeout}ms exceeded`);
  }
}

const MESSAGE_RESPONSE_TIMEOUT = 3e4;
const createPromisedChannel = (timeout = MESSAGE_RESPONSE_TIMEOUT) => {
  const messageChannel = new MessageChannel();
  return [
    messageChannel.port2,
    new Promise((resolve, reject) => {
      const timeoutId = setTimeout(() => {
        reject(new MessageTimeoutError(timeout));
      }, timeout);
      messageChannel.port1.addEventListener("message", (event) => {
        clearTimeout(timeoutId);
        const { response, error } = event.data ?? {};
        if (error) {
          reject(response);
        } else {
          resolve(response);
        }
        messageChannel.port1.close();
      });
      messageChannel.port1.start();
    })
  ];
};

class PortMessenger {
  constructor(port, options) {
    this.port = port;
    this.options = options;
  }
  async handleIncomingMessage({
    data,
    ports
  }) {
    if (data.asyncMessage) {
      if (this.options.asyncMessageCallback) {
        this.options.asyncMessageCallback(data);
      }
    } else if (this.options.messageCallback) {
      try {
        const result = await this.options.messageCallback(data);
        ports[0].postMessage({
          response: result,
          error: false
        });
      } catch (e) {
        ports[0].postMessage({
          response: e instanceof Error ? e.message : e,
          error: true
        });
      }
    }
  }
  sendMessage(message, timeout) {
    const [portToForward, promise] = createPromisedChannel(timeout);
    this.port.postMessage(message, [
      portToForward
    ]);
    return promise;
  }
  sendAsyncMessage(message) {
    this.port.postMessage({
      ...message,
      asyncMessage: true
    });
  }
}

export { HandshakeError, MESSAGE_RESPONSE_TIMEOUT, MessageTimeoutError, PortMessenger, createPromisedChannel };
