import { inject, injectable } from 'inversify';
import { OtelTelemetryLogsProvider } from './otelLogsProvider';
import { VimTelLoggerFactory, VimTelemetryLogger } from './vimLogger';

@injectable()
export abstract class VimTelemetryLogsProvider {
  constructor(
    @inject(OtelTelemetryLogsProvider) protected loggerProvider: OtelTelemetryLogsProvider,
    @inject(VimTelLoggerFactory) private vimLoggerFactory: VimTelLoggerFactory,
  ) {}
  getLogger(...args: Parameters<OtelTelemetryLogsProvider['getLogger']>): VimTelemetryLogger {
    return this.vimLoggerFactory(this.loggerProvider.getLogger(...args));
  }
}
