import { injectable, inject } from 'inversify';
import { Logger, LoggerFactory } from '@getvim-os/logger';
import { ParentMessage } from '@getvim-os/barebone/infra';
import { RuntimeToOsMessage } from '../types/events';

type ShouldIntercept = boolean;
type Interceptor = (msg: MessageEvent) => ShouldIntercept;

type MessageHandler<T extends VIM_OS_EVENTS> = (message: RuntimeToOsMessage[T]) => void;

interface ParentMessageData {
  type?: string;
  payload?: unknown;
}
export type ParentInterceptor = (msg: ParentMessage<ParentMessageData>) => ShouldIntercept;
export enum VIM_OS_EVENTS {
  AUTH_EVENT = 'AUTH_EVENT',
  WORKFLOW_EVENT = 'WORKFLOW_EVENT',
  APPLICATION_MANAGEMENT_EVENTS = 'APPLICATION_MANAGEMENT_EVENTS',
  WRITEBACK_EVENT = 'WRITEBACK_EVENT',
  DATA_TO_WIDGET = 'DATA_TO_WIDGET',
  ADAPTER_EVENT = 'ADAPTER_EVENT',
}
type VimOSMessageEvent = {
  osEvent: VIM_OS_EVENTS;
  type: string;
  payload: any;
};

@injectable()
export class RuntimeMessageHooks {
  private logger: Logger;

  constructor(@inject(LoggerFactory) loggerFactory: LoggerFactory) {
    this.logger = loggerFactory.create('runtime-message-hooks');
  }
  private messageHandlers: Partial<Record<VIM_OS_EVENTS, MessageHandler<any>[]>> = {};
  private fromRuntimeInterceptors: Interceptor[] = [];
  private toRuntimeInterceptors: ParentInterceptor[] = [];

  public addOSMessageHandler<T extends VIM_OS_EVENTS>(eventType: T, handler: MessageHandler<T>) {
    if (this.messageHandlers[eventType] === undefined) {
      this.messageHandlers[eventType] = [];
    }
    this.messageHandlers[eventType]?.push(handler);
  }

  public addMessageToRuntimeInterceptor(interceptor: ParentInterceptor) {
    this.toRuntimeInterceptors.push(interceptor);
  }

  public onOSMessageFromRuntime(msg: MessageEvent<VimOSMessageEvent>) {
    const { osEvent, type, payload } = msg.data;
    this.messageHandlers[osEvent]?.forEach(async (handler) => {
      try {
        await handler({ type, payload });
      } catch (error) {
        this.logger.warning('Handler threw an error, swallowing it', { error });
      }
    });
  }

  public onMessageFromRuntime(msg: MessageEvent, action: () => void) {
    let shouldIntercept = false;
    for (const interceptor of this.fromRuntimeInterceptors) {
      try {
        const intercepts = interceptor(msg);
        shouldIntercept ||= intercepts;
      } catch (error) {
        this.logger.warning('Interceptor threw an error, swallowing it', { error });
      }
    }

    if (!shouldIntercept) {
      action();
    }
  }

  public onMessageToRuntime(msg: ParentMessage, action: () => void) {
    let shouldIntercept = false;
    for (const interceptor of this.toRuntimeInterceptors) {
      try {
        const intercepts = interceptor(msg as ParentMessage<ParentMessageData>);
        shouldIntercept ||= intercepts;
      } catch (error) {
        this.logger.warning('Interceptor threw an error, swallowing it', error);
      }
    }

    if (!shouldIntercept) {
      action();
    }
  }
}
