import {
  ActionNames,
  Diagnosis,
  EHRResource,
  ENRICHED_EHR_STATE,
  EnrichedPatient,
  Order,
  UpdateEncounter,
  UpdateReferralWritebackInput,
} from '@getvim-os/types';
import { BaseProperties, Sessions, TrackPayload } from '../../../analytics-manager/events';
import { ApplicationInput } from './application';
import {
  ApplicationManagementEvents,
  AuthEvents,
  WidgetIncomingEvent,
  WorkflowEvents,
  WriteBackEvents,
} from './events';
import {
  BaseWriteBackConfiguration,
  SelectAppPayload,
  SendDataToWidgetPayload,
} from './runtimePayloads';
import { VIM_OS_EVENTS } from '../../runtime-wrapper/messageHooks';
import { PatientInEntity } from '../entities';
import { Me } from '../../../store/user/types';
import { HubPosition } from '../ui';
import { TrackPayload as AppTrackPayload } from '@getvim/vim-os-api';
import { AdapterToOsEventData } from '@getvim-adapter/api';

type WriteBackEvent = {
  error: any;
  writeBackPayload: WriteBackPayload;
  appName: string;
};
export enum OsToRuntimeEvents {
  EXECUTE_ACTION = 'vim-os-to-runtime-execute-action',
  EXECUTE_WRITE_BACK = 'vim-os-to-runtime-execute-write-back',
  HUB_WIDGET_POSITION_CHANGED = 'vim-os-to-runtime-hub-widget-position-changed',
  INJECT_LAUNCH_BUTTON = 'vim-os-to-runtime-inject-launch-button',
  ANALYTICS_TRACK = 'vim-os-to-runtime-analytics-track',
  ANALYTICS_SET_BASE_PROPERTIES = 'vim-os-to-runtime-analytics-set-base-properties',
  ANALYTICS_SET_SESSIONS = 'vim-os-to-runtime-analytics-set-sessions',
  APP_ANALYTICS_TRACK = 'vim-os-to-runtime-app-analytics-track',
}

export type WriteBackPayload =
  | {
      writeBack: 'updateProblemList';
      data: {
        conditionsToAdd: Diagnosis[];
        conditionsToDismiss: Pick<Diagnosis, 'code'>[];
      };
      config: undefined;
    }
  | {
      writeBack: 'updateEncounter';
      data: UpdateEncounter;
      config: undefined;
    }
  | {
      writeBack: 'updateReferral';
      data: UpdateReferralWritebackInput;
      config: {
        overrideEhrFields?: (keyof UpdateReferralWritebackInput)[];
        clearEmptyFields?: boolean;
      };
    };

export interface InjectInlineWidgetProps {
  id: string;
  url: string;
  display: boolean;
  section: UIInjectionName;
  size: {
    height: string;
    width: string;
  };
}

export type OsToRuntimeMessage =
  | {
      type: OsToRuntimeEvents.EXECUTE_ACTION;
      payload: {
        action: ActionNames;
        input: unknown;
      };
    }
  | { type: OsToRuntimeEvents.HUB_WIDGET_POSITION_CHANGED; payload: HubPosition }
  | {
      type: OsToRuntimeEvents.EXECUTE_WRITE_BACK;
      payload: Omit<WriteBackPayload, 'config'> & {
        config: {
          overrideEhrFields?: (keyof UpdateReferralWritebackInput)[];
          clearEmptyFields?: boolean;
          displayLoadingWidget: true;
          appName: string;
        };
      };
    }
  | {
      type: OsToRuntimeEvents.INJECT_LAUNCH_BUTTON;
      payload: InjectInlineWidgetProps;
    }
  | {
      type: OsToRuntimeEvents.ANALYTICS_TRACK;
      payload: TrackPayload;
    }
  | {
      type: OsToRuntimeEvents.ANALYTICS_SET_SESSIONS;
      payload: Sessions;
    }
  | {
      type: OsToRuntimeEvents.ANALYTICS_SET_BASE_PROPERTIES;
      payload: Partial<BaseProperties>;
    }
  | {
      type: OsToRuntimeEvents.APP_ANALYTICS_TRACK;
      payload: AppTrackPayload;
    };

export const enum WriteBackName {
  updateReferral = 'updateReferral',
  updateEncounter = 'updateEncounter',
  updateProblemList = 'updateProblemList',
}

export const enum UIInjectionName {
  referralSelectProvider = 'referralSelectProvider',
  labsSelectProvider = 'labsSelectProvider',
  diSelectProvider = 'diSelectProvider',
}

export type AvailableWriteBacks = {
  availableWriteBacks?: WriteBackName[];
};

export type AvailableUIInjections = {
  availableUIInjections?: UIInjectionName[];
};

export type EHRResourceEnhancements = AvailableWriteBacks &
  AvailableUIInjections &
  PatientInEntity & { isV2Adapter?: boolean };

export type EhrResourceFromRuntime<T extends EHRResource> = T extends EHRResource.orders
  ? { selectedOrders: Array<Order> } & EHRResourceEnhancements
  : ENRICHED_EHR_STATE[T] & EHRResourceEnhancements;
export type RuntimeToOsMessage = {
  [VIM_OS_EVENTS.DATA_TO_WIDGET]: {
    type: WidgetIncomingEvent.SendDataToWidget;
    payload: SendDataToWidgetPayload;
  };

  [VIM_OS_EVENTS.APPLICATION_MANAGEMENT_EVENTS]:
    | {
        type: ApplicationManagementEvents.InjectApplication;
        payload: ApplicationInput;
      }
    | {
        type: ApplicationManagementEvents.ModifyApplication;
        payload: {
          id: ApplicationInput['id'];
          modifications: Partial<Omit<ApplicationInput, 'id'>>;
        };
      }
    | {
        type: ApplicationManagementEvents.SelectApplication;
        payload: SelectAppPayload;
      }
    | {
        type: ApplicationManagementEvents.RemoveApplication;
        payload: ApplicationInput['id'];
      };

  [VIM_OS_EVENTS.AUTH_EVENT]:
    | {
        type: AuthEvents.INITIALIZE_ME;
        payload: { me: Me; hubPosition?: HubPosition };
      }
    | {
        type: AuthEvents.GET_HOSTNAME;
        payload: { hostname: string };
      }
    | {
        type: AuthEvents.ON_LOGOUT;
        payload: never;
      }
    | {
        type: AuthEvents.UPDATE_ACCESS_TOKEN;
        payload: {
          accessToken: string;
        };
      };

  [VIM_OS_EVENTS.WORKFLOW_EVENT]:
    | {
        type: WorkflowEvents.PATIENT_IN_CONTEXT;
        payload: EhrResourceFromRuntime<EHRResource.patient>;
      }
    | { type: WorkflowEvents.PATIENT_OUT_OF_CONTEXT; payload?: { isV2Adapter?: boolean } }
    | {
        type: WorkflowEvents.ENCOUNTER_START;
        payload: EhrResourceFromRuntime<EHRResource.encounter>;
      }
    | {
        type: WorkflowEvents.ENCOUNTER_UPDATED;
        payload: EhrResourceFromRuntime<EHRResource.encounter>;
      }
    | { type: WorkflowEvents.ENCOUNTER_EXIT }
    | {
        type: WorkflowEvents.REFERRAL_VIEWED;
        payload: EhrResourceFromRuntime<EHRResource.referral>;
      }
    | { type: WorkflowEvents.REFERRAL_CLOSED }
    | {
        type: WorkflowEvents.ORDER_VIEWED;
        payload: AvailableWriteBacks & { order: Order; patient: EnrichedPatient };
      }
    | {
        type: WorkflowEvents.ORDERS_VIEWED;
        payload: AvailableWriteBacks &
          AvailableUIInjections & { selectedOrders: Order[]; patient: EnrichedPatient };
      }
    | { type: WorkflowEvents.ORDERS_CLOSED }
    | { type: WorkflowEvents.REFERRAL_SAVED; payload: ENRICHED_EHR_STATE[EHRResource.referral] }
    | { type: WorkflowEvents.REFERRAL_DELETED; payload: ENRICHED_EHR_STATE[EHRResource.referral] }
    | { type: WorkflowEvents.ORDER_CREATED; payload: Order };
  [VIM_OS_EVENTS.WRITEBACK_EVENT]:
    | {
        type: WriteBackEvents.WRITE_BACK;
        payload: WriteBackEvent;
      }
    | {
        type: WriteBackEvents.DISPLAY_WRITE_BACK_OVERLAY;
        payload: BaseWriteBackConfiguration | undefined;
      }
    | {
        type: WriteBackEvents.HIDE_WRITE_BACK_OVERLAY;
      };
  [VIM_OS_EVENTS.ADAPTER_EVENT]: AdapterToOsEventData;
};
