import React, { FunctionComponent, PropsWithChildren } from 'react';
import Text from '../../atoms/text/index';
import { WeightEnum, ColorNameEnum, SizeEnum, AlignEnum } from '../../types/enums';

import './index.less';

type ThemeEditorWrapperProps = {
  mainColor?: string;
  buttonColor?: string;
  secondaryColor?: string;
  secondaryDarkColor?: string;
  secondaryColorLighter?: string;
  secondaryColorLight?: string;
  accentColor?: string;
  linkColor?: string;
  successColor?: string;
  font?: string;
  fontWeight?: string;
  buttonBorderRadius?: string;
  showThemeSummary?: boolean;
  appBgColor?: string;
  appTitleBgColor?: string;
  appIconColor?: string;
  appIconSelectedColor?: string;
  appIconDisabledColor?: string;
  secondaryAccentColor?: string;
  managementIconColor?: string;
  logo?: string;
  appLogo?: string;
  appAdjustedTitleBgColor?: string;
  appAdjustedTitleFontColor?: string;
  titleLogo?: string;
};

const ThemeEditorWrapper: FunctionComponent<ThemeEditorWrapperProps> = ({
  mainColor,
  buttonColor,
  secondaryColor,
  secondaryDarkColor,
  secondaryColorLight,
  secondaryColorLighter,
  accentColor,
  linkColor,
  successColor,
  font,
  fontWeight,
  buttonBorderRadius,
  showThemeSummary = false,
  children,
  appBgColor,
  appTitleBgColor,
  appIconColor,
  appIconSelectedColor,
  appIconDisabledColor,
  managementIconColor,
  secondaryAccentColor,
  logo,
  appLogo,
  appAdjustedTitleBgColor = '#e1e1e1',
  appAdjustedTitleFontColor = '#32323c',
  titleLogo = 'url(https://static.getvim.com/img/logos/apixio.png)',
}: PropsWithChildren<ThemeEditorWrapperProps>) => {
  const style = {
    '--main-color': mainColor,
    '--button-color': buttonColor,
    '--secondary-color': secondaryColor,
    '--secondary-dark-color': secondaryDarkColor,
    '--secondary-color-light': secondaryColorLight,
    '--secondary-color-lighter': secondaryColorLighter,
    '--accent-color': accentColor,
    '--link-color': linkColor,
    '--success-color': successColor,
    '--font': font,
    '--font-weight': fontWeight,
    '--button-border-radius': `${buttonBorderRadius}px`,
    '--app-bg-color': appBgColor,
    '--app-title-bg-color': appTitleBgColor,
    '--app-icon-color': appIconColor,
    '--app-icon-selected-color': appIconSelectedColor,
    '--app-icon-disabled-color': appIconDisabledColor,
    '--secondary-accent-color': secondaryAccentColor,
    '--management-icon-color': managementIconColor,
    '--logo': logo && `url(${logo})`,
    '--app-logo': appLogo && `url(${appLogo})`,
    '--app-adjusted-title-bg-color': appAdjustedTitleBgColor,
    '--app-adjusted-title-font-color': appAdjustedTitleFontColor,
    '--title-logo': titleLogo,
  } as React.CSSProperties;
  return (
    <div style={style}>
      {children}
      {showThemeSummary && (
        <div className="theme-summary-modal">
          <div className="theme-colors">
            <div className="card text-white" style={{ background: 'var(--main-color)' }}>
              mainColor:<div className="text-16">{mainColor}</div>
            </div>
            <div className="card text-white" style={{ background: 'var(--secondary-color)' }}>
              secondaryColor:
              <div className="text-16">{secondaryColor}</div>
            </div>
            <div
              className="card text-dark text-strong"
              style={{ background: 'var(--secondary-color-light)' }}
            >
              secondaryColorLight:
              <div className="text-16">{secondaryColorLight}</div>
            </div>
            <div
              className="card text-dark"
              style={{ background: 'var(--secondary-color-lighter)' }}
            >
              secondaryColorLighter:
              <div className="text-16">{secondaryColorLighter}</div>
            </div>
            <div className="card text-white" style={{ background: 'var(--link-color)' }}>
              linkColor:
              <div className="text-16">{linkColor}</div>
            </div>
            <div className="card text-white" style={{ background: 'var(--button-color)' }}>
              buttonColor:
              <div className="text-16">{buttonColor}</div>
            </div>
          </div>
          <div className="padding-h-30">
            <Text
              text="font:"
              colorName={ColorNameEnum.ultraDark}
              size={SizeEnum['18px']}
              align={AlignEnum.left}
            />
            <Text
              colorName={ColorNameEnum.ultraDark}
              weight={WeightEnum.bold}
              size={SizeEnum['22px']}
              align={AlignEnum.left}
            >
              <span style={{ fontFamily: 'var(--font)' }}>{font}</span>
            </Text>
          </div>
          <div className="padding-h-30 margin-top">
            <Text
              text="fontWeight:"
              colorName={ColorNameEnum.ultraDark}
              size={SizeEnum['18px']}
              align={AlignEnum.left}
            />
            <Text
              colorName={ColorNameEnum.ultraDark}
              weight={WeightEnum.bold}
              size={SizeEnum['22px']}
              align={AlignEnum.left}
              text={fontWeight}
            />
          </div>
          <div className="padding-h-30 margin-top">
            <Text
              text="buttonBorderRadius:"
              colorName={ColorNameEnum.ultraDark}
              size={SizeEnum['18px']}
              align={AlignEnum.left}
            />
            <Text
              colorName={ColorNameEnum.ultraDark}
              weight={WeightEnum.bold}
              size={SizeEnum['22px']}
              align={AlignEnum.left}
            >
              {buttonBorderRadius}px
            </Text>
          </div>
        </div>
      )}
    </div>
  );
};

export default ThemeEditorWrapper;
