import { UI } from '@deprecated/types';
import { EhrUser, Organization, Tags } from '@getvim-os/types';
import { EHRResource, OSMessageResponseType } from '@getvim/vim-os-api';

export enum UserManagementAnalyticsEventTypes {
  UserManagementTitlesSelected = 'user_management_titles_selected',
  UserManagementAppEnabled = 'user_management_app_enabled',
}

export enum VimOSAnalyticsEventTypes {
  VimAppLoaded = 'vim_app_loaded',
  VimAppInitialized = 'vim_app_initialized',
  VimAppActivationStateChanged = 'vim_app_activation_state_changed',
  VimAppOpened = 'vim_app_opened',
  VimAppClosed = 'vim_app_closed',
  VimHubPositionChange = 'vim_hub_position_change',
  VimHubAppTransition = 'vim_hub_app_transition',
}

export enum VimOSNotificationsEventTypes {
  VimHubNotificationDisplay = 'vim_hub_notification_displayed',
  VimHubNotificationHide = 'vim_hub_notification_hide',
  VimHubNotificationHovered = 'vim_hub_notification_hovered',
  VimHubNotificationClicked = 'vim_hub_notification_clicked',
  VimHubNotificationClosed = 'vim_hub_notification_closed',
  VimHubNotificationBlocked = 'vim_hub_notification_blocked',
  VimHubNotificationCtaClicked = 'vim_hub_notification_cta_clicked',
}

export enum VimHubNotificationBlockedReason {
  ReachedLimitation = 'reached_limitation',
  HubWasOpened = 'hub_was_opened',
  AppWasDisabled = 'app_was_disabled',
}

export enum WritebackAnalyticsEventTypes {
  VimHubEhrWriteback = 'vim_hub_ehr_writeback',
}

export enum CloseType {
  MANUAL = 'manual',
  AUTOMATIC = 'automatic',
}

export enum VimHubNotificationHiddenReason {
  HideByApp = 'hide_by_app',
  AppDisabled = 'app_was_disabled',
}

export enum ContactSupportEmailAnalyticsTypes {
  VimSupportIconHovered = 'vim_support_icon_hovered',
  VimSupportFormOpened = 'vim_support_form_opened',
  VimSupportFormConfirmed = 'vim_support_form_confirmed',
  VimHubSupportClosed = 'vim_hub_support_closed',
}

export enum SupportFlowLocation {
  IN_FOOTER = 'in_footer',
  SUPPORT_APP = 'support_app',
  AGENT = 'agent',
}

export enum WritebackStatus {
  SUCCESS = 'success',
  FAILED = 'failed',
}

export enum SupportFormSubmissionStatus {
  SUCCESS = 'success',
  FAILED = 'failed',
}

export enum ClosingMethod {
  X = 'X_arrow',
  OUT_OF_MODAL = 'out_of_modal',
  AUTO = 'automatic',
  VIM_HUB_CLOSE_CLICKED = 'VimHubCloseClicked',
  VIM_APP_CLOSE_CLICKED = 'VimAppCloseClicked',
}

export interface Sessions {
  preAuthSession?: string | null;
  userSession?: string | null;
  patientSession?: string | null;
  vimHubSession?: string | null;
  vimAppSession?: string | null;
}

interface NotificationEventPayload {
  widgetId: string;
  notificationId?: string;
  app_details: AppDetails;
}

interface AppDetails {
  app_id: string;
  app_name: string;
  sdk_version?: string;
  app_version?: string;
}

export type TrackPayload =
  | {
      event: UserManagementAnalyticsEventTypes.UserManagementAppEnabled;
      properties: {
        user: EhrUser & { organization: Organization };
        canEditRole: boolean;
        organization_tenant_patient_token?: string;
      };
    }
  | {
      event: UserManagementAnalyticsEventTypes.UserManagementTitlesSelected;
      properties: {
        me?: EhrUser & { organization: Organization };
        selectedTitles: string[];
        organization_tenant_patient_token?: string;
      };
    }
  | {
      event: VimOSAnalyticsEventTypes.VimHubPositionChange;
      properties: {
        hub_vertical_direction?: UI.VerticalDirection;
        hub_horizontal_direction?: UI.HorizontalDirection;
        hub_new_vertical_position: UI.VerticalHubPosition;
        hub_new_horizontal_position: UI.HorizontalHubPosition;
        hub_old_vertical_position: UI.VerticalHubPosition;
        hub_old_horizontal_position: UI.HorizontalHubPosition;
        hub_activation_status: boolean;
        organization_tenant_patient_token?: string;
      };
    }
  | {
      event: VimOSAnalyticsEventTypes.VimAppOpened;
      properties: {
        app_details: AppDetails;
        app_name: string;
        app_notifications_count: number;
        app_open_method?: UI.ExpandingType;
        app_sources?: string[];
        organization_tenant_patient_token?: string;
      };
    }
  | {
      event: VimOSAnalyticsEventTypes.VimAppClosed;
      properties: {
        app_details: AppDetails;
        app_name: string;
        app_close_reason: AppCloseReason;
        app_opened_for_time: number;
        organization_tenant_patient_token?: string;
      };
    }
  | {
      event: VimOSAnalyticsEventTypes.VimHubAppTransition;
      properties: {
        app_details: AppDetails;
        origin_app: string | null;
        active_app: string | null;
        app_transition_count: number;
        app_notifications_count: number;
        organization_tenant_patient_token?: string;
      };
    }
  | {
      event: VimOSAnalyticsEventTypes.VimAppLoaded;
      properties: {
        app_details: AppDetails;
        app_name: string;
        app_sources?;
        app_activation_status: boolean;
        app_notifications_count: number;
        response_time: number;
        organization_tenant_patient_token?: string;
      };
    }
  | {
      event: VimOSAnalyticsEventTypes.VimAppInitialized;
      properties: AppDetails & {
        initialization_success: boolean;
        initialization_failure_reason?: OSMessageResponseType;
        organization_tenant_patient_token?: string;
      };
    }
  | {
      event: VimOSAnalyticsEventTypes.VimAppActivationStateChanged;
      properties: AppDetails & {
        activation_status: 'enabled' | 'disabled';
        organization_tenant_patient_token?: string;
      };
    }
  | {
      event: VimOSNotificationsEventTypes.VimHubNotificationBlocked;
      properties: NotificationEventPayload & {
        reason: string;
        organization_tenant_patient_token?: string;
      };
    }
  | {
      event: VimOSNotificationsEventTypes.VimHubNotificationHide;
      properties: {
        appName: string;
        reason: VimHubNotificationHiddenReason;
        notificationId?: string;
        app_details: AppDetails;
        organization_tenant_patient_token?: string;
      };
    }
  | {
      event: VimOSNotificationsEventTypes.VimHubNotificationCtaClicked;
      properties: NotificationEventPayload & {
        action: string;
        organization_tenant_patient_token?: string;
      };
    }
  | {
      event:
        | VimOSNotificationsEventTypes.VimHubNotificationDisplay
        | VimOSNotificationsEventTypes.VimHubNotificationHovered
        | VimOSNotificationsEventTypes.VimHubNotificationClicked;
      properties: NotificationEventPayload & {
        organization_tenant_patient_token?: string;
      };
    }
  | {
      event: VimOSNotificationsEventTypes.VimHubNotificationClosed;
      properties: NotificationEventPayload & {
        method: CloseType;
        organization_tenant_patient_token?: string;
      };
    }
  | {
      event: ContactSupportEmailAnalyticsTypes.VimSupportIconHovered;
      properties: {
        app_name?: string;
        support_flow_location?: SupportFlowLocation;
        organization_tenant_patient_token?: string;
      };
    }
  | {
      event: ContactSupportEmailAnalyticsTypes.VimSupportFormOpened;
      properties: {
        app_name?: string;
        support_flow_location?: SupportFlowLocation;
        support_form_id?: string;
        organization_tenant_patient_token?: string;
      };
    }
  | {
      event: ContactSupportEmailAnalyticsTypes.VimSupportFormConfirmed;
      properties: {
        app_name?: string;
        support_flow_location?: SupportFlowLocation;
        support_form_submission_status?: SupportFormSubmissionStatus;
        support_form_id?: string;
        support_request_type?: string;
        organization_tenant_patient_token?: string;
      };
    }
  | {
      event: ContactSupportEmailAnalyticsTypes.VimHubSupportClosed;
      properties: {
        app_name?: string;
        support_flow_location?: SupportFlowLocation;
        closing_method?: string;
        support_form_id?: string;
        organization_tenant_patient_token?: string;
      };
    }
  | {
      event: WritebackAnalyticsEventTypes.VimHubEhrWriteback;
      properties: {
        updated_entity: EHRResource;
        writeback_status: WritebackStatus;
        writeback_details: string[];
        writeback_duration_in_ms: number;
        app_details: AppDetails;
        organization_tenant_patient_token?: string;
      };
    };

export interface BaseProperties {
  workstation?: Partial<WorkstationBaseProperties>;
  vim_user?: Partial<VimUserBaseProperties>;
  vim_patient?: Partial<VimPatientBaseProperties>;
  ehr_location_context?: Partial<EHRLocationContextBaseProperties>;
}

export interface WorkstationBaseProperties {
  device_id: string;
  version: string;
  agent_type: string;
  agent_version: string;
  ehr_version: string;
  adapter_name: string;
  window_id: string | number;
  user_agent: string;
  current_url: string;
}

export interface VimUserBaseProperties {
  linked_ehr_user_name?: string;
  email?: string;
  organization_id?: number;
  organization_name?: string;
  user_id?: string;
  user_tag?: Tags;
}

export interface VimPatientBaseProperties {
  vim_patient_id?: string | null;
  ehr_insurance?: string | null;
  vim_insurer?: string | null;
  vim_source_patient_ids?: string | null;
}

export interface EHRLocationContextBaseProperties {
  vim_referral_id?: string | null;
  referral_created_date?: string | null;
  days_from_ref_creation?: number | null;
  encounter_id?: string | null;
}

export enum AppCloseReason {
  AppSwitch = 'AppSwitch',
  AppDisabled = 'AppDisabled',
  VimHubCloseClicked = 'VimHubCloseClicked',
}
