import { DataSource, ProductSource, ProviderSource } from '@getvim-core-apps/organizations';
import { AppStoreServiceManifest, ApplicationManifest } from '..';

export enum Tags {
  VimTest,
  AppTest,
  ClinicTest,
}

export interface UserAddress {
  fullAddress: string;
  zip: string;
}

export enum Product {
  DiagnosisGaps = 'diagnosis-gaps',
  Eligibility = 'eligibility',
  OrderAssist = 'order-assist',
  PatientHealthHistory = 'patient-health-history',
  PriorAuthCheck = 'prior-auth-check',
  QualityGaps = 'quality-gaps',
  ReferralGuidance = 'referral-guidance',
  OptumReferralUtility = 'optum-referral-utility',
  OptimalCare = 'optimal-care',
  Scheduling = 'scheduling',
  OptumDataCore = 'optum-data-core',
  OptumPro = 'optum-pro',
  OptumDataCoreDashboard = 'optum-data-core-dashboard',
  MockCanvasApp = 'mock-canvas-app',
  FhirSimulator = 'fhir-simulator',
  CDE = 'clinical-data-exchange',
  PriviaInsights = 'privia-insights',
}

export interface Products {
  name: Product;
  sources: ProductSource[];
  providerSources?: ProviderSource[];
  contentSources?: DataSource[];
}

export type AppStoreServiceId = AppStoreServiceManifest['id'];

interface MeApplications {
  name: string;
  config: unknown;
}
export interface EhrUser {
  email: string;
  phoneNumber?: string;
  firstName: string;
  lastName: string;
  roles?: string[] | null;
  linkedEhrUser: string;
  npi: string;
  products: Products[];
  tin: string;
  address: UserAddress;
  externalId: string;
  tag: Tags;
  applications: MeApplications[];
  appStoreApplications: ApplicationManifest[];
}

export enum OS {
  Web = 'web',
  Desktop = 'desktop',
  Sdk = 'sdk',
}

export interface Organization {
  id: number;
  name: string;
  tin: string;
  ehrUrl: string;
  ehrSystem: string;
  slug: string;
  cipherKey: string;
  theme: string;
  isPopupsEnabled: boolean;
  config: {
    applications: { [key in Product]?: { sources: ProductSource[]; source: ProductSource } };
  };
}

export type User = {
  accessToken?: string;
  user?: EhrUser;
  organization?: Organization;
  userSessionId: string;
  hostname?: string;
};

export type LoggedInUser = User & Required<Pick<User, 'organization' | 'user'>>;
