import { Entities } from '@deprecated/types';
import {
  AvailableUIInjections,
  EhrResourceFromRuntime,
  UIInjectionName,
} from '@deprecated/types/events';
import { EHRResource, EHR_EVENT } from '@getvim-os/types';
import { ENRICHED_EHR_STATE } from '@getvim-os/types/ehr-state';
import { EnableLaunchButtonPayload, UpdatableEhrFields } from '@getvim/vim-os-api';
import { createSlice } from '@reduxjs/toolkit';
import { merge } from 'lodash-es';
import { v4 as uuid } from 'uuid';
import { PayloadAction } from '..';
import { actions as userActions } from '../user/userState.slice';
import { PendingEhrState } from './types';

const entityHasWrongPatient = (entity: Entities.PatientInEntity, state: ENRICHED_EHR_STATE) => {
  const { patientId, patient } = entity;
  const entityPatientId = patientId || patient?.patientId;
  return entityPatientId && entityPatientId !== state.patient?.patientId;
};

const UI_INJECTIONS_TO_LAUNCH_BUTTON_REAL_ESTATE: Record<
  UIInjectionName,
  EnableLaunchButtonPayload
> = {
  [UIInjectionName.diSelectProvider]: {
    orders: {
      DI_SELECT_PROVIDER: true,
    },
  },
  [UIInjectionName.labsSelectProvider]: {
    orders: {
      LABS_SELECT_PROVIDER: true,
    },
  },
  [UIInjectionName.referralSelectProvider]: {
    referral: {
      REFERRAL_SELECT_PROVIDER: true,
    },
  },
};

export const initialState: PendingEhrState = {};

const convertUiInjectionsToLaunchButtonRealEstate = (uiInjections: UIInjectionName[]) => {
  return uiInjections.reduce(
    (acc, uiInjection) => merge(acc, UI_INJECTIONS_TO_LAUNCH_BUTTON_REAL_ESTATE[uiInjection]),
    {},
  );
};

export const pendingEhrStateSlice = createSlice({
  name: 'ehrState',
  initialState,
  reducers: {
    resetState: () => {
      return initialState;
    },
    setUpdatableFields: (state, action: PayloadAction<UpdatableEhrFields | undefined>) => {
      state.updatableFields = action.payload;
    },
    updateUpdatableFields: (state, action: PayloadAction<Partial<UpdatableEhrFields>>) => {
      state.updatableFields = { ...state.updatableFields, ...action.payload };
    },
    onEhrEvent: (state, action: PayloadAction<EHR_EVENT>) => {
      state.ehrEvent = action.payload;
    },
    setPatient: (state, action: PayloadAction<EhrResourceFromRuntime<EHRResource.patient>>) => {
      const { availableWriteBacks, availableUIInjections, ...patient } = action.payload;
      if (state.patient?.patientId !== patient.patientId) {
        //new patient in context - reset all ehr state
        const newState = {
          ...initialState,
          patient,
        };
        newState.patient.patientSessionId = uuid();
        pendingEhrStateSlice.caseReducers.setLaunchButtonRealEstate(newState, action);

        return newState;
      }
      state.patient = { ...patient, patientSessionId: state.patient.patientSessionId };
      pendingEhrStateSlice.caseReducers.setLaunchButtonRealEstate(state, action);
    },
    unsetPatient: (state) => {
      state.patient = undefined;
    },
    setEncounter: (state, action: PayloadAction<EhrResourceFromRuntime<EHRResource.encounter>>) => {
      const { availableWriteBacks, availableUIInjections, ...encounter } = action.payload;
      if (entityHasWrongPatient(encounter, state)) {
        action.logger?.info('skipping set encounter with wrong patient', {
          encounterId: encounter?.encounterId,
          encounterPatientId: encounter?.patientId ?? encounter.patient?.patientId,
          patientId: state.patient?.patientId,
        });
        return state;
      }
      const { patient, patientId, ...encounterWithoutPatient } = encounter;
      state.encounter = encounterWithoutPatient;
      pendingEhrStateSlice.caseReducers.setLaunchButtonRealEstate(state, action);
    },
    updateEncounter: (
      state,
      action: PayloadAction<EhrResourceFromRuntime<EHRResource.encounter>>,
    ) => {
      const { availableWriteBacks, availableUIInjections, ...encounter } = action.payload;

      if (entityHasWrongPatient(encounter, state)) {
        action.logger?.info('skipping update encounter with wrong patient', {
          encounterId: encounter?.encounterId,
          encounterPatientId: encounter?.patientId ?? encounter.patient?.patientId,
          patientId: state.patient?.patientId,
        });
        return state;
      }

      const { patient, patientId, ...encounterWithoutPatient } = encounter;
      state.encounter = { ...state.encounter, ...encounterWithoutPatient };
    },
    unsetEncounter: (state) => {
      delete state.launchButtonRealEstate?.encounter;
      state.encounter = undefined;
    },
    setReferral: (state, action: PayloadAction<EhrResourceFromRuntime<EHRResource.referral>>) => {
      const { availableWriteBacks, availableUIInjections, ...referral } = action.payload;
      if (entityHasWrongPatient(referral, state)) {
        action.logger?.info?.('skipping referral with wrong patient', {
          referral,
          patient: state.patient,
          referralPatientId: referral?.patientId ?? referral.patient?.patientId,
        });
        return state;
      }
      const { patient, patientId, ...referralWithoutPatient } = referral;
      state.referral = referralWithoutPatient;
      pendingEhrStateSlice.caseReducers.setLaunchButtonRealEstate(state, action);
    },
    unsetReferral: (state) => {
      delete state.launchButtonRealEstate?.referral;
      state.referral = undefined;
    },
    setOrders: (state, action: PayloadAction<EhrResourceFromRuntime<EHRResource.orders>>) => {
      const { availableWriteBacks, availableUIInjections, ...orders } = action.payload;
      if (entityHasWrongPatient(orders, state)) {
        action.logger?.info('skipping orders with wrong patient', {
          orders,
          patient: state.patient,
          ordersPatientId: orders?.patientId ?? orders.patient?.patientId,
        });

        return state;
      }
      state.orders = orders.selectedOrders;
      pendingEhrStateSlice.caseReducers.setLaunchButtonRealEstate(state, action);
    },
    unsetOrders: (state) => {
      delete state.launchButtonRealEstate?.orders;
      state.orders = undefined;
    },
    setLaunchButtonRealEstate: (state, action: PayloadAction<AvailableUIInjections>) => {
      const uiInjections = action.payload.availableUIInjections;
      if (uiInjections) {
        state.launchButtonRealEstate = convertUiInjectionsToLaunchButtonRealEstate(uiInjections);
      }
    },
  },
  extraReducers: (builder) => {
    builder.addCase(userActions.logout, () => {
      return initialState;
    });
  },
});

export const actions = pendingEhrStateSlice.actions;

export default pendingEhrStateSlice.reducer;
