import { inject, injectable } from 'inversify';
import { BareboneManager } from '@/barebone-manager';
import { InitializeOnBootstrap } from '@/bootstrap';

const PADDING = 20;

@injectable()
export class ClipPathObserver implements InitializeOnBootstrap {
  private resizeObserver?: ResizeObserver;
  private mutationObserver?: MutationObserver;
  private observedElements: Set<HTMLElement> = new Set();

  constructor(@inject(BareboneManager) private bareboneManager: BareboneManager) {}

  initialize() {
    if (this.bareboneManager.shouldUseClipPath) {
      this.mutationObserver = new MutationObserver(() => {
        this.dispatchClipPath();
      });
      window.addEventListener('resize', () => {
        this.dispatchClipPath();
      });
      this.resizeObserver = new ResizeObserver(() => {
        this.dispatchClipPath();
      });
      this.dispatchClipPath();
    }
  }

  public observeBounds(element: HTMLElement) {
    if (!this.observedElements.has(element)) {
      this.observedElements.add(element);
      this.resizeObserver?.observe(element);
      this.mutationObserver?.observe(element, { subtree: true, childList: true, attributes: true });
      this.dispatchClipPath();
    }
    return () => {
      this.resizeObserver?.unobserve(element);
      this.observedElements.delete(element);
      this.dispatchClipPath();
    };
  }

  public dispatchClipPath() {
    if (!this.bareboneManager.shouldUseClipPath) {
      return;
    }
    let clipPath = `path('`;

    for (const element of this.observedElements) {
      const { x, y, height, width } = element.getBoundingClientRect();
      if (height * width > 0) {
        const startX = Math.floor(x - PADDING / 2);
        const startY = Math.floor(y - PADDING / 2);
        const paddedWidth = Math.floor(width + PADDING);
        const paddedHeight = Math.floor(height + PADDING);
        clipPath += ` M ${startX} ${startY} h ${paddedWidth} v ${paddedHeight} h ${-paddedWidth} v ${-paddedHeight} `;
      }
    }
    clipPath += `Z')`;

    if (clipPath === `path('Z')`) {
      clipPath = `path('M 0 0 Z')`;
    }
    this.bareboneManager.messaging.sendRawMessage({
      type: 'change-clip-path',
      payload: {
        state: clipPath,
      },
    });
  }
}
