import { inject, injectable } from 'inversify';
import { GET_APPLICATION_VERSIONS_QUERY } from './queries';
import { VimOsBffGraphQLClient } from '../vimOsBffGqlClient';
import { GetApplicationVersionsInput, GetApplicationVersionsResponse } from './types';

@injectable()
export class ApplicationVersionAPI {
  constructor(@inject(VimOsBffGraphQLClient) private gqlClient: VimOsBffGraphQLClient) {}

  public async getApplicationVersions(
    input: GetApplicationVersionsInput,
    operationName: string,
  ): Promise<GetApplicationVersionsResponse | undefined> {
    const result = await this.gqlClient.queryAuthenticated<
      { getApplicationVersions: GetApplicationVersionsResponse },
      { input: GetApplicationVersionsInput }
    >({
      query: GET_APPLICATION_VERSIONS_QUERY(operationName),
      variables: { input },
    });

    return result.data.getApplicationVersions;
  }
}
