import { z } from 'zod';
import { ProviderOrganizationSchema } from './providerOrganizationSchema';
import { ProviderDemographicsSchema } from './providerDemographicsSchema';

export const ProviderSchema = z.object({
  ehrProviderId: z.string(),
  npi: z.string().optional(),
  demographics: ProviderDemographicsSchema,
  facility: ProviderOrganizationSchema.optional(),
  specialty: z
    .array(z.object({ description: z.string(), taxonomies: z.array(z.string()) }))
    .optional(),
  providerDegree: z.string().optional(),
});

export type Provider = z.infer<typeof ProviderSchema>;
