export interface SessionKeyInput {
  publicKey: string;
}

export interface SessionKeyResponse {
  encryptedSessionKey: string;
  sessionKeyId: string;
}

export interface VaultPutInput {
  sensitiveData: string;
  iv: string;
  entityType: EntityType;
  sessionKeyId: string;
}

export type VaultPutResult = {
  [key in EntityType as `${Lowercase<key>}Put`]?: string;
};

export enum EntityType {
  PATIENT = 'PATIENT',
  REFERRAL = 'REFERRAL',
}
