import { inject, injectable } from 'inversify';
import { RuntimeMessageHooks } from './messageHooks';
import { FrameSource } from '@getvim-os/barebone/infra';

type ListenerMap = Record<string, (data: any, source: FrameSource) => boolean>;

@injectable()
export class AppsToVimConnectCommunicatorMessageListener {
  constructor(@inject(RuntimeMessageHooks) private messageHooks: RuntimeMessageHooks) {}

  public listenToEvents(listeners: ListenerMap) {
    this.messageHooks.addMessageToRuntimeInterceptor((msg) => {
      if (msg.data?.type === 'vim-connect-send-data-to-vim-connect-ui') {
        const { event, data } = msg.data.payload as { event: string; data };
        if (listeners[event]) {
          return listeners[event](data, msg.source);
        }
      }
      return false;
    });
  }
}
