import { OsToAdapterEventData } from '@getvim-adapter/api';
import { Logger, LoggerFactory } from '@getvim-os/logger';
import { NotInitializedError } from '@getvim-os/errors';
import { inject, injectable } from 'inversify';
import { RuntimeMessageHooks } from './messageHooks';
import { OsToRuntimeMessage } from '../types/events';

const OS_HANDSHAKE_MESSAGE_TYPE = 'vim-os-message-channel-handshake';
const RUNTIME_READY_MESSAGE_TYPE = 'vim-os-message-channel-runtime-ready';

@injectable()
export class OsMessageChannelManager {
  private logger: Logger;
  private messagePortPromise: Promise<MessagePort>;
  private messagePortResolve?: (port: MessagePort) => void;

  constructor(
    @inject(LoggerFactory) loggerFactory: LoggerFactory,
    @inject(RuntimeMessageHooks) private messageHooks: RuntimeMessageHooks,
  ) {
    this.logger = loggerFactory.create('runtime-os-message-channel-manager');
    this.messagePortPromise = new Promise((resolve) => {
      this.messagePortResolve = resolve;
    });
  }

  public initOSMessageChannel(runtimeIframe: HTMLIFrameElement) {
    runtimeIframe.addEventListener('load', () => {
      try {
        const channel = new MessageChannel();

        channel.port1.addEventListener('message', (msg) => {
          if (msg?.data?.type === RUNTIME_READY_MESSAGE_TYPE) {
            this.logger.info('OS Message Channel initialized');
            this.messagePortResolve!(channel.port1);
          } else {
            this.messageHooks.onOSMessageFromRuntime(msg);
          }
        });
        channel.port1.start();

        runtimeIframe.contentWindow?.postMessage({ type: OS_HANDSHAKE_MESSAGE_TYPE }, '*', [
          channel.port2,
        ]);
      } catch (error) {
        this.logger.error(
          'Could not setup OS message channel with runtime',
          {
            errorMessage: (error as Error).message,
            iframeId: runtimeIframe?.id,
          },
          error as Error,
        );
      }
    });
  }

  private getMessagePort(): Promise<MessagePort | undefined> {
    return new Promise((resolve) => {
      const timeout = setTimeout(() => {
        resolve(undefined);
      }, 30_000);
      (async () => {
        const messagePort = await this.messagePortPromise;
        clearTimeout(timeout);
        resolve(messagePort);
      })();
    });
  }

  public async sendMessageToRuntime(
    message: OsToRuntimeMessage | OsToAdapterEventData,
    transfer: Transferable[] = [],
  ) {
    const messagePort = await this.getMessagePort();
    if (!messagePort) {
      throw new NotInitializedError('MessageChannel between VimOS and Runtime');
    }

    messagePort.postMessage(message, transfer);
  }
}
