/* eslint-disable check-file/filename-naming-convention */
import { inject, injectable } from 'inversify';
import { iframeWidgetIdTag } from '../../../hub/components/app-wrapper/ApplicationIframe';
import { RuntimeMessageHooks, VIM_OS_EVENTS } from '../../runtime-wrapper/messageHooks';
import type { FrameSource } from '@getvim-os/barebone/infra';
import { Logger, LoggerFactory } from '@getvim-os/logger';

export type DataArrivalCallback<T> = (data: T, frameSource: FrameSource) => void;

@injectable()
export class RuntimeHubWidgetInfra {
  private logger: Logger;

  constructor(
    @inject(RuntimeMessageHooks) private messageHooks: RuntimeMessageHooks,
    @inject(LoggerFactory) loggerFactory: LoggerFactory,
  ) {
    this.logger = loggerFactory.create('RuntimeHubWidgetInfra');
  }

  public initialize() {
    this.messageHooks.addOSMessageHandler(VIM_OS_EVENTS.DATA_TO_WIDGET, (msg) => {
      const { widgetId, payload } = msg.payload as any;
      const widgetIframe = document.querySelector(
        `iframe[${iframeWidgetIdTag}="${widgetId}"]`,
      ) as HTMLIFrameElement;
      if (!widgetIframe?.contentWindow) {
        this.logger.error(`iframe widget of ${widgetId} was not found!`, { widgetId });
      }
      widgetIframe?.contentWindow?.postMessage(
        { type: 'vim-connect-send-data-to-widget', payload },
        '*',
      );
    });

    this.messageHooks.addMessageToRuntimeInterceptor((msg) => {
      if (msg.data?.type !== 'vim-connect-send-data-to-vim-connect-ui') {
        return false;
      }

      const { event, data } = msg.data.payload as { event: string; data: { payload?: unknown } };

      if (event === 'vim-connect-ui-send-to-runtime') {
        this.sendWidgetDataToRuntime({
          widgetId: 'vim-connect-ui',
          payload: {
            event: 'vim-connect-ui-widget-send-data-to-app',
            data,
          },
        });
        return true;
      }

      return false;
    });
  }

  private sendWidgetDataToRuntime(payload: unknown) {
    (
      document.getElementById('deprecated-runtime-iframe') as HTMLIFrameElement
    )?.contentWindow?.postMessage(
      {
        type: 'vim-connect-incoming-from-os-hub-widget-data',
        payload,
      },
      '*',
    );
  }

  public sendDataToRuntimeApp(payload: unknown) {
    (
      document.getElementById('deprecated-runtime-iframe') as HTMLIFrameElement
    ).contentWindow?.postMessage(
      {
        type: 'vim-connect-widget-data-to-app',
        payload: { payload, widgetId: 'vim-connect-ui' },
      },
      '*',
    );
  }
}
