import React from 'react';
import helpIconRed from '@/atomic/assets/help-icon-red.svg';
import { Button, ColorNameEnum, SizeEnum, Text, WeightEnum } from '@getvim/atomic-ui';
import failSvg from '../images/fail.svg';
import { CloseButton } from '@/atomic/atoms';

const FailPopover = ({ onClick, onClose }) => (
  <>
    <CloseButton onClick={onClose} dataTestId={'support-tab-close-button'} />
    <div className="text-center" data-testId="email-sent-or-failed">
      <img src={helpIconRed} className="help-icon-red" alt="Help icon"></img>
      <Text
        text="Oops"
        colorName={ColorNameEnum.extraDark}
        weight={WeightEnum.bold}
        size={SizeEnum['14px']}
      />
      <Text
        text="Something went wrong, please try again."
        colorName={ColorNameEnum.extraDark}
        weight={WeightEnum.normal}
        size={SizeEnum['14px']}
      />
      <div className="margin-top-50 margin-bottom-45">
        <img src={failSvg} alt="fail image" />
      </div>
      <Button
        text="Try Again"
        buttonType="tiny"
        bgColor="themedOutline"
        width="small"
        onClick={onClick}
      />
    </div>
  </>
);

export default FailPopover;
