import React from 'react';
import helpIconRed from '@/atomic/assets/help-icon-red.svg';
import { Button, ColorNameEnum, ProgressBar, SizeEnum, Text, WeightEnum } from '@getvim/atomic-ui';
import emailSvg from '../images/email.svg';
import { CloseButton } from '@/atomic/atoms';
import './index.less';

interface SuccessPopoverProps {
  email: string;
  onClick: () => void;
  onClose: () => void;
  animation_delay: number;
}

const SuccessPopover = ({ email, onClick, onClose, animation_delay }: SuccessPopoverProps) => (
  <>
    <CloseButton onClick={onClose} dataTestId={'support-tab-close-button'} />
    <div className="text-center" data-testId="email-sent-or-failed">
      <img src={helpIconRed} className="help-icon-red" alt="Help icon"></img>
      <Text
        text=" Issue submitted successfully."
        colorName={ColorNameEnum.extraDark}
        weight={WeightEnum.bold}
        size={SizeEnum['14px']}
      />
      <Text
        text={`Our support team will be in touch with you shortly at ${email}`}
        colorName={ColorNameEnum.extraDark}
        weight={WeightEnum.normal}
        size={SizeEnum['14px']}
      />
      <div className="margin-top-50 margin-bottom-45">
        <img src={emailSvg} alt="email image" />
      </div>
      <Button
        text="Report a new issue"
        buttonType="tiny"
        bgColor="themedOutline"
        width="smallMedium"
        onClick={onClick}
      />
      <br />
      <Button
        text="Close"
        className="margin-top-5"
        buttonType="tiny"
        bgColor="default"
        width="smallMedium"
        onClick={onClose}
      />
      <ProgressBar
        progressBarStyle="tiny"
        className="success-popover-progressbar"
        duration={`${animation_delay}ms`}
        animate
      ></ProgressBar>
    </div>
  </>
);

export default SuccessPopover;
