import { ErrorCodes, writebackErrorCodes } from '..';
import { StandardError } from '../base-error';

export type WritebackErrorProps = {
  errorCode: writebackErrorCodes[keyof writebackErrorCodes];
  error: unknown;
  status?: any;
  writebackPayload?: any;
  cause?: any;
  noPHI: boolean;
};

export class WritebackError extends StandardError {
  constructor(message: string, props: WritebackErrorProps) {
    super(message, ErrorCodes.INTERNAL_ERROR, { errorName: 'WriteBackError', ...props });
  }
}
