import { WebConnectors } from '../..';
import { WebConnectorMessageTypes } from '../../enum';

export async function decryptECWStringByKey(
  this: WebConnectors,
  {
    encryptedString,
    encryptionStorageKey,
  }: {
    encryptedString: string;
    encryptionStorageKey: string;
  },
): Promise<string> {
  return await this.messaging.sendMessage({
    type: WebConnectorMessageTypes.decryptECWStringByKey,
    payload: { encryptedString, encryptionStorageKey },
  });
}
