import { gql } from '@getvim-os/gql-client';

export const GET_ORGANIZATION_DETAILS_QUERY = gql`
  query getDeviceOrganizationDetailsQuery($input: OrganizationDataOnDevice!) {
    getDeviceOrganizationDetails(input: $input) {
      organizationId
      organizationKey
      organizationName
      organizationEhrSystem
    }
  }
`;

export const GET_ORGANIZATION_DETAILS_WITH_TAG_QUERY = gql`
  query getDeviceOrganizationDetailsQuery($input: OrganizationDataOnDevice!) {
    getDeviceOrganizationDetails(input: $input) {
      organizationId
      organizationKey
      organizationName
      organizationTag
      organizationEhrSystem
    }
  }
`;
