import { WebConnectors } from '../..';
import { WebConnectorMessageTypes } from '../../enum';
import { AddEventListenerInput } from '../../types/add-event-listener-input';
import { AddEventListenerResult } from '../../types/addEventListenerResult';

export async function addEventListener(
  this: WebConnectors,
  { selector, type, handler }: AddEventListenerInput,
): Promise<AddEventListenerResult | boolean> {
  const listenerId: string = await this.messaging.sendMessageWithHandler({
    type: WebConnectorMessageTypes.addEventListener,
    payload: { type, selector },
    handler,
  });
  return {
    remove: () => this.removeEventListener({ listenerId: listenerId as string }),
    listenerId,
  };
}
