export const getAffectedFields = (data, parentDetail = '') => {
  let details: string[] = [];
  for (const detail in data) {
    if (Object.prototype.hasOwnProperty.call(data, detail)) {
      const value = data[detail];
      const currentDetail = parentDetail ? `${parentDetail}.${detail}` : detail;

      if (value !== null && value !== undefined) {
        if (Array.isArray(value)) {
          if (value.length > 0) {
            details.push(currentDetail);
          }
        } else if (typeof value === 'object') {
          details = details.concat(getAffectedFields(value, currentDetail));
        } else {
          details.push(currentDetail);
        }
      }
    }
  }

  return details;
};
