import React, { createContext, useContext, useCallback, useState } from 'react';

type UserManagementContextValue = {
  notifications: number;
  setNotifications: (notification: number) => void;
};

const UserManagementContext = createContext<UserManagementContextValue>({
  notifications: 0,
  // eslint-disable-next-line @typescript-eslint/no-empty-function
  setNotifications: () => {},
});

export const UserManagementProvider: React.FC<
  React.PropsWithChildren<{ onNotificationsUpdate: (notifications: number) => void }>
> = ({ children, onNotificationsUpdate }) => {
  const [notifications, setNotifications] = useState(0);

  const setNotificationsHook = useCallback(
    (notification: number) => {
      onNotificationsUpdate?.(notification);
      setNotifications(notification);
    },
    [onNotificationsUpdate],
  );

  return (
    <UserManagementContext.Provider
      value={{ notifications, setNotifications: setNotificationsHook }}
    >
      {children}
    </UserManagementContext.Provider>
  );
};

export const useUserManagement = () => {
  return useContext(UserManagementContext);
};
