import { inject, injectable } from 'inversify';
import { BareboneMessaging } from '../../infra/messaging';
import {
  CommonConnectorMessageTypes,
  StorageKey,
  StorageSet,
  ModifyWidgetInput,
  InjectWidgetInput,
} from './message-types';

@injectable()
export class CommonConnectors {
  private _context?: string;

  constructor(@inject(BareboneMessaging) protected messaging: BareboneMessaging) {}

  public get context() {
    return this._context;
  }
  public set context(value) {
    this._context = value;
  }

  public async storageGet<T = unknown>({ key }: StorageKey): Promise<T> {
    return await this.messaging.sendMessage({
      type: CommonConnectorMessageTypes.chromeStorageGet,
      payload: { key },
    });
  }

  public async storageSet({ data }: StorageSet): Promise<void> {
    return await this.messaging.sendMessage({
      type: CommonConnectorMessageTypes.chromeStorageSet,
      payload: { data },
    });
  }

  public async storageRemove({ key }: StorageKey): Promise<void> {
    return await this.messaging.sendMessage({
      type: CommonConnectorMessageTypes.chromeStorageRemove,
      payload: { key },
    });
  }

  public async modifyWidget({ widget }: ModifyWidgetInput): Promise<void> {
    return await this.messaging.sendMessage({
      type: CommonConnectorMessageTypes.modifyWidget,
      payload: { widget },
    });
  }
  public async injectWidget({ selector, frameSelector, widget }: InjectWidgetInput) {
    return await this.messaging.sendMessage({
      type: CommonConnectorMessageTypes.injectWidget,
      payload: { selector, frameSelector, widget },
    });
  }
  public async copyToClipboard(text: string): Promise<any> {
    return await this.messaging.sendMessage({
      type: CommonConnectorMessageTypes.copyToClipboard,
      payload: { text },
    });
  }
  public async removeWidget(widgetId: string) {
    return await this.messaging.sendMessage({
      type: CommonConnectorMessageTypes.removeWidget,
      payload: { widgetId },
    });
  }
}
