import { createAsyncThunk, createSlice } from '@reduxjs/toolkit';
import { actions as userActions } from '../user/userState.slice';
import { VaultState } from './types';
import { RootState, ThunkExtra } from '..';
import { VaultManager } from './vault-tokenizer/vault.service';

export const initialState: VaultState = {};

export const createVaultSession = createAsyncThunk<
  VaultState,
  undefined,
  {
    state: RootState;
    extra: ThunkExtra;
  }
>('createVaultSession', async (_, api) => {
  const container = api.extra.container;
  const vaultManager = container.get(VaultManager);
  return await vaultManager.createSessionKey();
});

export const vaultSlice = createSlice({
  name: 'vault',
  initialState,
  reducers: {},
  extraReducers: (builder) => {
    builder
      .addCase(userActions.logout, () => {
        return initialState;
      })
      .addCase(createVaultSession.fulfilled, (_state, action) => {
        return action.payload;
      });
  },
});

export const actions = vaultSlice.actions;

export default vaultSlice.reducer;
