export interface MessengerType {
  send: (message: any) => Promise<any>;
  on: (event: string, listener: (data: any) => void) => void;
}
interface Message {
  type?: MessageType;
  metadata: { id: string };
  payload: any;
}
/**
 * A messenger implementation that uses web frame messaging (i.e window.postMessage)
 */
@injectable()
export class FrameMessenger implements MessengerType {
  constructor(@inject(BareboneMessaging) private messaging: BareboneMessaging) {}

  public async send<T extends Message>({ payload }): Promise<T> {
    return await this.messaging.sendMessage({
      type: 'sendAndReceiveDataFromWindowsAgent',
      payload,
      timeout: 30_000,
    });
  }

  public on(event, handler) {
    return this.messaging.sendMessageWithHandler({
      type: 'sendAndReceiveDataFromWindowsAgent',
      payload: { methodName: 'On', event },
      handler,
    });
  }
}
