import { inject, injectable } from 'inversify';
import { UPDATE_ME } from './queries';
import { VimOsBffGraphQLClient } from '../vimOsBffGqlClient';
import { UpdateMeInput, UpdateMeResponse } from './types';

@injectable()
export class UserAPI {
  constructor(@inject(VimOsBffGraphQLClient) private gqlClient: VimOsBffGraphQLClient) {}

  public async updateMe(
    input: Pick<UpdateMeInput, 'roles'>,
  ): Promise<UpdateMeResponse | undefined> {
    const result = await this.gqlClient.mutateAuthenticated<
      { updateMe: UpdateMeResponse },
      { input: Pick<UpdateMeInput, 'roles'> }
    >({
      mutation: UPDATE_ME,
      variables: { input },
    });

    return result.data?.updateMe;
  }
}
