/* eslint-disable @typescript-eslint/no-unused-vars */
export enum EncounterEnhancements {}

// eslint-disable-next-line @typescript-eslint/ban-types
export type EncounterEnhancementsAPI = {};

/**
 * THIS Types below are used to verify that the EncounterEnhancementsAPI
 * uses all the properties from the enum & the other way around
 *
 * The reason is using mapped types "[EncounterEnhancements.GET_PROBLEM_LIST]()" instead of "getProblemList()"
 * creates bad autocomplete typings
 */
type KeysOfAPI = {
  [x in keyof EncounterEnhancementsAPI]: any;
};
type KeysOfEnum = {
  [x in EncounterEnhancements]: any;
};
const enumObj: KeysOfEnum = {} as any;
const apiObj: KeysOfAPI = {} as any;

const assertOneWay = enumObj satisfies KeysOfAPI;
const assertOtherWay = apiObj satisfies KeysOfEnum;
