import { z } from 'zod';
import {
  DiagnosisSchema,
  ProcedureSchema,
  ProviderSchema,
  SpecialtySchema,
} from '../../entities/sub-entities';

export const UpdateReferralSchema = z.object({
  specialty: SpecialtySchema.optional(),
  diagnosis: z.array(DiagnosisSchema).optional(),
  reasons: z.array(z.string()).optional(),
  startDate: z.string().date().optional(), // yyyy-MM-dd
  endDate: z.string().date().optional(), // yyyy-MM-dd
  notes: z.string().optional(),
  priority: z.enum(['ROUTINE', 'URGENT', 'STAT']).optional(),
  cpts: z.array(ProcedureSchema).optional(),
  numberOfVisits: z.string().optional(),
  authCode: z.string().optional(),
  targetProvider: ProviderSchema.optional(),
  referringProvider: ProviderSchema.optional(),
});

export type UpdateReferral = z.infer<typeof UpdateReferralSchema>;

const ProviderToWriteBackSchema = ProviderSchema.omit({ ehrProviderId: true });

export const UpdateReferralWritebackInputSchema = UpdateReferralSchema.pick({
  specialty: true,
  diagnosis: true,
  reasons: true,
  startDate: true,
  endDate: true,
  notes: true,
  priority: true,
  cpts: true,
  numberOfVisits: true,
  authCode: true,
})
  .extend({
    targetProvider: ProviderToWriteBackSchema,
  })
  .partial();
