import { Logger } from '@getvim-os/logger';
import { MessengerType } from './messaging';
import { injectable } from 'inversify';

@injectable()
export class Hub<TChannels> {
  private channels: Partial<TChannels> = {};

  constructor(private messenger: MessengerType, private logger: Logger) {}

  public channel<TChannel extends keyof TChannels & string>(name: TChannel): TChannels[TChannel] {
    if (!this.channels[name]) {
      this.channels[name] = this.createProxyChannel(name, this.messenger) as any;
    }
    return this.channels[name] as any;
  }

  private createProxyChannel = <T extends Record<any, unknown>>(
    name: string,
    messenger: MessengerType,
  ): T => {
    return new Proxy<T>({} as any, {
      get: (_, method) => {
        if (typeof method !== 'string') {
          this.logger.error(`Given method for ${name} has invalid type of ${typeof method}`, {});
          return undefined;
        }

        if (method == 'on') {
          return (event: string, listener: (data: any) => void) => {
            messenger.on(event, listener);
          };
        }

        return (...args: any) => {
          return messenger.send({
            payload: {
              methodName: this.toPascalCase(method as string),
              arguments: args,
              channelName: name,
            },
          });
        };
      },
    });
  };

  toPascalCase = (value: string) => value[0].toUpperCase() + value.slice(1);
}
