import { Logger, LoggerFactory } from '@getvim-os/logger';
import { VimOsGraphQLClient } from '@getvim-os/gql-client';
import { inject, injectable } from 'inversify';
import { OrganizationNotFoundError } from './errors';
import { GET_ORGANIZATION_DETAILS_QUERY, GET_ORGANIZATION_DETAILS_WITH_TAG_QUERY } from './queries';
import { OrganizationDataOnDevice, OrganizationDetails } from './types';
import { OsFeatureFlagsClient } from '@/infra/featureFlags';

@injectable()
export class OrganizationsApi {
  private logger: Logger;

  constructor(
    @inject(VimOsGraphQLClient) private gqlClient: VimOsGraphQLClient,
    @inject(OsFeatureFlagsClient) private ffClient: OsFeatureFlagsClient,
    @inject(LoggerFactory) loggerFactory: LoggerFactory,
  ) {
    this.logger = loggerFactory.create('Organization-API');
  }

  public getDeviceOrganizationDetails = async (
    orgDataOnDevice: OrganizationDataOnDevice,
  ): Promise<OrganizationDetails> => {
    try {
      const shouldAddTagToQuery = await this.ffClient.getFlag({
        flagName: 'shouldAddTagToGetOrganizationDetails',
        defaultValue: false,
        namespace: 'vim_connect',
      });
      const { data } = await this.gqlClient.query<
        { getDeviceOrganizationDetails: OrganizationDetails },
        { input: OrganizationDataOnDevice }
      >({
        query: shouldAddTagToQuery
          ? GET_ORGANIZATION_DETAILS_WITH_TAG_QUERY
          : GET_ORGANIZATION_DETAILS_QUERY,
        variables: { input: orgDataOnDevice },
      });

      return data!.getDeviceOrganizationDetails;
    } catch (error) {
      if ((error as Error)?.message?.includes('Request failed with status code 404')) {
        throw new OrganizationNotFoundError({ orgDataOnDevice, error });
      } else {
        this.logger.warning(
          'Failed to get device organization Id',
          {
            orgDataOnDevice,
          },
          error as Error,
        );
        throw error;
      }
    }
  };
}
