import { injectable, inject } from 'inversify';
import { RuntimeIframeBootstrapper } from './bootstrap-runtime-iframe';
import { Logger, LoggerFactory } from '@getvim-os/logger';
import { BROADCAST_MODE } from '../../inversifyTokens';
import { OsFeatureFlagsClient } from '@/infra/featureFlags';
import { OsState } from '@/store';

@injectable()
export class RuntimeWrapper {
  private logger: Logger;
  static wrapperId = 'app-iframe-wrapper';

  constructor(
    @inject(RuntimeIframeBootstrapper) private iframeBootstrapper: RuntimeIframeBootstrapper,
    @inject(LoggerFactory) loggerFactory: LoggerFactory,
    @inject(OsFeatureFlagsClient) private ffClient: OsFeatureFlagsClient,
    @inject(OsState) private state,
    @inject(BROADCAST_MODE) private broadcastMode: boolean,
  ) {
    this.logger = loggerFactory.create('runtime-wrapper');
  }

  public async loadRuntime(
    organizationId?: string,
    organizationName = '',
    organizationTag = '',
  ): Promise<void> {
    const searchParams = new URLSearchParams(window.location.search);
    const adapterUrlRaw = searchParams.get('adapterUrl');
    const adapterVersion = searchParams.get('adapterVersion');
    const overrideV2 = searchParams.get('override-v2');
    const overrideV2Url = searchParams.get('override-v2-url');
    const overrideV2LocalUrl = searchParams.get('override-v2-local-url');
    const v2LocalUrl = searchParams.get('v2-adapter-local-url');
    const osSimulator = searchParams.get('osSimulator');
    const bareboneType = searchParams.get('bareboneType');
    const bareboneVersion = searchParams.get('bareboneVersion');
    const decodeOrgName = window.btoa(organizationName);

    let adapterUrl = adapterUrlRaw;
    if (adapterUrlRaw && !adapterUrlRaw?.startsWith('http')) {
      adapterUrl = window.atob(adapterUrlRaw!);
    }

    this.logger.info('Loading runtime', {
      adapterUrl,
    });

    if (!adapterUrl) {
      throw new Error("adapterURL does not exist in the query params, can't load runtime");
    }

    const appWrapperDiv = document.createElement('div');
    appWrapperDiv.setAttribute('id', RuntimeWrapper.wrapperId);
    Object.assign(appWrapperDiv.style, {
      position: 'absolute',
      border: 0,
      width: 0,
      height: 0,
      zIndex: 999_999_999_999,
    });
    const userSessionId = this.state?.store?.getState()?.userState?.userSessionId;
    const iframeElement = await this.createRuntimeAdapterIframe(
      adapterUrl,
      overrideV2,
      osSimulator,
      organizationId,
      decodeOrgName,
      overrideV2Url,
      // backwards compatibility due to different release cycles of extension and vim-os
      v2LocalUrl ?? overrideV2LocalUrl,
      bareboneType,
      bareboneVersion,
      userSessionId,
      organizationTag,
      adapterVersion,
    );

    this.iframeBootstrapper.bootstrapRuntimeIframe(iframeElement, adapterUrl);

    document.body.append(appWrapperDiv);
    appWrapperDiv.append(iframeElement);
  }

  private async createRuntimeAdapterIframe(
    adapterUrl: string,
    overrideV2?: string | null,
    osSimulator?: string | null,
    organizationId?: string,
    organizationName?: string,
    overrideV2Url?: string | null,
    v2LocalUrl?: string | null,
    bareboneType?: string | null,
    bareboneVersion?: string | null,
    userSessionId?: string | null,
    organizationTag?: string,
    adapterVersion?: string | null,
  ) {
    const iframe = document.createElement('iframe');

    const url = new URL(adapterUrl);

    url.searchParams.append('rand', Date.now().toString());
    url.searchParams.append('use_os_message_channel', 'true');
    if (!this.broadcastMode) {
      url.searchParams.append('use_message_channel', 'true');
    }
    url.searchParams.append('inside_vim_os', 'true');
    if (organizationId) {
      url.searchParams.append('organization_id', organizationId);
    }
    if (organizationName) {
      url.searchParams.append('organization_name', organizationName);
    }
    if (organizationTag) {
      url.searchParams.append('organization_tag', organizationTag);
    }
    if (overrideV2) url.searchParams.append('override-v2', overrideV2);
    if (overrideV2Url) url.searchParams.append('override-v2-url', overrideV2Url);
    if (v2LocalUrl) url.searchParams.append('v2-adapter-local-url', v2LocalUrl);
    if (osSimulator) url.searchParams.append('osSimulator', osSimulator);
    if (bareboneVersion) url.searchParams.append('bareboneVersion', bareboneVersion);
    if (bareboneType) url.searchParams.append('bareboneType', bareboneType);
    if (userSessionId) url.searchParams.append('userSessionId', userSessionId);
    if (adapterVersion) url.searchParams.append('adapterVersion', adapterVersion);

    iframe.src = url.href;
    iframe.id = 'deprecated-runtime-iframe';
    iframe.allow = 'clipboard-write;';

    Object.assign(iframe.style, {
      display: 'none',
    });

    return iframe;
  }
}
