import { gql } from '@getvim-os/gql-client';

export const GET_APPLICATION_VERSIONS_QUERY = (operationName: string) => gql`
  query ${operationName}($input: GetApplicationVersionsInput) {
    getApplicationVersions(input: $input) {
      applicationVersions {
        appId
        version
        manifest {
          metadata {
            id
            url
            allowedUrls
            launchEndpoint
            tokenEndpoint
            name
            services {
              id
              metadata {
                url
              }
              capabilities {
                enrichEHR {
                  allowedPII
                  resources {
                    patient
                    encounter
                    orders
                    referral
                  }
                }
                enrichUser
                custom
                actions
              }
            }
          }
          session {
            permissions
          }
          UI {
            tooltip
            icon
            iconUrl
            size
            capabilities {
              showPatientHeader
              counterNotificationLabel
              popupNotification
              autoPopup
              overridePopupLimit
              appAlwaysAvailable
            }
            launchButton {
              ... on ConfigurableLaunchButton {
                irrelevantVisibility
                alt
                primaryColor
                borderColor
                fontColor
                mode {
                  small {
                    text
                    image
                  }
                  medium {
                    text
                    image
                  }
                  large {
                    text
                    image
                  }
                }
              }
              ... on CustomLaunchButton {
                customHTML
              }
            }
          }
          EHR {
            resources
            resourceUpdates {
              patient
              encounter
              orders
              referral
            }
            resourcePermissions
            allowedPII
            actions {
              FIND_PATIENT
              GET_PATIENT
              GET_ENCOUNTER_DATA
              GET_PATIENT_ENCOUNTERS
              GET_PROVIDER_DAILY_SUMMARY
              GET_PATIENT_SCHEDULED_APPOINTMENTS
              PRINT_ENCOUNTER
              PRINT_ENCOUNTERS
              SEARCH_PROVIDERS
              GET_ENCOUNTER_LAB_RESULTS
              GET_ENCOUNTER_VITALS
              NAVIGATE_TO_ENCOUNTER
              GET_PROVIDER_APPOINTMENTS
              GET_GAPS_FROM_SHARED_REQUEST
            }
            launchButtonRealEstate {
              referral {
                REFERRAL_SELECT_PROVIDER
              }
              orders {
                DI_SELECT_PROVIDER
                LABS_SELECT_PROVIDER
              }
            }
          }
          auth {
            vimTokens
          }
          vimCapabilities {
            trackAnalytics
          }
        }
      }
    }
  }
`;
