import { sentryTransactionManager } from '@getvim/vim-sentry';
import { SpanType } from './spanType';

export function sentryTransactionWrap() {
  return function (_target: any, propertyKey: string, descriptor: PropertyDescriptor) {
    const func = descriptor.value;
    descriptor.value = async function (this: any, ...args: any) {
      const name = this.name || this.context;
      const context = {
        name: name,
        op: propertyKey,
      };
      const transaction = sentryTransactionManager.startTransaction({
        context,
        id: name,
      });
      try {
        const result = await func.apply(this, args);
        return result;
      } catch (error: any) {
        transaction.setStatus(error?.message);
        throw error;
      } finally {
        transaction.finish();
      }
    };
  };
}

export function sentrySpanWrap(type: SpanType = 'Structure') {
  return function (_target: any, propertyKey: string, descriptor: PropertyDescriptor) {
    const func = descriptor.value;
    descriptor.value = async function (this: any, ...args: any) {
      const name = this.name || this.context;
      const transaction = sentryTransactionManager.getTransaction({
        id: name,
      });
      if (!transaction) {
        const result = await func.apply(this, args);
        return result;
      }
      const span = transaction.startChild({
        description: propertyKey,
        op: name,
        tags: { type },
      });
      try {
        const result = await func.apply(this, args);
        return result;
      } catch (error: any) {
        span.setStatus(error?.message);
        throw error;
      } finally {
        span.finish();
      }
    };
  };
}

export { sentryTransactionManager, Sentry, initSentry } from '@getvim/vim-sentry';
