import { injectable } from 'inversify';
import {
  ResourcePermissions,
  PatientDemographics,
  DeepPartial,
  Order,
  ApplicationManifest,
} from '@getvim-os/types';

import {
  UpdatableEhrFields,
  EncounterOptionsFlags,
  UpdatableEhrState,
  OSEhrStateWithPII,
  EHRResource,
} from '@getvim/vim-os-api';
import { isEmpty, isUndefined, omitBy, isBoolean, isPlainObject } from 'lodash-es';

@injectable()
export class ResourcePermissionsService {
  constructor() {}

  public sanitizeReadPermittedFields<T extends EHRResource>(
    resourceState: OSEhrStateWithPII[T],
    resourcePermissions: ResourcePermissions,
    resource: T,
  ): DeepPartial<OSEhrStateWithPII[T]> | undefined {
    switch (resource) {
      case EHRResource.patient:
        return this.sanitizePatientReadPermittedFields(
          resourceState as OSEhrStateWithPII[typeof EHRResource.patient],
          resourcePermissions[EHRResource.patient],
        ) as DeepPartial<OSEhrStateWithPII[T]>;
      case EHRResource.encounter:
        return this.sanitizeEncounterReadPermittedFields(
          resourceState as OSEhrStateWithPII[typeof EHRResource.encounter],
          resourcePermissions[EHRResource.encounter],
        ) as DeepPartial<OSEhrStateWithPII[T]>;
      case EHRResource.referral:
        return this.sanitizeReferralReadPermittedFields(
          resourceState as OSEhrStateWithPII[typeof EHRResource.referral],
          resourcePermissions[EHRResource.referral],
        ) as DeepPartial<OSEhrStateWithPII[T]>;
      case EHRResource.orders:
        return this.sanitizeOrdersReadPermittedFields(
          resourceState as OSEhrStateWithPII[typeof EHRResource.orders],
          resourcePermissions[EHRResource.orders],
        ) as DeepPartial<OSEhrStateWithPII[T]>;
      default:
        throw new Error(`Unsupported resource: ${resource}`);
    }
  }

  public sanitizeWritePermittedFields<T extends EHRResource>(
    updatableFields: UpdatableEhrFields,
    resourcePermissions: ResourcePermissions,
    resource: T,
  ): Partial<UpdatableEhrFields[T]> | undefined {
    switch (resource) {
      case EHRResource.patient:
        return this.sanitizePatientWritePermittedFields(
          updatableFields[EHRResource.patient],
          resourcePermissions[EHRResource.patient],
        ) as Partial<UpdatableEhrFields[T]>;
      case EHRResource.encounter:
        return this.sanitizeEncounterWritePermittedFields(
          updatableFields[EHRResource.encounter],
          resourcePermissions[EHRResource.encounter],
        ) as Partial<UpdatableEhrFields[T]>;
      case EHRResource.referral:
        return this.sanitizeReferralWritePermittedFields(
          updatableFields[EHRResource.referral],
          resourcePermissions[EHRResource.referral],
        ) as Partial<UpdatableEhrFields[T]>;
      case EHRResource.orders:
        return this.sanitizeOrdersWritePermittedFields(
          updatableFields[EHRResource.orders],
          resourcePermissions[EHRResource.orders],
        ) as Partial<UpdatableEhrFields[T]>;
      default:
        throw new Error(`Unsupported resource: ${resource}`);
    }
  }

  public filterWriteEnabledFieldsInEHR<T extends EHRResource>(
    updatableFields: UpdatableEhrFields[T],
    updatePayload: {
      resource: T;
      dataToUpdate?: UpdatableEhrState<T>;
      dataToAdd?: UpdatableEhrState<T>;
    },
  ): { dataToUpdate: UpdatableEhrState<T>; dataToAdd: UpdatableEhrState<T> } {
    const { resource, dataToUpdate, dataToAdd } = updatePayload;
    const res: ReturnType<typeof this.filterWriteEnabledFieldsInEHR> = {
      dataToAdd: {},
      dataToUpdate: {},
    };
    if (!updatableFields) {
      return res;
    }
    if (dataToAdd) {
      res.dataToAdd = Object.keys(dataToAdd).reduce((acc, fieldToAdd) => {
        if (updatableFields?.[fieldToAdd] === true) {
          return { ...acc, [fieldToAdd]: dataToAdd[fieldToAdd] };
        }
        return acc;
      }, {});
    }
    if (dataToUpdate) {
      res.dataToUpdate = Object.entries(dataToUpdate).reduce((acc, [fieldToUpdate, value]) => {
        const fieldsIsUpdatable =
          resource === EHRResource.encounter
            ? Object.keys(value).every(
                (objectKey) => updatableFields?.[fieldToUpdate]?.[objectKey] === true,
              )
            : updatableFields?.[fieldToUpdate] === true;
        if (fieldsIsUpdatable) {
          return { ...acc, [fieldToUpdate]: value };
        }
        return acc;
      }, {});
    }
    return res;
  }

  public isWriteAllowedForApp<T extends EHRResource>(
    manifest: ApplicationManifest | undefined,
    resource: T,
    dataToUpdate: UpdatableEhrState<T>,
    dataToAdd: UpdatableEhrState<T>,
  ): boolean {
    const { resourceUpdates, resources, resourcePermissions } = manifest?.EHR || {};

    if (!resourcePermissions && !resourceUpdates && !resources) {
      return false;
    }

    if (resourcePermissions) {
      switch (resource) {
        case EHRResource.encounter: {
          const permittedWrite: Partial<EncounterOptionsFlags> =
            this.getEncounterWritePermittedFields(resourcePermissions[resource]);
          return this.isAddAndUpdatedAllowed<typeof EHRResource.encounter>(
            permittedWrite,
            dataToUpdate,
            dataToAdd,
          );
        }

        case EHRResource.referral: {
          const permittedWrite = this.getReferralWritePermittedFields(
            resourcePermissions[resource],
          );
          return this.isAddAndUpdatedAllowed<typeof EHRResource.referral>(
            permittedWrite,
            dataToUpdate,
            dataToAdd,
          );
        }

        default:
          return false;
      }
    } else {
      return !!resourceUpdates?.[resource as EHRResource];
    }
  }

  private isAddAndUpdatedAllowed<T extends EHRResource>(
    permittedWrite: UpdatableEhrFields[T],
    dataToUpdate: UpdatableEhrState<T>,
    dataToAdd: UpdatableEhrState<T>,
  ): boolean {
    return (
      (dataToUpdate
        ? (this.allFieldsPermittedRecursive(permittedWrite, dataToUpdate) as boolean)
        : true) &&
      (dataToAdd ? (this.allFieldsPermittedRecursive(permittedWrite, dataToAdd) as boolean) : true)
    );
  }

  private sanitizePatientReadPermittedFields(
    patient: OSEhrStateWithPII[typeof EHRResource.patient],
    patientPermissions: ResourcePermissions[typeof EHRResource.patient],
  ): DeepPartial<OSEhrStateWithPII[typeof EHRResource.patient]> | undefined {
    let demographics: Partial<PatientDemographics> | undefined;

    if (isUndefined(patientPermissions) || isEmpty(patientPermissions)) {
      return undefined;
    }

    if (patientPermissions?.demographics?.read && patient?.demographics) {
      demographics = {
        firstName: patient.demographics.firstName,
        lastName: patient.demographics.lastName,
        middleName: patient.demographics.middleName,
        dateOfBirth: patient.demographics.dateOfBirth,
        gender: patient.demographics.gender,
      };
    }

    if (patientPermissions?.address?.read && patient?.demographics?.address) {
      if (!demographics) {
        demographics = {};
      }

      demographics.address = patient?.demographics.address;
    }

    const result: DeepPartial<OSEhrStateWithPII[typeof EHRResource.patient]> = {
      token: patient?.token,
      patientId: patientPermissions?.identifiers?.read ? patient?.patientId : undefined,
      vimPatientId: patient?.vimPatientId,
      mrn: patientPermissions?.identifiers?.read ? patient?.mrn : undefined,
      contact_info: patientPermissions?.contactInformation?.read
        ? patient?.contact_info
        : undefined,
      insurance: patientPermissions?.insuranceInfo?.read ? patient?.insurance : undefined,
      problemList: patientPermissions?.problemList?.read ? patient?.problemList : undefined,
      pcp: patientPermissions?.pcp?.read ? patient?.pcp : undefined,
      demographics,
      potentialInsurers: patientPermissions?.insuranceInfo?.read
        ? patient?.potentialInsurers
        : undefined,
      // internal fields - not exposed to external sdk.
      patientSessionId: patientPermissions?.identifiers?.read
        ? patient?.patientSessionId
        : undefined,
      contentSupplierInternalPersonIds: patientPermissions?.identifiers?.read
        ? patient?.contentSupplierInternalPersonIds
        : undefined,
      contentSupplierIdentifiers: patientPermissions?.identifiers?.read
        ? patient?.contentSupplierIdentifiers
        : undefined,
      releaseOfInformation: patientPermissions?.insuranceInfo?.read
        ? patient?.releaseOfInformation
        : undefined,
      selfPay: patientPermissions?.insuranceInfo?.read ? patient?.selfPay : undefined,
    };

    return omitBy(result, isUndefined);
  }

  private sanitizeEncounterReadPermittedFields(
    encounter: OSEhrStateWithPII[typeof EHRResource.encounter],
    encounterPermissions: ResourcePermissions[typeof EHRResource.encounter],
  ): Partial<OSEhrStateWithPII[typeof EHRResource.encounter]> {
    if (isUndefined(encounterPermissions) || isEmpty(encounterPermissions)) {
      return undefined;
    }

    const result: Partial<OSEhrStateWithPII[typeof EHRResource.encounter]> = {
      encounterId: encounterPermissions?.identifiers?.read ? encounter?.encounterId : undefined,
      provider: encounterPermissions?.provider?.read ? encounter?.provider : undefined,
      assessments: encounterPermissions?.assessment?.read ? encounter?.assessments : undefined,
      isLocked: encounterPermissions?.basicInformation?.read ? encounter?.isLocked : undefined,
      encounterDate: encounterPermissions?.basicInformation?.read
        ? encounter?.encounterDate
        : undefined,
    };

    return omitBy(result, isUndefined);
  }

  private sanitizeReferralReadPermittedFields(
    referral: OSEhrStateWithPII[typeof EHRResource.referral],
    referralPermissions: ResourcePermissions[typeof EHRResource.referral],
  ): Partial<OSEhrStateWithPII[typeof EHRResource.referral]> {
    if (isUndefined(referralPermissions) || isEmpty(referralPermissions)) {
      return undefined;
    }

    const result: Partial<OSEhrStateWithPII[typeof EHRResource.referral]> = {
      referralId: referralPermissions?.identifiers?.read ? referral?.referralId : undefined,
      vimReferralId: referralPermissions?.identifiers?.read ? referral?.vimReferralId : undefined,
      token: referralPermissions?.identifiers?.read ? referral?.token : undefined,
      referringProvider: referralPermissions?.referringProvider?.read
        ? referral?.referringProvider
        : undefined,
      targetProvider: referralPermissions?.targetProvider?.read
        ? referral?.targetProvider
        : undefined,

      cpts: referralPermissions?.procedureCodes?.read ? referral?.cpts : undefined,
      diagnosis: referralPermissions?.conditions?.read ? referral?.diagnosis : undefined,

      notes: referralPermissions?.basicInformation?.read ? referral?.notes : undefined,
      reasons: referralPermissions?.basicInformation?.read ? referral?.reasons : undefined,
      specialty: referralPermissions?.basicInformation?.read ? referral?.specialty : undefined,
      authCode: referralPermissions?.basicInformation?.read ? referral?.authCode : undefined,
      numberOfVisits: referralPermissions?.basicInformation?.read
        ? referral?.numberOfVisits
        : undefined,
      facilityName: referralPermissions?.basicInformation?.read
        ? referral?.facilityName
        : undefined,
      priority: referralPermissions?.basicInformation?.read ? referral?.priority : undefined,
      status: referralPermissions?.basicInformation?.read ? referral?.status : undefined,
      startDate: referralPermissions?.basicInformation?.read ? referral?.startDate : undefined,
      createdDate: referralPermissions?.basicInformation?.read ? referral?.createdDate : undefined,
      endDate: referralPermissions?.basicInformation?.read ? referral?.endDate : undefined,
      isLocked: referralPermissions?.basicInformation?.read ? referral?.isLocked : undefined,
    };

    return omitBy(result, isUndefined);
  }

  private sanitizeOrdersReadPermittedFields(
    orders: OSEhrStateWithPII[typeof EHRResource.orders],
    ordersPermissions: ResourcePermissions[typeof EHRResource.orders],
  ): DeepPartial<OSEhrStateWithPII[typeof EHRResource.orders]> {
    if (isUndefined(ordersPermissions) || isEmpty(ordersPermissions)) {
      return undefined;
    }

    return orders!.map((order) => {
      const result: Partial<Order> = {
        id: ordersPermissions?.identifiers?.read ? order.id : undefined,
        createdDate: ordersPermissions?.basicInformation?.read ? order.createdDate : undefined,
        encounterId: ordersPermissions?.basicInformation?.read ? order.encounterId : undefined,
        type: ordersPermissions?.basicInformation?.read ? order.type : undefined,
        cpts: ordersPermissions?.procedureCodes?.read ? order.cpts : undefined,
        icd: ordersPermissions?.assessments?.read ? order.icd : undefined,
      };

      return omitBy(result, isUndefined);
    });
  }

  private sanitizePatientWritePermittedFields(
    _updatableFields: UpdatableEhrFields[typeof EHRResource.patient],
    _patientPermissions: ResourcePermissions[typeof EHRResource.patient],
  ): Partial<UpdatableEhrFields[typeof EHRResource.patient]> {
    return undefined;
  }

  private sanitizeEncounterWritePermittedFields(
    updatableFields: UpdatableEhrFields[typeof EHRResource.encounter],
    encounterPermissions: ResourcePermissions[typeof EHRResource.encounter],
  ): Partial<UpdatableEhrFields[typeof EHRResource.encounter]> {
    if (isUndefined(encounterPermissions) || isEmpty(encounterPermissions)) {
      return undefined;
    }
    const permittedWrite = this.getEncounterWritePermittedFields(encounterPermissions);

    return this.permittedAndUpdatableRecursive(permittedWrite, updatableFields) as Partial<
      UpdatableEhrFields[typeof EHRResource.encounter]
    >;
  }

  private sanitizeReferralWritePermittedFields(
    updatableFields: UpdatableEhrFields[typeof EHRResource.referral],
    referralPermissions: ResourcePermissions[typeof EHRResource.referral],
  ): Partial<UpdatableEhrFields[typeof EHRResource.referral]> {
    if (isUndefined(referralPermissions) || isEmpty(referralPermissions)) {
      return undefined;
    }

    const permittedWrite = this.getReferralWritePermittedFields(referralPermissions);

    return this.permittedAndUpdatableRecursive(permittedWrite, updatableFields) as Partial<
      UpdatableEhrFields[typeof EHRResource.referral]
    >;
  }

  private getReferralWritePermittedFields(
    referralPermissions: ResourcePermissions[typeof EHRResource.referral],
  ): Partial<UpdatableEhrFields[typeof EHRResource.referral]> {
    const basicInfoWrite = !!referralPermissions?.basicInformation?.write;
    const targetProviderWrite = !!referralPermissions?.targetProvider?.write;
    const procedureCodesWrite = !!referralPermissions?.procedureCodes?.write;
    const conditionsWrite = !!referralPermissions?.conditions?.write;

    const result: Partial<UpdatableEhrFields[typeof EHRResource.referral]> = {
      specialty: basicInfoWrite,
      startDate: basicInfoWrite,
      endDate: basicInfoWrite,
      authCode: basicInfoWrite,
      notes: basicInfoWrite,
      reasons: basicInfoWrite,
      priority: basicInfoWrite,
      numberOfVisits: basicInfoWrite,
      targetProvider: targetProviderWrite,
      cpts: procedureCodesWrite,
      diagnosis: conditionsWrite,
    };

    return result;
  }

  private getEncounterWritePermittedFields(
    encounterPermissions: ResourcePermissions[typeof EHRResource.encounter],
  ): Partial<EncounterOptionsFlags> {
    const objectiveWrite = !!encounterPermissions?.objective?.write;

    const subjectiveWrite = !!encounterPermissions?.subjective?.write;

    const assessmentWrite = !!encounterPermissions?.assessment?.write;

    const planWrite = !!encounterPermissions?.plan?.write;

    const patientInstructionsWrite = !!encounterPermissions?.patientInstructions?.write;

    const result: Partial<UpdatableEhrFields[typeof EHRResource.encounter]> = {
      objective: {
        generalNotes: objectiveWrite,
        physicalExamNotes: objectiveWrite,
      },
      subjective: {
        generalNotes: subjectiveWrite,
        historyOfPresentIllnessNotes: subjectiveWrite,
        chiefComplaintNotes: subjectiveWrite,
        reviewOfSystemsNotes: subjectiveWrite,
      },
      assessments: {
        generalNotes: assessmentWrite,
        diagnosisCodes: assessmentWrite,
      },
      plans: {
        procedureCodes: planWrite,
        generalNotes: planWrite,
      },
      patientInstructions: {
        generalNotes: patientInstructionsWrite,
      },
    };

    return result;
  }

  private sanitizeOrdersWritePermittedFields(
    _updatableFields: UpdatableEhrFields[typeof EHRResource.orders],
    _ordersPermissions: ResourcePermissions[typeof EHRResource.orders],
  ): Partial<UpdatableEhrFields[typeof EHRResource.orders]> {
    return undefined;
  }

  private permittedAndUpdatableRecursive(
    permissions: UpdatableEhrFields[EHRResource] | boolean,
    updatableFields: UpdatableEhrFields[EHRResource] | boolean,
  ): UpdatableEhrFields[EHRResource] | boolean {
    if (isUndefined(permissions)) {
      return false;
    }

    if (isBoolean(permissions)) {
      return permissions === updatableFields;
    }

    const result = {};

    const permissionKeys: string[] = Object.keys(permissions);
    for (const permissionKey of permissionKeys) {
      const permissionValue = permissions[permissionKey];
      const updatableValue = updatableFields?.[permissionKey];

      result[permissionKey] = this.permittedAndUpdatableRecursive(permissionValue, updatableValue);
    }

    return result;
  }

  private allFieldsPermittedRecursive(
    permittedField: UpdatableEhrFields[EHRResource] | boolean,
    fieldData: UpdatableEhrState<EHRResource>,
  ): UpdatableEhrFields[EHRResource] | boolean {
    if (!fieldData || !isPlainObject(fieldData) || !permittedField || isBoolean(permittedField)) {
      return permittedField === true;
    }

    const dataKeys: string[] = Object.keys(fieldData);
    for (const key of dataKeys) {
      if (
        !permittedField[key] ||
        !this.allFieldsPermittedRecursive(permittedField[key], fieldData[key])
      ) {
        return false;
      }
    }

    return true;
  }
}
