import { Logger, LoggerFactory } from '@getvim-os/logger';
import { BareboneMessaging } from '@getvim-os/barebone/infra';
import { inject, injectable } from 'inversify';
import { RuntimeMessageHooks } from './messageHooks';
import { BROADCAST_MODE } from '../../inversifyTokens';
import { OsMessageChannelManager } from './osMessageChannelManager';

const HANDSHAKE_MESSAGE_REPLY_TYPE = 'vim-connect-message-channel-handshake-reply';
const HANDSHAKE_MESSAGE_TYPE = 'vim-connect-message-channel-handshake';
const IFRAME_LOAD_TIMEOUT = 8000;

@injectable()
export class RuntimeIframeBootstrapper {
  private logger: Logger;

  constructor(
    @inject(RuntimeMessageHooks) private messageHooks: RuntimeMessageHooks,
    @inject(LoggerFactory) loggerFactory: LoggerFactory,
    @inject(BareboneMessaging) private bareboneMessaging: BareboneMessaging,
    @inject(OsMessageChannelManager) private osMessageChannelManager: OsMessageChannelManager,
    @inject(BROADCAST_MODE) private broadcastMode: boolean,
  ) {
    this.logger = loggerFactory.create('runtime-wrapper');
  }
  bootstrapRuntimeIframe(runtimeIframe: HTMLIFrameElement, adapterUrl: string) {
    this.osMessageChannelManager.initOSMessageChannel(runtimeIframe);
    if (!this.broadcastMode) {
      this.startMessageChannelHandshake(runtimeIframe, adapterUrl);
    }

    this.bareboneMessaging.onParentMessages((messageFromParent) => {
      this.messageHooks.onMessageToRuntime(messageFromParent, () => {
        runtimeIframe.contentWindow?.postMessage(messageFromParent.data, '*');
      });
    });
    this.listenToChildrenMessages(runtimeIframe, adapterUrl);
  }

  private startMessageChannelHandshake(runtimeIframe: HTMLIFrameElement, adapterUrl: string) {
    let isIframeLoaded = false;
    runtimeIframe.addEventListener('load', () => {
      try {
        const channel = new MessageChannel();
        // Listen for messages on port1
        // eslint-disable-next-line unicorn/prefer-add-event-listener
        channel.port1.onmessage = (msg) => {
          if (msg?.data?.type === HANDSHAKE_MESSAGE_REPLY_TYPE) {
            isIframeLoaded = true;
            this.logger.info('Runtime iframe loaded on time.', {
              iframeTitle: runtimeIframe.title,
            });
          } else {
            this.messageHooks.onMessageFromRuntime(msg, () => {
              this.bareboneMessaging.sendRawMessage(msg.data, msg.ports);
            });
          }
        };
        runtimeIframe.contentWindow?.postMessage({ type: HANDSHAKE_MESSAGE_TYPE }, '*', [
          channel.port2,
        ]);
      } catch (error) {
        this.logger.error(
          'Could not establish communication with runtime iFrame',
          {
            errorMessage: (error as Error).message,
            iframeId: runtimeIframe?.id,
          },
          error as Error,
        );
      }
    });

    setTimeout(() => {
      if (!isIframeLoaded) {
        this.logger.warning(
          'Adapter loading time passed, currently in monitor state, nothing happened.',
          {
            adapterUrl,
          },
        );
      }
    }, IFRAME_LOAD_TIMEOUT);
  }

  private listenToChildrenMessages(runtimeIframe: HTMLIFrameElement, adapterUrl: string) {
    const runtimeOrigin = new URL(adapterUrl).origin;

    window.addEventListener('message', (msg) => {
      const children = document.querySelectorAll('iframe') as NodeListOf<HTMLIFrameElement>;
      const messageFromChild =
        msg.source &&
        // eslint-disable-next-line unicorn/prefer-spread
        Array.from(children)
          .map((child) => child.contentWindow)
          .includes(msg.source as WindowProxy);

      if (!messageFromChild) {
        return;
      }
      const messageFromHubWidget = msg.origin !== runtimeOrigin;
      if (messageFromHubWidget) {
        this.messageHooks.onMessageToRuntime(
          {
            data: msg.data,
            source: msg.source as WindowProxy,
          },
          () => {
            runtimeIframe.contentWindow?.postMessage(msg.data, '*');
          },
        );
      } else {
        this.messageHooks.onMessageFromRuntime(msg, () => {
          this.bareboneMessaging.sendRawMessage(msg.data);
        });
      }
    });
  }
}
