import { AppStoreApplication } from '@/hub/types/application';
import { InternalApi } from '@/internal-api';
import { CreateAppLaunchInput } from '@/internal-api/application-auth/types';
import { UnauthorizedError } from '@getvim-os/errors';
import { Logger, LoggerFactory } from '@getvim-os/logger';
import { retry } from '@getvim-os/promises';
import { VimTracerProvider } from '@getvim-os/telemetry';
import { Container, inject, injectable } from 'inversify';

const MAX_RETRY_AMOUNT = 3;

@injectable()
export class ApplicationAuthManager {
  private logger: Logger;

  constructor(
    @inject(Container) private container: Container,
    @inject(LoggerFactory) loggerFactory: LoggerFactory,
    @inject(VimTracerProvider) private traceProvider: VimTracerProvider,
    @inject(InternalApi) private internalApi: InternalApi,
  ) {
    this.logger = loggerFactory.create('ApplicationAuthManager');
  }

  public async createAppLaunchId(input: CreateAppLaunchInput) {
    const tracer = this.traceProvider.getTracer('createAppLaunch');

    return await tracer.startAutoRootSpan(
      'create-app-launch-id',
      { attributes: { appId: input.appId } },
      async (parentSpan) => {
        try {
          const { createAppLaunch } = await retry(
            (attempt) =>
              parentSpan.startAutoChildSpan(`create app launch id attempt ${attempt}`, {}, () =>
                this.internalApi.vimAppAuthApi.createAppLaunch(input),
              ),
            {
              retries: MAX_RETRY_AMOUNT,
            },
          );

          return createAppLaunch;
        } catch (error) {
          this.logger.error(
            'Failed to create app launch id',
            {
              retries: MAX_RETRY_AMOUNT,
            },
            error as Error,
          );
          throw error;
        }
      },
    );
  }

  public async verifyAppHandshake({
    iframeLoadedOrigin,
    application,
    token,
  }: {
    iframeLoadedOrigin: string;
    application: AppStoreApplication;
    token: string;
  }) {
    const allowedOrigins = application.allowedOrigins || [];

    const iframeOrigin = new URL(iframeLoadedOrigin).origin;

    if (!allowedOrigins.includes(iframeOrigin)) {
      throw new UnauthorizedError('Iframe loaded URL is not allowed');
    }
    const result = await retry(
      () =>
        this.internalApi.vimAppAuthApi.verifyAppToken({
          appId: application.id,
          token,
          launchId: application.launchId!,
        }),
      {
        retries: MAX_RETRY_AMOUNT,
        stopRetryPredicate: (error) =>
          (error as any)?.data?.errors?.find(
            (element) => element?.extensions?.code === 'FORBIDDEN',
          ),
      },
    ).catch((error) => {
      throw new UnauthorizedError('Invalid App Token', error);
    });

    if (!result.verifyAppToken.isValid) {
      throw new UnauthorizedError('Invalid App Token');
    }
  }
}
