import { Tooltip } from '@getvim/atomic-ui';
import React from 'react';
import { insurerToLogo } from './utils/insurersLogo';
import { getAgeInYearsMonthDays, getPatientAgeString, getPatientDateOfBirth } from './utils/dates';

import './index.less';
import { Entities } from '@deprecated/types';

interface PatientHeaderProps {
  patient: Pick<Entities.Patient, 'demographics' | 'insurance'>;
}

const PatientHeader: React.FC<PatientHeaderProps> = ({ patient }: PatientHeaderProps) => {
  const {
    demographics: { dateOfBirth, firstName, lastName },
    insurance,
  } = patient;

  const insurerLogo = insurance?.insurer && insurerToLogo[insurance.insurer];
  const insurerName = insurance?.ehrInsurance;

  return (
    <div className="patient-header">
      <div className="patient-header__main-details">
        <div
          className="patient-header__name"
          data-testid="patient-header-name"
        >{`${firstName} ${lastName}`}</div>
        <div className="patient-header__age-details">
          {[
            '',
            getPatientDateOfBirth(dateOfBirth).format('MMM DD, YYYY'),
            getPatientAgeString(getAgeInYearsMonthDays(dateOfBirth)),
          ].join('  /  ')}
        </div>
      </div>
      <div className="patient-header__insurance-details" data-testid="patient-header-insurance">
        {insurerLogo && (
          <>
            <img
              src={insurerLogo}
              alt={insurance?.insurer}
              data-testid="patient-header-insurance-logo"
              className="patient-header__logo"
            />
          </>
        )}
        {insurerLogo && insurerName && <div>&nbsp;/&nbsp;</div>}
        {!!insurerName && (
          <>
            <Tooltip
              tooltipContent={
                <span data-testid="patient-header-insurance-tooltip">{insurerName}</span>
              }
              referenceClassName="patient-header__insurer-name-tooltip"
              className="insurer-tooltip text-center"
              width="large"
            >
              <div
                className="patient-header__insurer-name"
                data-testid="patient-header-insurance-name"
              >
                {insurerName}
              </div>
            </Tooltip>
          </>
        )}
      </div>
    </div>
  );
};

export default PatientHeader;
