import { WebConnectors } from '../..';
import { WebConnectorMessageTypes } from '../../enum';
import { AddXhrListenerParams } from '../../types/addXhrListenerParams';
import { AddXhrListenerResult } from '../../types/addXhrListenerResult';

export async function addXhrListener(
  this: WebConnectors,
  { urlPattern, handler }: AddXhrListenerParams,
): Promise<AddXhrListenerResult | boolean> {
  const listenerId = await this.messaging.sendMessageWithHandler({
    type: WebConnectorMessageTypes.addXhrListener,
    payload: { urlPattern },
    handler,
  });

  return listenerId
    ? { remove: () => this.removeXhrListener({ listenerId: listenerId as string }), listenerId }
    : false;
}
