import { OTLPLogExporter } from '@opentelemetry/exporter-logs-otlp-http';
import {
  LoggerProvider,
  NoopLogRecordProcessor,
  SimpleLogRecordProcessor,
} from '@opentelemetry/sdk-logs';

import { Resource } from '@opentelemetry/resources';
import { inject, injectable } from 'inversify';
import { BaseUrl } from '../symbols';

@injectable()
export abstract class OtelTelemetryLogsProvider {
  constructor(protected loggerProvider: LoggerProvider) {}
  getLogger(
    ...args: Parameters<LoggerProvider['getLogger']>
  ): ReturnType<LoggerProvider['getLogger']> {
    return this.loggerProvider.getLogger(...args);
  }
}

@injectable()
export class HttpTelemetryLogsProvider extends OtelTelemetryLogsProvider {
  constructor(@inject(Resource) resource: Resource, @inject(BaseUrl) baseUrl: string) {
    super(
      new LoggerProvider({
        resource,
      }),
    );

    this.loggerProvider.addLogRecordProcessor(
      new SimpleLogRecordProcessor(
        new OTLPLogExporter({
          url: `${baseUrl}/v1/logs`,
          concurrencyLimit: 1,
        }),
      ),
    );
  }
}

@injectable()
export class NoopTelemetryLogsProvider extends OtelTelemetryLogsProvider {
  constructor(@inject(Resource) resource: Resource) {
    super(new LoggerProvider({ resource }));

    this.loggerProvider.addLogRecordProcessor(new NoopLogRecordProcessor());
  }
}
