import { z } from 'zod';
import { EventData } from './common';
import { EntitiesRequestSchema } from '../dynamic/entitiesRequest';
import { createWritebackPayloadSchema, WritebackTypeEnum } from '../writeback-api';

export const OsToAdapterEvents = z.enum([
  'SET_ACTIVE_ENTITIES',
  'GET_ADAPTER_MANIFEST',
  'EXECUTE_WRITEBACK_V2',
]);

export type OsToAdapterEvents = z.infer<typeof OsToAdapterEvents>;

const WritebackEncounterSchema = createWritebackPayloadSchema(WritebackTypeEnum.updateEncounter);
const WritebackReferralSchema = createWritebackPayloadSchema(WritebackTypeEnum.updateReferral);

const eventSchemas = {
  [OsToAdapterEvents.Enum.SET_ACTIVE_ENTITIES]: EntitiesRequestSchema,
  [OsToAdapterEvents.Enum.GET_ADAPTER_MANIFEST]: z.never(),
  [OsToAdapterEvents.Enum.EXECUTE_WRITEBACK_V2]: z.union([
    WritebackEncounterSchema,
    WritebackReferralSchema,
  ]),
};

type EventSchemas = typeof eventSchemas;

export type OsToAdapterEventData<T extends keyof EventSchemas = keyof EventSchemas> = EventData<
  EventSchemas,
  T
>;
