import { runMethodWithTimeout } from '@getvim-os/promises';
import { createSlice } from '@reduxjs/toolkit';
import { Container } from 'inversify';
import { createNonConcurrentAsyncThunk } from '../lib';
import { actions as userActions } from '../user/userState.slice';
import { PendingEhrState, PublishedEhrState } from './types';
import { VaultTokenizerService } from './vault-tokenizer/vaultTokenizer.service';
import { LoggerFactory } from '@getvim-os/logger';
import { PromiseTimeoutError } from '@getvim-os/errors';

export const initialState: PublishedEhrState = {};

const TOKENIZATION_MAX_TIME = 10_000;
const TOKENIZATION_TIME_ALERT_THRESHOLD = 5000;

const applyTokensOnPendingState = ({
  pendingState,
  ...tokens
}: {
  patientToken?: string;
  referralToken?: string;
  pendingState: PendingEhrState;
}) => {
  return {
    ...pendingState,
    patient: pendingState.patient
      ? {
          ...pendingState.patient,
          token: tokens.patientToken!,
        }
      : undefined,
    referral: pendingState.referral
      ? {
          ...pendingState.referral,
          token: tokens.referralToken!,
        }
      : undefined,
  } satisfies PublishedEhrState;
};

export const vaultTokenizeEhrState = createNonConcurrentAsyncThunk<
  PublishedEhrState,
  PendingEhrState
>('tokenizeEhrState', async (newEhrState, api): Promise<PublishedEhrState> => {
  const container = (api.extra as any).container as Container;
  const tokenizer = container.get(VaultTokenizerService);
  const logger = container.get(LoggerFactory).create('tokenizeEhrStateThunk');
  const startTime = Date.now();

  try {
    const tokens = await runMethodWithTimeout(
      () => tokenizer.generateEntitiesTokens(newEhrState, api.signal),
      TOKENIZATION_MAX_TIME,
    ).catch((error) => {
      /* istanbul ignore if  -- @preserve */
      if (!api.signal.aborted) {
        if (error instanceof PromiseTimeoutError) {
          api.abort('TimedOut');
        } else {
          logger.warning(`Entities tokenization logic failed`, { error });
        }
      }
      throw error;
    });

    return applyTokensOnPendingState({
      patientToken: tokens.patientPut,
      referralToken: tokens.referralPut,
      pendingState: newEhrState,
    });
  } finally {
    const totalTime = Date.now() - startTime;
    if (
      (!api.signal.aborted || api.signal.reason === 'TimedOut') &&
      totalTime > TOKENIZATION_TIME_ALERT_THRESHOLD
    ) {
      logger.error('State vault tokenization execution time passed threshold', {
        totalTime,
        aborted: api.signal.aborted,
      });
    }
  }
});

export const ehrStateSlice = createSlice({
  name: 'ehrState',
  initialState,
  reducers: {},
  extraReducers: (builder) => {
    builder
      .addCase(userActions.logout, () => {
        return initialState;
      })
      .addCase(vaultTokenizeEhrState.fulfilled, (_state, action) => {
        return action.payload;
      })
      .addCase(vaultTokenizeEhrState.rejected, (_state, action) => {
        if (action.error?.name !== 'AbortError') {
          return initialState;
        }
      });
  },
});

export const actions = ehrStateSlice.actions;

export default ehrStateSlice.reducer;
