import { OTLPTraceExporter } from '@opentelemetry/exporter-trace-otlp-http';
import { Resource } from '@opentelemetry/resources';
import {
  BatchSpanProcessor,
  ConsoleSpanExporter,
  NoopSpanProcessor,
  WebTracerProvider,
} from '@opentelemetry/sdk-trace-web';
import { inject, injectable } from 'inversify';
import { BaseUrl } from '../symbols';

@injectable()
export abstract class OtelTracerProvider {
  constructor(protected webTracerProvider: WebTracerProvider) {}

  getTracer(
    ...args: Parameters<WebTracerProvider['getTracer']>
  ): ReturnType<WebTracerProvider['getTracer']> {
    return this.webTracerProvider.getTracer(...args);
  }

  exportSpanToConsole() {
    const exporter = new ConsoleSpanExporter();
    const processor = new BatchSpanProcessor(exporter);
    this.addSpanProcessor(processor);
  }

  addSpanProcessor(...args: Parameters<WebTracerProvider['addSpanProcessor']>) {
    return this.webTracerProvider.addSpanProcessor(...args);
  }
}

@injectable()
export class HttpTracerProvider extends OtelTracerProvider {
  constructor(@inject(Resource) resource: Resource, @inject(BaseUrl) baseUrl: string) {
    super(
      new WebTracerProvider({
        resource,
      }),
    );
    this.webTracerProvider.addSpanProcessor(
      new BatchSpanProcessor(
        new OTLPTraceExporter({
          url: `${baseUrl}/v1/traces`,
          headers: {
            'Access-Control-Allow-Credentials': true,
          },
        }),
      ),
    );
  }
}

@injectable()
export class NoopTracerProvider extends OtelTracerProvider {
  constructor(@inject(Resource) resource: Resource) {
    super(
      new WebTracerProvider({
        resource,
      }),
    );
    this.webTracerProvider.addSpanProcessor(new NoopSpanProcessor());
  }
}
