import { ApplicationSize, EHRResource, Product } from '@getvim-os/types';
import { map } from 'lodash-es';
import { Action, createSlice } from '@reduxjs/toolkit';
import { PayloadAction } from '..';
import { InjectedApplication } from '../../hub/types/application';
import { actions as userActions } from '../user/userState.slice';
import { actions as pendingEhrActions } from '../ehr/pendingEhrState.slice';
import {
  ApplicationIdPayload,
  ApplicationsState,
  ModifyApplicationPayload,
  PopupHistory,
  PopupHistoryUpdatePayload,
  SelectedApplication,
} from './types';
import { USER_MANAGEMENT_APPLICATION_ID } from '../../hub/components/user-management-metadata';
import { createAppLaunchId, resetAppForRerender } from './applicationsState.thunk';
import {
  EnableLaunchButtonPayload,
  LaunchButtonRealEstates,
  OSMessageResponseType,
} from '@getvim/vim-os-api';

export const APP_SIZE_MAP = {
  [ApplicationSize.CLASSIC]: { width: '365px', height: '90vh' },
  [ApplicationSize.LARGE]: { width: '800px', height: '90vh' },
  [ApplicationSize.EXTRA_LARGE]: { width: '1350px', height: '90vh' },
};

const applicationWithoutPatientHeader = new Set([
  'privia-insights',
  'cde',
  'optum-data-core',
  'optum-data-core-dashboard',
]);

const alwaysOnApplications = new Set([
  'privia-insights',
  'cde',
  'optum-data-core',
  'optum-data-core-dashboard',
]);

const AppPopupPriority: Record<string, number> = {
  'rx-assist': 1,
  [Product.DiagnosisGaps]: 2,
};

export const getTodayDate = () => new Date().toISOString().slice(0, 10);

export const initialState: ApplicationsState = {
  applications: {},
  selectedApplication: {},
  popupHistory: {},
  userManagementNotificationCount: 0,
};

const clearResourceEnabledButton = (state: ApplicationsState, resource: EHRResource) => {
  for (const appId in state.applications) {
    if (state.applications[appId].enabledLaunchButtons?.[resource]) {
      state.applications[appId].enabledLaunchButtons![resource] = undefined;
    }
  }
};

export const slice = createSlice({
  name: 'applications',
  initialState,
  reducers: {
    setPopupHistory: (state, action: PayloadAction<PopupHistory>) => {
      state.popupHistory = action.payload;
    },
    updatePopupHistory: (state, action: PayloadAction<PopupHistoryUpdatePayload>) => {
      const { patientId } = action.payload;
      state.popupHistory[patientId] = { lastPopupDate: getTodayDate() };
    },
    updateEnabledLaunchButtons: (
      state,
      {
        payload: { appId, enabledLaunchButtons },
      }: PayloadAction<{ appId: string; enabledLaunchButtons: EnableLaunchButtonPayload }>,
    ) => {
      if ('manifest' in state.applications[appId]) {
        state.applications[appId].enabledLaunchButtons = enabledLaunchButtons;
      }
    },
    launchButtonClicked: (
      _state,
      _action: PayloadAction<{
        appId: ApplicationIdPayload;
        clickedRealEstate: LaunchButtonRealEstates;
      }>,
    ) => {},
    addApplication: (state, action: PayloadAction<InjectedApplication>) => {
      const application = action.payload;

      if (state.applications[application.id]) {
        action.logger?.warning('Application to add already exist', {
          id: application?.id,
          previousInjectSource: state.applications[application.id].injectSource,
          previousAppStoreApp: state.applications[application.id].manifest !== undefined,
          currentInjectSource: application.injectSource,
          currentAppStoreApp: application.manifest !== undefined,
          apps: map(state.applications, (app, id) => ({ id, name: app.manifest?.metadata?.name })),
        });
        throw new Error(`Application to add already exist`);
      }
      const hidePatientHeader =
        applicationWithoutPatientHeader.has(application.id) ||
        (application.manifest && !application.manifest.UI.capabilities?.showPatientHeader);
      const affectVimHubTurnedOn = application.manifest
        ? !application.manifest.UI.capabilities?.appAlwaysAvailable
        : !alwaysOnApplications.has(application.id);
      const size = application.manifest?.UI.size && APP_SIZE_MAP[application.manifest.UI.size];
      const autoPopPriority = AppPopupPriority[application.id];
      state.applications[application.id] = {
        ...application,
        ...(hidePatientHeader && { hidePatientHeader }),
        ...(size && { size }),
        ...(autoPopPriority && { autoPopPriority }),
        ...(affectVimHubTurnedOn && { affectVimHubTurnedOn }),
        clearNotificationsIndicator: true,
      };
      action.logger?.info('Added application successfully', { id: application.id });
    },
    modifyApplication: (state, action: PayloadAction<ModifyApplicationPayload>) => {
      const { modifiedApplication, id } = action.payload;
      if (!state.applications[id]) {
        action.logger?.warning('Application to modify not found', {
          id,
          apps: map(state.applications, (app, id) => ({ id, name: app.manifest?.metadata?.name })),
        });
        throw new Error('Application to modify not found');
      }
      if (modifiedApplication.loading) {
        state.applications[id].reason = undefined;
      }
      if (modifiedApplication.disabled && id === state.selectedApplication.selectedAppId) {
        state.selectedApplication = {};
      }

      state.applications[id] = {
        ...state.applications[id],
        ...modifiedApplication,
      };
    },
    healthCheckFailed: (state, action: PayloadAction<ApplicationIdPayload>) => {
      state.applications[action.payload].initializationStatus = 'down';
    },
    iframeLoadProcessStart: (state, action: PayloadAction<ApplicationIdPayload>) => {
      state.applications[action.payload].initializationStatus = 'initializing';
    },
    iframeInitializing: (state, action: PayloadAction<ApplicationIdPayload>) => {
      state.applications[action.payload].initializationStatus = 'initializing';
    },
    iframeCommunicationEstablishing: (state, action: PayloadAction<ApplicationIdPayload>) => {
      if (state.applications[action.payload].initializationStatus !== 'communication_established') {
        state.applications[action.payload].initializationStatus = 'communication_establishing';
      }
    },
    iframeCommunicationError: (
      state,
      action: PayloadAction<{
        id: ApplicationIdPayload;
        error: string;
        errorCode: OSMessageResponseType;
      }>,
    ) => {
      if (
        state.applications[action.payload.id].initializationStatus !== 'communication_established'
      ) {
        state.applications[action.payload.id].initializationStatus = 'communication_error';
      }
    },
    iframeInitFailed: (
      state,
      action: PayloadAction<{
        id: ApplicationIdPayload;
        error: string;
        errorCode: OSMessageResponseType;
      }>,
    ) => {
      if (
        state.applications[action.payload.id].initializationStatus !== 'communication_established'
      ) {
        state.applications[action.payload.id].initializationStatus = 'failed';
      }
    },
    iframeDestroyed: (_state, _action: PayloadAction<ApplicationIdPayload>) => {},
    iframeLoaded: (state, action: PayloadAction<ApplicationIdPayload>) => {
      if (state.applications[action.payload].initializationStatus !== 'communication_established') {
        state.applications[action.payload].initializationStatus = 'iframe_loaded';
      }
    },
    iframeCommunicationEstablished: (state, action: PayloadAction<ApplicationIdPayload>) => {
      state.applications[action.payload].initializationStatus = 'communication_established';
    },
    removeApplication: (state, action: PayloadAction<ApplicationIdPayload>) => {
      delete state.applications[action.payload];
    },
    selectApplication: (
      state,
      action: PayloadAction<Omit<SelectedApplication, 'appOpenedTime'>>,
    ) => {
      if (state.applications[action.payload.selectedAppId]) {
        state.applications[action.payload.selectedAppId].disabled = false;
        state.applications[action.payload.selectedAppId].loading = false;
      }
      state.selectedApplication = { ...action.payload, appOpenedTime: Date.now() };
    },
    clearSelectedApplication: (state) => {
      state.selectedApplication = {};
    },
    setUserManagementErrored: (state, action: PayloadAction<boolean>) => {
      state.userManagementErrored = action.payload;
    },
    setUserManagementNotification: (state, action: PayloadAction<number>) => {
      state.userManagementNotificationCount = action.payload;
    },
    showNotification: (
      state,
      action: PayloadAction<{
        appId: ApplicationIdPayload;
        notification: NonNullable<InjectedApplication['notification']>;
      }>,
    ) => {
      const { appId, notification } = action.payload;
      if (appId === USER_MANAGEMENT_APPLICATION_ID) {
        state.userManagementNotification = notification;
      } else {
        state.applications[appId].notification = notification;
      }
    },
    hideNotification: (state, action: PayloadAction<ApplicationIdPayload>) => {
      const appId = action.payload;
      if (appId === USER_MANAGEMENT_APPLICATION_ID) {
        delete state.userManagementNotification;
      } else {
        delete state.applications[appId].notification;
      }
    },
    setAppSessionId: (state, action: PayloadAction<string | undefined>) => {
      state.appSessionId = action.payload;
    },
    setSdkVersion: (
      state,
      action: PayloadAction<{
        appId: string;
        sdkVersion: string | undefined;
      }>,
    ) => {
      state.applications[action.payload.appId].sdkVersion = action.payload.sdkVersion;
    },
  },
  extraReducers: (builder) => {
    builder.addCase(userActions.logout, (_state, action: PayloadAction<any>) => {
      action.logger?.info('Application state received logout - reset state');
      return initialState;
    });
    builder.addCase(resetAppForRerender.pending, (state, action) => {
      (action as PayloadAction<any>).logger?.info('Resetting app for rerender', {
        input: action.meta.arg,
      });

      const appId = action.meta.arg.appId;
      const isRetry = action.meta.arg.retry;

      if (isRetry) {
        state.applications[appId].initializeAttempt =
          (state.applications[appId].initializeAttempt ?? 0) + 1;
      } else {
        state.applications[appId].initializeAttempt = 0;
        state.applications[appId].recoveryAttempt =
          (state.applications[appId].recoveryAttempt ?? 0) + 1;
      }
      delete state.applications[appId].launchId;
    });
    builder.addCase(createAppLaunchId.fulfilled, (state, action) => {
      const appId = action.meta.arg.appId;
      state.applications[appId].launchId = action.payload?.launchId;
    });
    builder.addCase(createAppLaunchId.rejected, (state, action) => {
      const appId = action.meta.arg.appId;
      state.applications[appId].initializationStatus = 'create-launch-id-failed';
    });

    /**
     * Caution these reducers are from the pendingEhrState
     * Meaning that it will take a bit time until it will be synced to the EHR state
     * for launch button cleanups - that's ok
     */
    builder.addCase(pendingEhrActions.unsetPatient, (state) => {
      clearResourceEnabledButton(state, EHRResource.patient);
    });

    const actionIsUnsetPatientOr = (otherAction: Action) => (action: Action) =>
      [pendingEhrActions.unsetPatient.type, otherAction.type].includes(action.type);

    builder.addMatcher(actionIsUnsetPatientOr(pendingEhrActions.unsetReferral), (state) => {
      clearResourceEnabledButton(state, EHRResource.referral);
    });

    builder.addMatcher(actionIsUnsetPatientOr(pendingEhrActions.unsetOrders), (state) => {
      clearResourceEnabledButton(state, EHRResource.orders);
    });

    builder.addMatcher(actionIsUnsetPatientOr(pendingEhrActions.unsetEncounter), (state) => {
      clearResourceEnabledButton(state, EHRResource.encounter);
    });
  },
});

export const actions = slice.actions;

export default slice.reducer;
