import { inject, injectable } from 'inversify';
import { v4 as uuid } from 'uuid';
import { SendMessageInput, PARENT_COMMUNICATOR, ParentCommunicator, ParentMessage } from './types';

export { PARENT_COMMUNICATOR } from './types';
export type { ParentMessage, FrameSource, ParentCommunicator } from './types';
export * from './parent-communicators';

interface SendMessageWithHandlerInput<T> extends SendMessageInput {
  handler: (data: T, isError: boolean) => void;
}

@injectable()
export class BareboneMessaging {
  @inject(PARENT_COMMUNICATOR) private parentCommunicator!: ParentCommunicator;

  public async setupMessaging(): Promise<void> {
    return await this.parentCommunicator.setup(this.getTabIdFromUrl());
  }

  public onParentMessages(callback: (parentMessage: ParentMessage) => void) {
    return this.parentCommunicator.onParentMessage(callback);
  }

  public sendRawMessage(message: object, ports?: ReadonlyArray<MessagePort>): void {
    const rawMessage = {
      ...this.getMessageMetadata(),
      ...message,
    };
    this.parentCommunicator.sendRawMessage(rawMessage, ports);
  }

  public sendAsyncMessage({ type, payload, ehrInstanceId }: SendMessageInput): void {
    this.parentCommunicator.sendAsyncMessage({
      type,
      payload,
      ...this.getMessageMetadata(),
      ...(ehrInstanceId
        ? {
            ehrInstanceId,
          }
        : {}),
    });
  }

  public async sendMessage<RETURN = unknown>({
    type,
    payload,
    ehrInstanceId,
    timeout,
  }: SendMessageInput): Promise<RETURN> {
    return await this.parentCommunicator.sendMessage(
      {
        type,
        payload,
        ...this.getMessageMetadata(),
        ...(ehrInstanceId
          ? {
              ehrInstanceId,
            }
          : {}),
      },
      timeout,
    );
  }

  public async sendMessageWithHandler<T>({
    type,
    payload,
    handler,
    ehrInstanceId,
    timeout,
  }: SendMessageWithHandlerInput<T>): Promise<string> {
    return await this.parentCommunicator.sendMessageWithHandler(
      {
        type,
        payload,
        ...this.getMessageMetadata(),
        ...(ehrInstanceId
          ? {
              ehrInstanceId,
            }
          : {}),
      },
      handler,
      timeout,
    );
  }

  private getMessageMetadata() {
    return {
      messageId: uuid(),
      ehrInstanceId: this.getTabIdFromUrl(),
    };
  }

  private getTabIdFromUrl() {
    const url = new URL(window.location.href);
    const tabId = url.searchParams.get('tabId');
    const pid = url.searchParams.get('pid');
    return tabId ? Number(tabId) : pid ? Number(pid) : undefined;
  }
}
