import { isFunction, isNil, isInteger } from 'lodash-es';
import promiseRetry from 'promise-retry';
import { ErrorCodes, StandardError } from '@getvim-os/errors';
import { RetryOptions } from './retry-options';

export const defaultOptions = {
  minTimeout: 25,
  retries: 8,
  maxTimeout: Number.POSITIVE_INFINITY,
  factor: 2,
};

/**
 * Automatic retry of Promises that reject with exponential backoff until some max retry amount.
 * Give it a function that returns a Promise. If the Promise rejects this function waits before
 * retrying. If the Promise rejected enough times this function will reject too. This is controlled
 * by the retries param.The wait time between retries will be minTimeout milliseconds for the first retry,
 * and then doubled for each retry. So, with the values of retries = 3, minTimeout = 25, the wait
 * time between failures will be 25ms->50ms->100ms->reject.
 * If there are some errors on which you don't want to retry, you can supply a stopRetryPredicate.
 * The stopRetryPredicate is a function that takes the rejection value and the attempt number as a
 * parameter and returns a boolean of whether to continue. If the value is true, there won't be
 * another attempt.
 * The functionToRetry receives an "attemptNumber" parameter, you can use it for logging.
 * @param functionToRetry Takes an attemptNumber param and returns a Promise
 * @param optionsOverride an object with parameters: retries, minTimeout, stopRetryPredicate
 */
export function retry<RETURN_TYPE>(
  functionToRetry: (attemptNumber: number) => Promise<RETURN_TYPE>,
  optionsOverride: RetryOptions,
) {
  return Promise.resolve().then<RETURN_TYPE>(() => {
    const options = Object.assign({}, defaultOptions, optionsOverride);
    AssertParameters(functionToRetry, options);
    const { stopRetryPredicate, retries, minTimeout, maxTimeout, factor } = options;

    // Defined here because we also want to know what the attempt number is for retryPredicate
    let attemptNumber = 0;
    return promiseRetry(
      (retryFunc, internalAttemptNumber) => {
        attemptNumber = internalAttemptNumber;
        return functionToRetry(attemptNumber).catch((error) => {
          if (stopRetryPredicate && stopRetryPredicate(error, attemptNumber)) {
            throw error;
          } else {
            retryFunc(error);
          }
        });
      },
      { retries, minTimeout, maxTimeout, factor },
    );
  });
}

function AssertParameters(functionToRetry, retryOptions: RetryOptions) {
  if (!isFunction(functionToRetry)) {
    throw new StandardError('functionToRetry must be a function', ErrorCodes.INVALID_INPUT);
  }
  if (!isNil(retryOptions.stopRetryPredicate) && !isFunction(retryOptions.stopRetryPredicate)) {
    throw new StandardError('stopRetryPredicate must be a function', ErrorCodes.INVALID_INPUT);
  }
  if (
    isNil(retryOptions.retries) ||
    !isInteger(retryOptions.retries) ||
    (retryOptions.retries ?? 0) < 0
  ) {
    throw new StandardError(
      `retries must be an integer >= 0,  got ${retryOptions.retries}`,
      ErrorCodes.INVALID_INPUT,
    );
  }
  if (
    isNil(retryOptions.minTimeout) ||
    !isInteger(retryOptions.minTimeout) ||
    (retryOptions.minTimeout ?? 0) < 0
  ) {
    throw new StandardError('minTimeout must be an integer >= 0', ErrorCodes.INVALID_INPUT);
  }
}
