import { AdapterID } from '../adapter-manager/manifest-support/types';

export const OS_IFRAME_WRAPPER_ID = 'os-iframe-wrapper';

export const getDeviceId = (): string => {
  const searchParams = new URLSearchParams(window.location.search);
  return searchParams.get('deviceId') ?? '';
};

export const isBroadcastMode = (): boolean => {
  const searchParams = new URLSearchParams(window.location.search);
  return searchParams.get('broadcastMode') === 'true';
};

export const getBareboneType = (): string => {
  const searchParams = new URLSearchParams(window.location.search);
  return searchParams.get('bareboneType') ?? '';
};

export const getBareboneVersion = (): string => {
  const searchParams = new URLSearchParams(window.location.search);
  return searchParams.get('bareboneVersion') ?? '';
};

const extractLastPath = (url: string) => new URL(url).pathname.split('/')?.pop?.();

export const getAdapterId = () => {
  const searchParams = new URLSearchParams(window.location.search);
  const adapterUrlRaw = searchParams.get('adapterUrl')!;

  let adapterUrl = adapterUrlRaw;
  if (!adapterUrlRaw.startsWith('http')) {
    adapterUrl = window.atob(adapterUrlRaw);
  }

  return extractLastPath(adapterUrl)?.replace('.html', '') as AdapterID;
};
