import { WebConnectors } from '../..';
import { WebConnectorMessageTypes } from '../../enum';
import { InvokeFunction } from '../../types/invoke-function';

export async function backboneInvoke(this: WebConnectors, payload: InvokeFunction) {
  return await this.messaging.sendMessage({
    type: WebConnectorMessageTypes.backboneInvoke,
    payload,
  });
}
