import React, { useCallback, useState, useEffect } from 'react';
import './index.less';
import classNames from 'classnames';

export interface CodePreviewProps {
  componentName?: string;
  args?: any;
  smallFont?: boolean;
  reactFormat?: boolean;
  jsonFormat?: boolean;
  cssFormat?: boolean;
}

const CodePreview = ({
  componentName,
  args,
  reactFormat = true,
  jsonFormat = false,
  cssFormat = false,
  smallFont = false,
}: CodePreviewProps) => {
  const [isCopied, setIsCopied] = useState(false);
  const [activeTab, setActiveTab] = useState('');

  useEffect(() => {
    if (reactFormat) setActiveTab('React');
    else if (jsonFormat) setActiveTab('JSON');
    else if (cssFormat) setActiveTab('CSS');
  }, [reactFormat, jsonFormat, cssFormat]);

  const generateCode = useCallback(() => {
    if (activeTab === 'React') {
      return `<${componentName} ${Object.entries(args)
        .filter(([_, value]) => value !== undefined)
        .map(([key, value]) =>
          typeof value === 'boolean'
            ? value
              ? `${key}`
              : `${key}={false}`
            : `${key}=${JSON.stringify(value)}`,
        )
        .join(' ')} />`;
    }
    if (activeTab === 'JSON') {
      return JSON.stringify(args, null, 2);
    }
    if (activeTab === 'CSS') {
      return Object.entries(args)
        .filter(([_, value]) => value !== undefined)
        .map(([key, value]) => {
          const cssVarName = `--${key.replace(/[A-Z]/g, (match) => `-${match.toLowerCase()}`)}`;
          return `${cssVarName}: ${value};`;
        })
        .join('\n');
    }
    return '';
  }, [activeTab, componentName, args]);

  const handleCopyReset = useCallback(() => {
    setTimeout(() => setIsCopied(false), 1000);
  }, []);

  const copyCode = useCallback(async () => {
    const code = generateCode();
    try {
      await navigator.clipboard.writeText(code);
      setIsCopied(true);
      handleCopyReset();
    } catch (error) {
      console.error('Failed to copy code:', error);
    }
  }, [generateCode, handleCopyReset]);

  // Memoized tab handlers
  const handleSetReactTab = useCallback(() => setActiveTab('React'), []);
  const handleSetJsonTab = useCallback(() => setActiveTab('JSON'), []);
  const handleSetCssTab = useCallback(() => setActiveTab('CSS'), []);
  const handleCopyClick = useCallback(() => {
    void copyCode();
  }, [copyCode]);

  return (
    <div className={classNames('code-preview-box', smallFont ? 'small-font-size' : '')}>
      <div className="tabs">
        {reactFormat && (
          <button
            type="button"
            onClick={handleSetReactTab}
            className={activeTab === 'React' ? 'active' : ''}
          >
            React
          </button>
        )}
        {jsonFormat && (
          <button
            type="button"
            onClick={handleSetJsonTab}
            className={activeTab === 'JSON' ? 'active' : ''}
          >
            JSON
          </button>
        )}
        {cssFormat && (
          <button
            type="button"
            onClick={handleSetCssTab}
            className={activeTab === 'CSS' ? 'active' : ''}
          >
            CSS
          </button>
        )}
      </div>

      <div className="code-content">
        {activeTab === 'React' && reactFormat && (
          <>
            <div className="import">
              import <span className="blue">{componentName}</span> from{' '}
              <span className="green">'@getvim/atomic-ui'</span>
            </div>
            <br />
            <div>{generateCode()}</div>
          </>
        )}
        {activeTab === 'JSON' && jsonFormat && <div>{generateCode()}</div>}
        {activeTab === 'CSS' && cssFormat && <div>{generateCode()}</div>}
      </div>

      <button
        type="button"
        onClick={handleCopyClick}
        className={`copy-btn ${isCopied ? 'copied' : ''}`}
      >
        {isCopied ? (
          <>
            <i className="icon-copy i-va-fix-2" />
            &nbsp;Copied
            <i className="icon-check" />
          </>
        ) : (
          <>
            <i className="icon-copy i-va-fix-2" /> Copy
          </>
        )}
      </button>
    </div>
  );
};

export default CodePreview;
