import { PatientEnhancements, PatientEnhancementsAPI } from './patient';
import { EHRResource } from '..';
import { ReferralEnhancements, ReferralEnhancementsAPI } from './referral';
import { EncounterEnhancements, EncounterEnhancementsAPI } from './encounter';
import { OrdersEnhancements, OrdersEnhancementsAPI } from './orders';

export type EHRResourceEnhancementsApi<T extends EHRResource> = Partial<
  T extends EHRResource.patient
    ? PatientEnhancementsAPI
    : T extends EHRResource.referral
    ? ReferralEnhancementsAPI
    : T extends EHRResource.encounter
    ? EncounterEnhancementsAPI
    : T extends EHRResource.orders
    ? OrdersEnhancementsAPI
    : Record<string, never>
>;

export type { PatientEnhancementsAPI } from './patient';
export { PatientEnhancements } from './patient';

export const EHRResourceEnhancements = {
  [EHRResource.patient]: PatientEnhancements,
  [EHRResource.referral]: ReferralEnhancements,
  [EHRResource.encounter]: EncounterEnhancements,
  [EHRResource.orders]: OrdersEnhancements,
};

export type AllEnhancements = PatientEnhancementsAPI &
  ReferralEnhancementsAPI &
  EncounterEnhancementsAPI &
  OrdersEnhancementsAPI;
