import {
  SpanKind,
  SpanStatusCode,
  context,
  trace,
  type Exception,
  type SpanOptions,
  type Tracer,
} from '@opentelemetry/api';
import { inject, injectable, interfaces } from 'inversify';
import { AttributesInjector } from '../attributes/injector';
import { VimSpan } from './vimSpan';

export const VimTracerFactory = Symbol('Factory<VimTracerFactory>');

export type VimTracerFactoryDynamicArgs = [Tracer];

const Tracer = Symbol('Tracer');

export type VimTracerFactory = interfaces.SimpleFactory<VimTracer, VimTracerFactoryDynamicArgs>;

export const createVimTracer = (container: interfaces.Container, tracer: Tracer) => {
  const childContainer = container.createChild();
  childContainer.bind<Tracer>(Tracer).toConstantValue(tracer);

  return childContainer.get(VimTracer);
};

@injectable()
export class VimTracer {
  constructor(
    @inject(Tracer) public openTelemetryTracer: Tracer,
    @inject(AttributesInjector) public injector: AttributesInjector,
  ) {}

  startRootSpan(name: string, options?: SpanOptions): VimSpan {
    const openTelemetrySpan = this.openTelemetryTracer.startSpan(
      name,
      {
        root: true,
        kind: SpanKind.CLIENT,
        ...options,
        attributes: this.injector.injectToAttributesObj(options?.attributes),
      },
      context.active(),
    );

    return new VimSpan(openTelemetrySpan, this);
  }

  async startAutoRootSpan<T>(
    name: string,
    options: SpanOptions | undefined,
    cb: (rootSpan: VimSpan) => Promise<T>,
  ): Promise<T> {
    const rootSpan = this.startRootSpan(name, options);
    try {
      return await cb(rootSpan);
    } catch (error: any) {
      rootSpan.recordException(error);
      rootSpan.setStatus({ code: SpanStatusCode.ERROR });
      throw error;
    } finally {
      rootSpan.end();
    }
  }

  startChildSpan(parentSpan: VimSpan, name: string, options?: SpanOptions): VimSpan {
    const ctx = trace.setSpan(context.active(), parentSpan);

    const openTelemetryChildSpan = this.openTelemetryTracer.startSpan(
      name,
      { ...(options || {}), attributes: this.injector.injectToAttributesObj(options?.attributes) },
      ctx,
    );

    return new VimSpan(openTelemetryChildSpan, this);
  }

  async startAutoChildSpan<T>(
    parentSpan: VimSpan,
    name: string,
    options: SpanOptions | undefined,
    cb: (childSpan: VimSpan) => Promise<T>,
  ): Promise<T> {
    const childSpan = this.startChildSpan(parentSpan, name, options);
    try {
      return await cb(childSpan);
    } catch (error) {
      childSpan.recordException(error as Exception);
      childSpan.setStatus({ code: SpanStatusCode.ERROR });
      throw error;
    } finally {
      childSpan.end();
    }
  }
}
