import React, { TextareaHTMLAttributes, RefObject } from 'react';
import classNames from 'classnames';
import './index.less';

export interface LabeledTextAreaProps extends TextareaHTMLAttributes<HTMLTextAreaElement> {
  active?: boolean;
  label?: string;
  htmlFor?: string;
  refs?: RefObject<HTMLTextAreaElement>;
}

export default function LabeledTextArea({
  active,
  htmlFor,
  label,
  refs,
  ...textarea
}: LabeledTextAreaProps) {
  return (
    <div className="input">
      <label
        aria-hidden="true"
        className={classNames('atomic-textarea-label', { 'textarea-active': active })}
        htmlFor={htmlFor}
      >
        {label}
      </label>
      <textarea {...textarea} ref={refs} />
    </div>
  );
}
