import { z } from 'zod';
import { DiagnosisSchema, ProcedureSchema, SafeTextSchema } from '../../entities/sub-entities';

export const UpdateEncounterSchema = z.object({
  subjective: z
    .object({
      generalNotes: SafeTextSchema.optional(),
      chiefComplaintNotes: SafeTextSchema.optional(),
      historyOfPresentIllnessNotes: SafeTextSchema.optional(),
      reviewOfSystemsNotes: SafeTextSchema.optional(),
    })
    .optional(),
  objective: z
    .object({
      generalNotes: SafeTextSchema.optional(),
      physicalExamNotes: SafeTextSchema.optional(),
    })
    .optional(),
  assessments: z
    .object({
      generalNotes: SafeTextSchema.optional(),
      diagnosisCodes: z.array(DiagnosisSchema).optional(),
    })
    .optional(),
  plans: z
    .object({
      generalNotes: SafeTextSchema.optional(),
      procedureCodes: z.array(ProcedureSchema).optional(),
    })
    .optional(),
  patientInstructions: z
    .object({
      generalNotes: SafeTextSchema.optional(),
    })
    .optional(),
});

export type UpdateEncounter = z.infer<typeof UpdateEncounterSchema>;
