import { inject, injectable } from 'inversify';
import { VimOsBffGraphQLClient } from '../vimOsBffGqlClient';
import { PUT_ENCRYPTED_DATA } from './mutations';
import { GET_SESSION_KEY_FOR_ENCRYPTION } from './queries';
import { SessionKeyInput, SessionKeyResponse, VaultPutInput, VaultPutResult } from './types';

@injectable()
export class VaultApi {
  constructor(@inject(VimOsBffGraphQLClient) private gqlClient: VimOsBffGraphQLClient) {}

  public async getSessionKey(input: SessionKeyInput): Promise<SessionKeyResponse> {
    const { data } = await this.gqlClient.queryAuthenticated<
      { getSessionKeyForEncryption: SessionKeyResponse },
      { input: SessionKeyInput }
    >({
      query: GET_SESSION_KEY_FOR_ENCRYPTION,
      variables: { input },
    });
    return data?.getSessionKeyForEncryption;
  }

  public putEncryptedData = async (input: VaultPutInput[], abortSignal?: AbortSignal) => {
    const result = await this.gqlClient.mutateAuthenticated<VaultPutResult>({
      mutation: PUT_ENCRYPTED_DATA(input),
      variables: input.reduce(
        (aggregate, { entityType, iv, sensitiveData, sessionKeyId }) => ({
          ...aggregate,
          [`input_${entityType.toLowerCase()}`]: {
            entityType,
            iv,
            sensitiveData,
            sessionKeyId,
          },
        }),
        {},
      ),
      context: {
        fetchOptions: {
          signal: abortSignal,
        },
      },
      fetchPolicy: 'network-only',
    });

    return result.data!;
  };
}
