import { Logger, LoggerFactory } from '@getvim-os/logger';
import { VimOsGraphQLClient } from '@getvim-os/gql-client';
import { inject, injectable } from 'inversify';
import { LOG_WRITEBACK_AUDIT } from './mutations';
import { WritebackAuditLogPayload } from './types';

@injectable()
export class WritebackAuditLoggerApi {
  private logger: Logger;

  constructor(
    @inject(VimOsGraphQLClient) private gqlClient: VimOsGraphQLClient,
    @inject(LoggerFactory) loggerFactory: LoggerFactory,
  ) {
    this.logger = loggerFactory.create('WritebackAuditLogger-API');
  }

  public log = async (logPayload: WritebackAuditLogPayload): Promise<void> => {
    try {
      const result = await this.gqlClient.mutateAuthenticated({
        mutation: LOG_WRITEBACK_AUDIT,
        variables: { input: logPayload },
      });

      if (result && 'success' in result) {
        const { success } = result;
        if (!success) {
          this.logger.warning('Logging writeback did not succeed', {
            logPayload,
          });
        }
      }
    } catch (error) {
      this.logger.error(
        'Failed log to writeback request',
        {
          logPayload,
        },
        error as Error,
      );
    }
  };
}
