import { inject, injectable } from 'inversify';
import { VimOsBffGraphQLClient } from '../vimOsBffGqlClient';
import { CREATE_APP_LAUNCH, VERIFY_APP_TOKEN } from './mutations';
import {
  CreateAppLaunchInput,
  CreateAppLaunchResponse,
  VerifyAppTokenInput,
  VerifyAppTokenResponse,
} from './types';

@injectable()
export class VimAppAuthApi {
  constructor(@inject(VimOsBffGraphQLClient) private gqlClient: VimOsBffGraphQLClient) {}

  public async createAppLaunch(input: CreateAppLaunchInput): Promise<CreateAppLaunchResponse> {
    const result = await this.gqlClient.mutateAuthenticated<CreateAppLaunchResponse>({
      mutation: CREATE_APP_LAUNCH,
      variables: {
        input,
      },
    });

    return result.data!;
  }

  public async verifyAppToken(input: VerifyAppTokenInput): Promise<VerifyAppTokenResponse> {
    const result = await this.gqlClient.mutateAuthenticated<VerifyAppTokenResponse>({
      mutation: VERIFY_APP_TOKEN,
      variables: {
        input,
      },
    });

    return result.data!;
  }
}
