import { EntityType, VaultPutResult } from '@/internal-api/vault/types';
import { VimTracerProvider } from '@getvim-os/telemetry';
import { EHRResource } from '@getvim-os/types';
import { inject, injectable } from 'inversify';
import { v4 as uuid } from 'uuid';
import { EhrPIIRemover } from '../ehrPIIRemover';
import { PendingEhrState } from '../types';
import { VaultManager } from './vault.service';

@injectable()
export class VaultTokenizerService {
  private tokensCache: Record<
    string,
    {
      content: string;
      token: string;
    }
  > = {};

  constructor(
    @inject(EhrPIIRemover) private ehrPIIRemover: EhrPIIRemover,
    @inject(VaultManager) private vaultManager: VaultManager,
    @inject(VimTracerProvider) private traceProvider: VimTracerProvider,
  ) {}

  public async generateEntitiesTokens(
    pendingEhrState: PendingEhrState,
    abortSignal?: AbortSignal,
  ): Promise<VaultPutResult> {
    const tracer = this.traceProvider.getTracer('tokenize-entities');
    return await tracer.startAutoRootSpan('entities-tokenize-logic', {}, async (rootSpan) => {
      const tokens: VaultPutResult = {};
      const entitiesToTokenize: {
        [key in EHRResource]?: { id: any; entityType: EntityType };
      } = {};

      if (pendingEhrState.patient) {
        const [_, { patientId, ...patientPii }] = this.ehrPIIRemover.removePatientPII(
          pendingEhrState.patient,
        );

        const vaultPayload = {
          id: patientId,
          entityType: EntityType.PATIENT,
          ...patientPii,
        };

        const content = this.encodeEntityPayload(vaultPayload);
        if (this.tokensCache[patientId]?.content === content) {
          tokens.patientPut = this.tokensCache[patientId].token;
        } else {
          entitiesToTokenize.patient = vaultPayload;
        }
      }
      if (pendingEhrState.referral) {
        const [cleanReferral, referralPii] = this.ehrPIIRemover.removeReferralPII(
          pendingEhrState.referral,
        );

        const vaultPayload = {
          id: cleanReferral.vimReferralId ?? uuid(),
          entityType: EntityType.REFERRAL,
          ...referralPii,
        };

        const content = this.encodeEntityPayload(vaultPayload);
        if (this.tokensCache[vaultPayload.id]?.content === content) {
          tokens.referralPut = this.tokensCache[vaultPayload.id].token;
        } else {
          entitiesToTokenize.referral = vaultPayload;
        }
      }

      const sensitiveDataForVault = Object.values(entitiesToTokenize).map(
        ({ entityType, ...sensitiveData }) => ({
          entityType,
          sensitiveData,
        }),
      );

      const results =
        sensitiveDataForVault.length > 0
          ? await rootSpan.startAutoChildSpan(
              'put sensitive data in vault',
              {},
              async (childSpan) =>
                await this.vaultManager.putSensitiveDataInVault(
                  sensitiveDataForVault,
                  abortSignal,
                  childSpan,
                ),
            )
          : {};

      if (entitiesToTokenize.patient && results.patientPut) {
        this.tokensCache[entitiesToTokenize.patient.id] = {
          content: this.encodeEntityPayload(entitiesToTokenize.patient),
          token: results.patientPut,
        };
      }
      if (entitiesToTokenize.referral && results.referralPut) {
        this.tokensCache[entitiesToTokenize.referral.id] = {
          content: this.encodeEntityPayload(entitiesToTokenize.referral),
          token: results.referralPut,
        };
      }

      return {
        ...tokens,
        ...results,
      };
    });
  }

  private encodeEntityPayload(entity: { id: any }) {
    const uniqueId = window.btoa(encodeURIComponent(JSON.stringify(entity)));
    return uniqueId;
  }
}
