import { injectable, inject, interfaces } from 'inversify';
import { Logger, VimOsLogger } from './logger';
import { LoggerTags } from './types';
import { SYMBOLS } from './symbols';

export const loggerFactoryFn = (
  scopedContainer: interfaces.Container,
  scope: string,
  moduleName?: string,
  tags?: LoggerTags,
) => {
  const logger = scopedContainer.get(VimOsLogger);
  logger.scope = scope;
  if (moduleName) {
    logger.moduleName = moduleName;
  }
  if (tags) {
    logger.tags = tags;
  }

  return logger;
};

export type LoggerFactoryParameters = { moduleName?: string; tags?: LoggerTags };

@injectable()
export class LoggerFactory {
  constructor(
    @inject(SYMBOLS.LoggerFactoryFn)
    private loggerFactoryFn: interfaces.SimpleFactory<Logger>,

    @inject(SYMBOLS.ScopedContainer) private scopedContainer: interfaces.Container,
  ) {}

  public create(scope: string, params?: LoggerFactoryParameters): Logger {
    return this.loggerFactoryFn(this.scopedContainer, scope, params?.moduleName, params?.tags);
  }
}
