import React from 'react';
export const iframeWidgetIdTag = 'vim-connect-ui-widget-id';

import { getWindowTabId } from '@/infra/hostInfo';

const tabId = getWindowTabId();
export const generateIframeUrl = (iframeUrl: string) => {
  const url = new URL(iframeUrl);
  url.searchParams.set('tabId', tabId);

  return url.href;
};

type IframeProps = {
  appId: string;
  iframeSource: string;
  onIframeLoad?: React.ReactEventHandler<HTMLIFrameElement>;
};

export const ApplicationIframe: React.FC<IframeProps> = ({ appId, onIframeLoad, iframeSource }) => (
  <iframe
    {...{ [iframeWidgetIdTag]: appId }}
    onLoad={onIframeLoad}
    title={appId}
    src={generateIframeUrl(iframeSource)}
    width="100%"
    height="100%"
    allow="clipboard-write; microphone; screen-wake-lock;"
  />
);
