import { injectable } from 'inversify';
import { EnrichedPatientPII } from '@getvim-os/types';
import { OSEhrStateWithPII, OSEhrState, EHRResource } from '@getvim/vim-os-api';

import { PendingEhrState } from './types';

type ParsableEntity<RESOURCE extends EHRResource> =
  | Required<OSEhrStateWithPII>[RESOURCE]
  | Required<PendingEhrState>[RESOURCE];

type PIIRemovedEntity<
  RESOURCE extends EHRResource,
  T extends ParsableEntity<RESOURCE>,
> = T extends Required<OSEhrStateWithPII>[RESOURCE]
  ? Required<OSEhrState>[RESOURCE]
  : T extends Required<PendingEhrState>[RESOURCE]
  ? Omit<Required<OSEhrState>[RESOURCE], 'token'>
  : never;

@injectable()
export class EhrPIIRemover {
  private resourceToRemover = {
    [EHRResource.referral]: this.removeReferralPII,
    [EHRResource.patient]: this.removePatientPII,
    [EHRResource.encounter]: this.removeEncounterPII,
    [EHRResource.orders]: this.removeOrdersPII,
  };
  constructor() {}

  public removePIIFromResource<T extends EHRResource>(
    resource: T,
    resourceState: Required<OSEhrStateWithPII>[T],
  ): [PIIRemovedEntity<T, Required<OSEhrStateWithPII>[T]>, any] {
    return (this.resourceToRemover[resource] as any).bind(this)(resourceState);
  }

  public removeOrdersPII<
    T extends ParsableEntity<typeof EHRResource.orders> = ParsableEntity<typeof EHRResource.orders>,
  >(orders: T) {
    return [orders, {}];
  }

  public removeEncounterPII<
    T extends ParsableEntity<typeof EHRResource.encounter> = ParsableEntity<
      typeof EHRResource.encounter
    >,
  >(encounter: T) {
    return [encounter, {}];
  }

  public removePatientPII({
    insurance,
    pcp,
    problemList,
    vimPatientId,
    memberTokens,
    contentSupplierIdentifiers,
    potentialInsurers,
    contentSupplierInternalPersonIds,
    ...restOfPatient
  }: ParsableEntity<typeof EHRResource.patient>) {
    const { ehrInsurance, groupId, insurer, payerId, ...insurancePII } = insurance ?? {};

    const { demographics, patientId, contact_info, mrn } = restOfPatient;
    const pii: EnrichedPatientPII = {
      demographics,
      patientId,
      contact_info,
      insurance,
      mrn,
    };
    let token;
    if ('token' in restOfPatient) {
      token = restOfPatient.token;
    }

    return [
      {
        token,
        insurance: insurance ? { ehrInsurance, groupId, insurer, payerId } : undefined,
        pcp,
        problemList,
        vimPatientId,
        memberTokens,
        contentSupplierIdentifiers,
        potentialInsurers,
        contentSupplierInternalPersonIds,
        demographics: {
          address: restOfPatient.demographics?.address
            ? { state: restOfPatient.demographics.address.state }
            : undefined,
        },
      } as PIIRemovedEntity<typeof EHRResource.patient, ParsableEntity<typeof EHRResource.patient>>,
      {
        ...pii,
        insurance: insurance ? insurancePII : undefined,
      } as EnrichedPatientPII,
    ] as const;
  }

  public removeReferralPII<
    T extends ParsableEntity<typeof EHRResource.referral> = ParsableEntity<
      typeof EHRResource.referral
    >,
  >({
    referralId,
    vimReferralId,
    specialty,
    diagnosis,
    targetProvider,
    referringProvider,
    startDate,
    endDate,
    createdDate,
    status,
    priority,
    numberOfVisits,
    cpts,
    facilityName,
    authCode,
    isLocked,
    ...pii
  }: T) {
    let token: string | undefined = undefined;

    if ('token' in pii) {
      token = pii.token as string;
      delete pii.token;
    }

    return [
      {
        referralId,
        vimReferralId,
        specialty,
        diagnosis,
        targetProvider,
        referringProvider,
        startDate,
        endDate,
        createdDate,
        status,
        priority,
        numberOfVisits,
        cpts,
        facilityName,
        authCode,
        isLocked,
        token,
      } as PIIRemovedEntity<typeof EHRResource.referral, T>,
      pii,
    ] as const;
  }
}
