import { WebConnectors } from '../..';
import { WebConnectorMessageTypes } from '../../enum';
import { WebRequestAwait } from '../../types/webRequestAwait';

export async function awaitForWebRequest(
  this: WebConnectors,
  { urlPattern, timeoutMs = 5000 }: WebRequestAwait,
): Promise<boolean> {
  let resolver;
  const listenerPromise = new Promise((resolve) => (resolver = resolve));
  const listenerId = await this.messaging.sendMessageWithHandler({
    type: WebConnectorMessageTypes.addWebRequestListener,
    payload: { urlPattern },
    handler: resolver,
  });
  let result = true;
  try {
    await Promise.race([
      listenerPromise,
      new Promise((_resolve, reject) => setTimeout(reject, timeoutMs)),
    ]);
  } catch {
    result = false;
  }
  await this.removeWebRequestListener({ listenerId: listenerId as string });
  return result;
}
