import { inject, injectable } from 'inversify';
import { OsMessageChannelManager } from '../deprecated/runtime-wrapper/osMessageChannelManager';
import { Events } from '@deprecated/types';
import {
  BaseProperties,
  TrackPayload,
  Sessions,
  WritebackAnalyticsEventTypes,
  WritebackStatus,
} from './events';
import { EHRResource, TrackPayload as AppTrackPayload } from '@getvim/vim-os-api';
import { Logger, LoggerFactory } from '@getvim-os/logger';
import { BaseStateManager } from '@/store/lib/baseStateManager.service';
import { OsState } from '@/store';
import { getAffectedFields } from '@/adapter-manager/utils';
import { InjectedApplication } from '@/hub/types/application';

interface TrackWritebackPayload {
  resource: EHRResource;
  writebackData: any;
  appId: string;
  application: InjectedApplication | undefined;
  status: WritebackStatus;
  duration?: number;
  organization_tenant_patient_token?: string;
}

@injectable()
export class AnalyticsManager extends BaseStateManager {
  private logger: Logger;
  constructor(
    @inject(OsState) osState: OsState,
    @inject(OsMessageChannelManager) private osMessageChannelManager: OsMessageChannelManager,
    @inject(LoggerFactory) loggerFactory: LoggerFactory,
  ) {
    super(osState);
    this.logger = loggerFactory.create('AnalyticsManager');
  }

  public track(payload: TrackPayload) {
    return this.osMessageChannelManager.sendMessageToRuntime({
      type: Events.OsToRuntimeEvents.ANALYTICS_TRACK,
      payload: {
        event: payload.event,
        properties: {
          ...payload.properties,
          organization_tenant_patient_token: this.store.getState().ehrState.patient?.token,
        },
      } as TrackPayload,
    });
  }

  public setSession(payload: Sessions) {
    return this.osMessageChannelManager.sendMessageToRuntime({
      type: Events.OsToRuntimeEvents.ANALYTICS_SET_SESSIONS,
      payload,
    });
  }
  public setBaseProperties(payload: BaseProperties) {
    return this.osMessageChannelManager.sendMessageToRuntime({
      type: Events.OsToRuntimeEvents.ANALYTICS_SET_BASE_PROPERTIES,
      payload,
    });
  }

  public async appTrack(payload: AppTrackPayload, appId: string) {
    const isAllowedUsingAnalytics = this.getManifest(appId)?.vimCapabilities.trackAnalytics;
    if (!isAllowedUsingAnalytics) {
      return this.logger.error(
        `Application ${appId} is not allowed to use analytics; please update the application manifest & get it approved.`,
      );
    } else {
      return this.osMessageChannelManager.sendMessageToRuntime({
        type: Events.OsToRuntimeEvents.APP_ANALYTICS_TRACK,
        payload,
      });
    }
  }

  public trackWriteback({
    resource,
    writebackData,
    appId,
    application,
    status,
    duration = 0,
  }: TrackWritebackPayload) {
    this.track({
      event: WritebackAnalyticsEventTypes.VimHubEhrWriteback,
      properties: {
        updated_entity: resource,
        writeback_status: status,
        writeback_details: getAffectedFields(writebackData),
        writeback_duration_in_ms: duration,
        app_details: {
          app_name: application?.manifest?.metadata.name || appId,
          app_id: appId,
          sdk_version: application?.sdkVersion,
          app_version: application?.manifest?.version,
        },
      },
    });
  }
}
