import { ErrorCodes } from '..';
import { StandardError } from '../base-error';

export class InvalidInputError extends StandardError {
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  constructor(message: string, props: any = {}) {
    super(message, ErrorCodes.INVALID_INPUT, { errorName: 'InvalidInput', ...props });
  }
}
