import { z } from 'zod';
import { VimEntityTypeEnum } from '../entities/vimEntity';

const SubSectionRequest = z.object({
  read: z.boolean().optional(),
  write: z.boolean().optional(),
});

export const EntitiesRequestSchema = z
  .object({
    [VimEntityTypeEnum.referral]: z.object({
      identifiers: z.optional(SubSectionRequest),
      basicInformation: z.optional(SubSectionRequest),
      conditions: z.optional(SubSectionRequest),
      procedureCodes: z.optional(SubSectionRequest),
      referringProvider: z.optional(SubSectionRequest),
      targetProvider: z.optional(SubSectionRequest),
    }),
    [VimEntityTypeEnum.editableReferral]: z.object({
      identifiers: z.optional(SubSectionRequest),
      basicInformation: z.optional(SubSectionRequest),
      conditions: z.optional(SubSectionRequest),
      procedureCodes: z.optional(SubSectionRequest),
      referringProvider: z.optional(SubSectionRequest),
      targetProvider: z.optional(SubSectionRequest),
    }),
    [VimEntityTypeEnum.order]: z.object({
      identifiers: z.optional(SubSectionRequest),
      basicInformation: z.optional(SubSectionRequest),
      assessments: z.optional(SubSectionRequest),
      procedureCodes: z.optional(SubSectionRequest),
    }),
    [VimEntityTypeEnum.encounter]: z.object({
      identifiers: z.optional(SubSectionRequest),
      basicInformation: z.optional(SubSectionRequest),
      subjective: z.optional(SubSectionRequest),
      objective: z.optional(SubSectionRequest),
      assessment: z.optional(SubSectionRequest),
      plan: z.optional(SubSectionRequest),
      patientInstructions: z.optional(SubSectionRequest),
      provider: z.optional(SubSectionRequest),
    }),
    [VimEntityTypeEnum.editableEncounter]: z.object({
      identifiers: z.optional(SubSectionRequest),
      basicInformation: z.optional(SubSectionRequest),
      subjective: z.optional(SubSectionRequest),
      objective: z.optional(SubSectionRequest),
      assessment: z.optional(SubSectionRequest),
      plans: z.optional(SubSectionRequest),
      patientInstructions: z.optional(SubSectionRequest),
      provider: z.optional(SubSectionRequest),
    }),
    [VimEntityTypeEnum.patient]: z.object({
      identifiers: z.optional(SubSectionRequest),
      demographics: z.optional(SubSectionRequest),
      address: z.optional(SubSectionRequest),
      contactInformation: z.optional(SubSectionRequest),
      insuranceInfo: z.optional(SubSectionRequest),
      problemList: z.optional(SubSectionRequest),
      pcp: z.optional(SubSectionRequest),
    }),
  })
  .partial();

export type EntitiesRequest = z.infer<typeof EntitiesRequestSchema>;
