/**
 * Vim error codes for writeback flow.
 */
export type writebackErrorCodes = {
  UNKNOWN_ERROR: writebackErrorCodesEnum.UNKNOWN_ERROR;
  STOP_WRITEBACK: writebackErrorCodesEnum.STOP_WRITEBACK;
  ALL_FIELDS_FAILED_WRITEBACK: writebackErrorCodesEnum.ALL_FIELDS_FAILED_WRITEBACK;
  SOME_FIELDS_FAILED_WRITEBACK: writebackErrorCodesEnum.SOME_FIELDS_FAILED_WRITEBACK;
  PROCEDURE_WRITEBACK: writebackErrorCodesEnum.PROCEDURE_WRITEBACK;
  PROCEDURE_WRITEBACK_VALIDATION: writebackErrorCodesEnum.PROCEDURE_WRITEBACK_VALIDATION;
  DIAGNOSIS_WRITEBACK: writebackErrorCodesEnum.DIAGNOSIS_WRITEBACK;
  DIAGNOSIS_WRITEBACK_VALIDATION: writebackErrorCodesEnum.DIAGNOSIS_WRITEBACK_VALIDATION;
  ENCOUNTER_NOT_READY_FOR_WRITEBACK: writebackErrorCodesEnum.ENCOUNTER_NOT_READY_FOR_WRITEBACK;
  REFERRAL_WRITEBACK: writebackErrorCodesEnum.REFERRAL_WRITEBACK;
  FAIL_TO_GET_MESSAGE_FROM_EXTENSION: writebackErrorCodesEnum.FAIL_TO_GET_MESSAGE_FROM_EXTENSION;
  PERMISSION_DENIED: writebackErrorCodesEnum.PERMISSION_DENIED;
};

export enum writebackErrorCodesEnum {
  UNKNOWN_ERROR = 6000,
  STOP_WRITEBACK = 6100,
  ALL_FIELDS_FAILED_WRITEBACK = 6010,
  SOME_FIELDS_FAILED_WRITEBACK = 6155,
  PROCEDURE_WRITEBACK = 6001,
  PROCEDURE_WRITEBACK_VALIDATION = 6101,
  DIAGNOSIS_WRITEBACK = 6002,
  DIAGNOSIS_WRITEBACK_VALIDATION = 6102,
  ENCOUNTER_NOT_READY_FOR_WRITEBACK = 6103,
  REFERRAL_WRITEBACK = 6003,
  FAIL_TO_GET_MESSAGE_FROM_EXTENSION = 6199,
  PERMISSION_DENIED = 6004,
}
