import { AuthEvents } from '../../deprecated/types/events';
import { Product } from '../../hub/types/analytics';
import { VaultTokenizerService } from '../ehr/vault-tokenizer/vaultTokenizer.service';
import { BaseStateManager } from '../lib/baseStateManager.service';
import { ApplicationOverridesManager } from './applicationOverrides.service';
import { actions, getTodayDate } from './applicationsState.slice';
import { createAppLaunchId } from './applicationsState.thunk';
import { ResourcePermissionsService } from './resourcePermissions.service';
import { PopupHistory, SelectApplicationPayload } from './types';
import { WritebackStatus } from '@/analytics-manager/events';
import { AnalyticsManager } from '@/analytics-manager';

export const AUTO_POPUP_DELAY = 1000;

const POPUP_HISTORY_STORAGE_KEY = 'POPUP_HISTORY';

const unlimitedPopupApps = new Set<string>([
  Product.OrderAssist,
  Product.PriorAuthCheck,
  Product.OptumReferralUtility,
]);

type AppLimiter = Map<string, RateLimiter>;
@injectable()
export class ApplicationManager extends BaseStateManager implements InitializeOnBootstrap {
  private appActionsLimiter: AppLimiter = new Map<string, RateLimiter>();
  private appResourceUpdateLimiter: AppLimiter = new Map<string, RateLimiter>();
  private logger: Logger;
  private telLogger: VimTelemetryLogger;
  private appPendingPopup: string | undefined;
  constructor(
    @inject(VaultTokenizerService) private tokenizer: VaultTokenizerService,
    @inject(OsState) osState: OsState,
    @inject(RuntimeMessageHooks) private messageHooks: RuntimeMessageHooks,
    @inject(AppsToVimConnectCommunicatorMessageListener)
    private oldAppsMessageListener: AppsToVimConnectCommunicatorMessageListener,
    @inject(AdapterManager) private adapterManager: AdapterManager,
    @inject(LoggerFactory) loggerFactory: LoggerFactory,
    @inject(BareboneStorage) private storage: BareboneStorage,
    @inject(BareboneManager) private bareboneManager: BareboneManager,
    @inject(ApplicationOverridesManager)
    private applicationOverridesManager: ApplicationOverridesManager,
    @inject(OsFeatureFlagsClient) private ffClient: OsFeatureFlagsClient,
    @inject(ResourcePermissionsService)
    private resourcePermissionsService: ResourcePermissionsService,
    @inject(VimTelemetryLogsProvider) telemetryLogsProvider: VimTelemetryLogsProvider,
    @inject(AnalyticsManager) private analyticsManager: AnalyticsManager,
  ) {
    super(osState);
    this.logger = loggerFactory.create('ApplicationManager');
    this.telLogger = telemetryLogsProvider.getLogger('ApplicationManager');
  }

  public initialize() {
    this.listenToEvents();
    this.initPopupHistory().catch((error) => {
      this.logger.error('Failed to initialize popup history', error);
    });
  }

  private listenToEvents() {
    this.messageHooks.addOSMessageHandler(
      VIM_OS_EVENTS.APPLICATION_MANAGEMENT_EVENTS,
      this.onApplicationManagementEvents.bind(this),
    );
    this.messageHooks.addOSMessageHandler(
      VIM_OS_EVENTS.AUTH_EVENT,
      this.onAuthMessageEvent.bind(this),
    );
    this.oldAppsMessageListener.listenToEvents({
      [Events.VimConnectUIEvents.VCNewModifyApplication]:
        this.modifyNonAppstoreApplication.bind(this),
    });
  }

  private convertNotificationsBeforeModifyApp(data) {
    if (data.notifications === 0) {
      data.clearNotificationsIndicator = true;
    } else if (data.notifications > 0) {
      data.clearNotificationsIndicator = false;
    }
    return data;
  }

  private modifyNonAppstoreApplication(data, frameSource: FrameSource) {
    if (data.data?.notifications !== undefined) {
      data.data = this.convertNotificationsBeforeModifyApp(data.data);
    }
    const senderWidgetId = extractSenderWidgetId(frameSource);
    if (senderWidgetId) {
      this.store.dispatch(
        actions.modifyApplication({ id: senderWidgetId, modifiedApplication: data.data }),
      );
      return true;
    }
    return false;
  }

  private getAllowedOrigins(manifest: ApplicationManifest): string[] {
    const { allowedUrls, url } = manifest.metadata;
    try {
      if (allowedUrls) {
        return allowedUrls
          .split(',')
          .map((url) => {
            try {
              const origin = new URL(url.trim()).origin;
              return origin;
            } catch (error) {
              const message = 'Error extracting allowed origins from allowedUrls';
              this.logger.error('Error extracting allowed origins from allowedUrls', {
                allowedUrls,
                url,
                error,
              });
              this.telLogger.alert(new Error(message, { cause: error }), { url, allowedUrls });
              return null;
            }
          })
          .filter((url) => !!url) as string[];
      }

      return [new URL(url).origin];
    } catch (error) {
      const message = 'Error extracting allowed origins from allowedUrls';
      this.logger.error('Error extracting allowed origins from allowedUrls', {
        allowedUrls,
        url,
        error,
      });
      this.telLogger.alert(new Error(message, { cause: error }), { url, allowedUrls });
      return [];
    }
  }

  private async onAuthMessageEvent({
    type,
    payload,
  }: Events.RuntimeToOsMessage[VIM_OS_EVENTS.AUTH_EVENT]) {
    switch (type) {
      case AuthEvents.INITIALIZE_ME:
        {
          const { ehrUser } = payload.me;
          const { appStoreApplications = [] } = ehrUser;
          const applicationOverrides =
            (await this.applicationOverridesManager.getApplicationOverrides(
              appStoreApplications,
            )) || {};

          await Promise.all(
            appStoreApplications.map(async (app) => {
              try {
                const appId = app.metadata.id;
                const override = applicationOverrides[appId];

                let finalApp = app;

                if (override) {
                  finalApp = override;
                }

                const appInOAuthMode = await this.ffClient.getFlag({
                  flagName: 'app_store_app_oauth_mode',
                  flagContext: {
                    appId: finalApp.metadata.id,
                    appVersion: finalApp.version,
                  },
                  defaultValue: false,
                });

                const allowedOrigins = this.getAllowedOrigins(finalApp);

                this.store.dispatch(
                  actions.addApplication({
                    id: finalApp.metadata.id,
                    tooltip: finalApp.UI.tooltip,
                    iframeSource: finalApp.metadata.url,
                    disabled: true,
                    oAuthMode: appInOAuthMode,
                    responseTime: 0,
                    manifest: finalApp,
                    initializeAttempt: 0,
                    injectSource: AuthEvents.INITIALIZE_ME,
                    allowedOrigins,
                  }),
                );

                if (appInOAuthMode) {
                  this.store.dispatch(
                    createAppLaunchId({
                      appId: finalApp.metadata.id,
                      version: finalApp.version,
                      tokenEndpoint: finalApp.metadata.tokenEndpoint,
                    }),
                  );
                }
              } catch (error) {
                this.logger.error('failed to add application', {
                  errorMessage: (error as Error).message,
                  applicationId: app.metadata.id,
                });
              }
            }),
          );
        }
        break;
      default:
        break;
    }
  }
  private onApplicationManagementEvents({
    type,
    payload,
  }: Events.RuntimeToOsMessage[VIM_OS_EVENTS.APPLICATION_MANAGEMENT_EVENTS]) {
    switch (type) {
      case Events.ApplicationManagementEvents.InjectApplication: {
        this.store.dispatch(
          actions.addApplication({
            ...payload,
            injectSource: Events.ApplicationManagementEvents.InjectApplication,
          }),
        );
        break;
      }
      case Events.ApplicationManagementEvents.ModifyApplication: {
        if (payload.modifications?.notifications !== undefined) {
          payload.modifications = this.convertNotificationsBeforeModifyApp(payload.modifications);
        }
        this.store.dispatch(
          actions.modifyApplication({
            id: payload.id,
            modifiedApplication: payload.modifications,
          }),
        );
        break;
      }
      case Events.ApplicationManagementEvents.SelectApplication: {
        const { expandingType = UI.ExpandingType.VIM_APP_MANUAL, id } = payload;
        return this.requestToSelectApplication({ appId: id, expandingType });
      }
      case Events.ApplicationManagementEvents.RemoveApplication: {
        this.store.dispatch(actions.removeApplication(payload));
        break;
      }
    }
  }

  private didAppReachLimit(appId: string, appLimiter: AppLimiter) {
    let limiter: RateLimiter;
    if (appLimiter.has(appId)) {
      limiter = appLimiter.get(appId)!;
    } else {
      limiter = new RateLimiter({ tokensPerInterval: 10, interval: 'minute' });
      appLimiter.set(appId, limiter);
    }

    return !limiter.tryRemoveTokens(1);
  }

  private async initPopupHistory() {
    const popupHistory = await this.storage.get<PopupHistory | undefined>(
      POPUP_HISTORY_STORAGE_KEY,
    );
    this.store.dispatch(actions.setPopupHistory(popupHistory || {}));
  }

  private getPatientInContext(): string | undefined {
    return this.getState().ehrState.patient?.patientId;
  }

  private getPopupHistory() {
    return this.getState().applicationsState.popupHistory;
  }

  private getPatientLastPopup(patientId: string): string | undefined {
    return this.getPopupHistory()[patientId]?.lastPopupDate;
  }

  public async askToPopup(appId: string) {
    const popupAllowed = this.getManifest(appId)?.UI.capabilities.autoPopup;
    if (!popupAllowed) {
      this.logger.info('Application requested to popup but is not allowed', { appId });
      this.telLogger.alert(
        new UnauthorizedError('Application requested to popup but is not allowed'),
      );
      return;
    }
    return await this.requestToSelectApplication({
      appId,
      expandingType: UI.ExpandingType.AUTOMATIC,
    });
  }

  private async enableAppOnAutoPopup(appId: string) {
    return await this.ffClient.getFlag({
      flagName: 'enable_app_on_auto_popup',
      flagContext: {
        appId,
      },
      defaultValue: false,
    });
  }

  public async requestToSelectApplication({ appId, expandingType }: SelectApplicationPayload) {
    const { selectedAppId } = this.getState().applicationsState.selectedApplication;
    const application = this.getApplication(appId);
    if (appId === selectedAppId) {
      this.logger.info(`Select application prevented because it's already selected`, {
        appId,
        expandingType,
      });
      return;
    }
    if (application?.disabled && !(await this.enableAppOnAutoPopup(appId))) {
      this.logger.info(`Select application prevented because it's disabled`, {
        appId,
        expandingType,
      });
      return;
    }

    if (expandingType == UI.ExpandingType.AUTOMATIC && !(await this.canAppAutoPopup(appId))) {
      this.logger.info(
        `Application requested to be opened automatically and was blocked by limit / organization / user configuration`,
        {
          appId,
        },
      );
      return;
    }
    if (expandingType !== UI.ExpandingType.AUTOMATIC) {
      return await this.selectApp(appId, expandingType);
    }
    if (this.appPendingPopup) {
      const thisAppPriority = application?.autoPopPriority ?? 0;
      const pendingAppPriority = this.getApplication(this.appPendingPopup)?.autoPopPriority ?? 0;
      if (pendingAppPriority >= thisAppPriority) {
        this.logger.info('Application auto popup blocked because of pending app', {
          blockedApp: appId,
          pendingApp: this.appPendingPopup,
        });
        return;
      } else {
        this.logger.info(
          'Application auto popup canceled because of new incoming app with higher priority',
          {
            newApp: appId,
            canceledApp: this.appPendingPopup,
          },
        );
      }
    }
    this.appPendingPopup = appId;
    return await this.delaySelectApp(appId, expandingType);
  }

  public async clearSelectedApplication() {
    this.store.dispatch(actions.clearSelectedApplication());
  }

  private async selectApp(appId: string, expandingType: UI.ExpandingType) {
    this.appPendingPopup = undefined;
    const { selectedAppId } = this.getState().applicationsState.selectedApplication;
    if (expandingType === UI.ExpandingType.AUTOMATIC && selectedAppId !== undefined) {
      this.logger.info(
        `Auto Select application prevented because another app is already selected`,
        {
          appId,
          selectedAppId,
        },
      );
      return;
    }
    const patientInContext = this.getPatientInContext();
    if (expandingType === UI.ExpandingType.AUTOMATIC && patientInContext) {
      this.store.dispatch(actions.updatePopupHistory({ patientId: patientInContext }));
    }
    const popupHistory = this.getPopupHistory();
    await this.storage.set(POPUP_HISTORY_STORAGE_KEY, popupHistory);
    this.store.dispatch(
      actions.selectApplication({
        selectedAppId: appId,
        expandingType,
      }),
    );
  }

  private delaySelectApp = debounce(this.selectApp, AUTO_POPUP_DELAY);

  public async canAppAutoPopup(appId: string) {
    if (!this.getState().userState.organization?.isPopupsEnabled) {
      return false;
    }

    const vimUserEmail = this.getState().userState.user?.email;
    const linkedEhrUser = this.getState().userState.user?.linkedEhrUser;

    const shouldDisableUserAutoPopup: boolean =
      (!!vimUserEmail || !!linkedEhrUser) &&
      (await this.ffClient.getFlag({
        flagName: 'disable_user_auto_popup',
        flagContext: {
          vimUserEmail,
          linkedEhrUser,
        },
        defaultValue: false,
      }));

    if (shouldDisableUserAutoPopup === true) {
      return false;
    }

    const { overridePopupLimit } = this.getManifest(appId)?.UI.capabilities || {};
    const patientInContext = this.getPatientInContext();
    const patientLastPopup = patientInContext && this.getPatientLastPopup(patientInContext);
    if (patientInContext) {
      if (overridePopupLimit || unlimitedPopupApps.has(appId)) {
        return true;
      }
      if (patientLastPopup && patientLastPopup >= getTodayDate()) {
        return false;
      }
    }
    return true;
  }

  public async executeAction(appId: string, payload: ExecuteActionPayload, span?: VimSpan) {
    const actionAllowed = this.getManifest(appId)?.EHR.actions?.[payload.action];

    if (!actionAllowed) {
      const errorMessage = `Application ${appId} is not allowed to perform action ${payload.action}, please update the application manifest & get it approved.`;
      this.logger.error(errorMessage, {
        appId,
        storeManifest: this.getManifest(appId),
        action: payload.action,
      });
      throw new UnauthorizedError(errorMessage);
    }

    const result = await this.adapterManager.actionManager.executeActionPromised(
      payload.action,
      payload.input,
      span,
    );

    return await this.enhanceActionResult(payload.action, result, span);
  }

  private async enhanceActionResult(
    action: ActionNames,
    result: unknown,
    span: VimSpan | undefined,
  ) {
    switch (action) {
      case ActionNames.GET_PATIENT: {
        const patient = result as EnrichedPatient;
        const promise = this.tokenizer.generateEntitiesTokens({ patient });
        const tokenized = await (span
          ? span?.startAutoChildSpan('generate get patient token', {}, () => promise)
          : promise);
        return {
          ...patient,
          token: tokenized.patientPut,
        };
      }
      default:
        return result;
    }
  }

  public async executeEHRResourceEnhancement(
    appId: string,
    payload: ExecuteResourceEnhancementPayload,
    span?: VimSpan,
  ) {
    if (!this.isEHRResourceEnhancementAllowed(appId, payload)) {
      throw new UnauthorizedError(
        `Application ${appId} is not allowed to perform enhancement ${payload.enhancement}, please update the application manifest & get it approved.`,
      );
    }

    if (this.didAppReachLimit(appId, this.appActionsLimiter)) {
      throw new RateLimitError(
        "Reached enhancements quota, You're not allowed to execute more than 10 enhancements in a minute, please try again later.",
      );
    }
    return await this.adapterManager.resourceEnhancementsManager.executeEhrResourceEnhancement(
      payload,
      span,
    );
  }

  private shouldValidateInVimOS(appId: string) {
    return this.ffClient.getFlag({
      flagName: 'validate_writeback_in_vimos',
      flagContext: {
        appId,
      },
      defaultValue: false,
    });
  }

  public async updateResource(appId: string, payload: UpdateResourcePayload, span?: VimSpan) {
    const { resource, dataToUpdate: originalDataToUpdate, dataToAdd: originalDataToAdd } = payload;

    const updatableFields = this.getState().ehrState.updatableFields;
    const manifest = this.getManifest(appId);
    const application = this.getApplication(appId);
    let error;
    const { dataToUpdate, dataToAdd } = (await this.shouldValidateInVimOS(appId))
      ? this.resourcePermissionsService.filterWriteEnabledFieldsInEHR(
          updatableFields?.[resource],
          payload,
        )
      : payload;
    if (!isEqual(originalDataToUpdate, dataToUpdate) || !isEqual(originalDataToAdd, dataToAdd)) {
      this.logger.warning('Application tried to update fields that are not currently updatable', {
        appId,
        resource,
        originalDataToUpdate,
        originalDataToAdd,
        dataToUpdate,
        dataToAdd,
      });
    }
    if (isEmpty(dataToUpdate) && isEmpty(dataToAdd)) {
      this.logger.warning("Application couldn't update anything due to lack of EHR support", {
        appId,
        payload,
        updatableFields,
      });
      error = new InvalidInputError(
        `The update of ${resource} failed due to preconditions. The EHR system does not support the fields you are trying to update.`,
      );
    } else if (
      !this.resourcePermissionsService.isWriteAllowedForApp(
        manifest,
        resource,
        dataToUpdate,
        dataToAdd,
      )
    ) {
      const message = `Application ${appId} is not allowed to update some/all of the ${resource} resource; please update the application manifest & get it approved.`;
      this.logger.warning(message, {
        appId,
        resource,
        resources: manifest?.EHR?.resources,
        resourceUpdates: manifest?.EHR?.resourceUpdates,
        resourcePermissions: manifest?.EHR?.resourcePermissions,
      });
      error = new UnauthorizedError(
        `The update of ${resource} failed due to insufficient permissions. Your application does not have the correct write permissions.`,
      );
    } else if (this.didAppReachLimit(appId, this.appResourceUpdateLimiter)) {
      error = new RateLimitError(
        "Reached actions quota, You're not allowed to execute more than 10 actions in a minute, please try again later.",
      );
    }
    if (error) {
      this.analyticsManager.trackWriteback({
        resource,
        writebackData: { ...dataToAdd, ...dataToUpdate },
        appId,
        application,
        status: WritebackStatus.FAILED,
      });
      throw error;
    } else
      return await this.adapterManager.resourceUpdateManager.updateResource(
        resource,
        dataToUpdate ?? {},
        dataToAdd ?? {},
        appId,
        span,
      );
  }

  public async copyToClipboard(payload: string) {
    return await this.bareboneManager.commonConnectors.copyToClipboard(payload);
  }

  public getManifestSupport(appId: string): ApplicationManifest | undefined {
    const applicationManifest = this.getManifest(appId);
    const adapterManifestSupport = this.adapterManager.getManifestSupport();

    if (!applicationManifest) {
      return undefined;
    }
    const applicationManifestSupport: ApplicationManifest = {
      ...applicationManifest,
      EHR: {
        ...applicationManifest?.EHR,
        actions: Object.fromEntries(
          Object.entries({ ...adapterManifestSupport.EHR.actions }).filter(
            ([action, supported]) => {
              return supported && (applicationManifest?.EHR.actions ?? {})[action];
            },
          ),
        ),
      },
    };

    return applicationManifestSupport;
  }

  private isEHRResourceEnhancementAllowed(
    appId: string,
    payload: ExecuteResourceEnhancementPayload,
  ): boolean {
    if (
      !Object.values(EHRResourceEnhancements[payload.resource] ?? {}).includes(
        payload.enhancement as any,
      )
    ) {
      return false;
    }

    const ehrSection = this.getManifest(appId)?.EHR;

    if (
      !ehrSection?.resourcePermissions &&
      !(ehrSection?.resources || ehrSection?.resourceUpdates)
    ) {
      return false;
    }

    if (ehrSection?.resourcePermissions) {
      if (
        payload?.resource === EHRResource.patient &&
        payload.enhancement === EHRResourceEnhancements.patient.GET_PROBLEM_LIST
      ) {
        return !!ehrSection?.resourcePermissions?.[EHRResource.patient]?.problemList?.read;
      } else {
        throw new Error(
          `ResourceEnhancement ${payload.resource} ${payload.enhancement} not supported!`,
        );
      }
    } else {
      return !!ehrSection?.resources?.[payload.resource];
    }
  }

  public informAdapterOnRequiredResources() {
    const appStoreApps = Object.values(this.store.getState().applicationsState.applications).filter(
      (app) => app.manifest !== undefined,
    ) as AppStoreApplication[];

    const requestedResources = appStoreApps.reduce((acc, app) => {
      Object.entries(app.manifest!.EHR.resourcePermissions ?? {}).forEach(
        ([resourceKey, resourcePermission]) => {
          if (!acc[resourceKey]) {
            acc[resourceKey] = {};
          }
          Object.entries(resourcePermission).forEach(([groupKey, groupPermissions]) => {
            if (groupPermissions.read) {
              acc[resourceKey][groupKey] = {
                ...(acc[resourceKey][groupKey] ?? {}),
                read: true,
              };
            }
            if (groupPermissions.write) {
              acc[resourceKey][groupKey] = {
                ...(acc[resourceKey][groupKey] ?? {}),
                write: true,
              };
            }
          });
        },
      );
      return acc;
    }, {} as EntitiesRequest);

    this.adapterManager.setActiveResources(requestedResources);
  }
}
