import { OsState } from '@/store';
import { EHRResourceEnhancements } from '@getvim-os/types';
import { ActionNames, PatientAction } from '@getvim-os/types/actions';
import { inject, injectable } from 'inversify';
import { ActionsManager } from './actionsManager';
import { VimSpan } from '@getvim-os/telemetry';
import { InvalidInputError } from '@getvim-os/errors';
import { ExecuteResourceEnhancementPayload } from '@getvim/vim-os-api';

@injectable()
export class ResourceEnhancementsManager {
  constructor(
    @inject(ActionsManager) private actionsManager: ActionsManager,
    @inject(OsState) private osState: OsState,
  ) {}

  public async executeEhrResourceEnhancement(
    payload: ExecuteResourceEnhancementPayload,
    span?: VimSpan,
  ) {
    if (payload.enhancement !== EHRResourceEnhancements.patient.GET_PROBLEM_LIST) {
      throw new InvalidInputError('Enhancement not supported yet');
    } else if (
      this.osState.store.getState().ehrState.patient?.token !== payload.resourceId &&
      this.osState.store.getState().ehrState.patient?.vimPatientId !== payload.resourceId
    ) {
      throw new InvalidInputError('Current patient is different than requested enhancement');
    } else {
      const patient = await this.actionsManager.executeActionPromised<
        PatientAction.Input,
        PatientAction.Response
      >(
        ActionNames.GET_PATIENT,
        {
          patientId: this.osState.store.getState().ehrState.patient!.patientId,
          disableCache: true,
        },
        span,
      );

      return patient.problemList;
    }
  }
}
