import { Logger } from '@getvim-os/logger';
import { useLogger } from '@getvim-os/logger/hooks';
import React, { useEffect, useMemo, useState } from 'react';
import { useIsLoggedIn } from './store/user';
import { useInjection } from 'inversify-react';
import { BareboneManager } from '@/barebone-manager';
import { VimTelemetryLogsProvider, VimTelemetryLogger } from '@getvim-os/telemetry';

export const useWidgetTransparency = () => {
  const bareboneManager = useInjection(BareboneManager);
  const [pointerEventsState, setPointerEvent] = useState<'none' | 'initial'>('none');

  useEffect(() => {
    function onmousemove(clientX, clientY) {
      if (bareboneManager.shouldUseClipPath) {
        return;
      }
      const element = document.elementFromPoint(clientX, clientY);
      const newState =
        !element ||
        ['BODY', 'HTML'].includes(element.tagName) ||
        ['theme-css-variables-wrapper', 'root'].includes(element.id) ||
        element.classList.contains('main-container')
          ? 'none'
          : 'initial';

      setPointerEvent(newState);
    }

    if (!bareboneManager.shouldUseClipPath) {
      window.addEventListener('message', (e) => {
        if (e.data.type === 'host-mousemove') {
          const { clientX, clientY } = e.data.payload;
          onmousemove(clientX, clientY);
        }
      });

      document.body.addEventListener('mousemove', (e) => {
        const { clientX, clientY } = e;
        onmousemove(clientX, clientY);
      });
    }
  }, [bareboneManager.shouldUseClipPath]);

  const isLoggedIn = useIsLoggedIn();

  useEffect(() => {
    if (isLoggedIn && !bareboneManager.shouldUseClipPath) {
      const message = {
        type: 'change-pointer-events',
        payload: {
          state: pointerEventsState,
        },
      };

      bareboneManager.messaging.sendRawMessage(message);
    }
  }, [
    bareboneManager.messaging,
    bareboneManager.shouldUseClipPath,
    isLoggedIn,
    pointerEventsState,
  ]);
};

class WidgetTransparencyErrorBoundary extends React.Component<
  React.PropsWithChildren<{
    logger: Logger;
    bareboneManager: BareboneManager;
    otelLogger: VimTelemetryLogger;
  }>
> {
  componentDidCatch(error) {
    let errorMessage: string;
    if (this.props.bareboneManager.shouldUseClipPath) {
      this.props.bareboneManager.messaging.sendRawMessage({
        type: 'change-clip-path',
        payload: {
          state: 'path("M 0 0 Z")',
        },
      });
      errorMessage = 'Fullscreen app had a render error, sent empty clip-box none to barebone';
    } else {
      this.props.bareboneManager.messaging.sendRawMessage({
        type: 'change-pointer-events',
        payload: {
          state: 'none',
        },
      });
      errorMessage = 'Fullscreen app had a render error, sent pointer events none to barebone';
    }
    this.props.logger.error(errorMessage, error);
    this.props.otelLogger.alert(error, {
      message: errorMessage,
    });
  }

  render() {
    const { children } = this.props;
    return children;
  }
}

export const WidgetTransparencyProvider: React.FC<React.PropsWithChildren<unknown>> = ({
  children,
}) => {
  const logger = useLogger('widget-transparency-provider');
  const bareboneManager = useInjection(BareboneManager);
  const otelLoggerProvider = useInjection(VimTelemetryLogsProvider);
  const otelLogger = useMemo(() => {
    return otelLoggerProvider.getLogger('widget-transparency-provider');
  }, [otelLoggerProvider]);

  return (
    <WidgetTransparencyErrorBoundary
      bareboneManager={bareboneManager}
      logger={logger}
      otelLogger={otelLogger}
    >
      {children}
    </WidgetTransparencyErrorBoundary>
  );
};
