import React, { useCallback, useMemo } from 'react';
import classNames from 'classnames';
import { ApplicationSvgUrlIcon, ClickableDiv, Tooltip } from '@getvim/atomic-ui';
import { IconsMapping, SVGIconProps } from '@getvim/vim-icons';
import { NotificationsIndicator } from '../../atoms';
import './index.less';
import { UI } from '@deprecated/types';
import { useTheme } from '@getvim/components-hooks-use-theme';

interface AppMenuIconProps {
  onClick?: (id: string) => void;
  iconSource?: React.FC<SVGIconProps>;
  iconName?: string;
  iconUrl?: string;
  id: string;
  loading?: boolean;
  disabled?: boolean;
  whiteBackground?: boolean;
  notifications?: number;
  ignoreNotificationsValue?: boolean;
  tooltip: string;
  hubPosition: UI.HubPosition;
  selected?: boolean;
  defaultIconColor?: string;
  clearNotificationsIndicator?: boolean;
}

const MAX_TOOLTIP_LENGTH = 50;

const changeableIconApplications = new Set(['eligibility']);

const getApplicationIcon = (applicationId: string, disabled?: boolean) => {
  if (changeableIconApplications.has(applicationId) && disabled !== undefined) {
    const disabledText = disabled ? 'disabled' : 'active';
    return IconsMapping[`${applicationId}-${disabledText}`];
  }
  return IconsMapping[applicationId];
};

const isDevPath = ['dev', 'staging'].includes(window.$vim_environment?.APP_ENV);

export const defaultAppIcon = `https://static${isDevPath ? '.dev' : ''}.getvim.com/${
  isDevPath ? 'dev' : 'prod'
}/vim-os-appstore/apps/icons/appDefaultIcon.svg`;

export const defaultAppIconAlt = 'appDefaultIcon';

export const AppMenuIcon = React.forwardRef<HTMLDivElement, AppMenuIconProps>(
  (
    {
      onClick,
      id,
      iconSource,
      iconName,
      iconUrl,
      loading,
      disabled,
      whiteBackground,
      notifications,
      tooltip,
      hubPosition,
      selected = false,
      defaultIconColor = 'var(--app-icon-color)',
      clearNotificationsIndicator,
    },
    toolTipRef,
  ) => {
    const theme = useTheme();
    const themeStyle = useMemo(() => {
      if (selected) {
        return theme.appIconSelectedColor;
      }
      if (loading) {
        return theme.appIconColor;
      }
      if (disabled) {
        return theme.appIconDisabledColor;
      }
      return theme.appIconColor;
    }, [disabled, loading, selected, theme]);

    const getIconFillColor = () => {
      if (selected) return 'var(--app-icon-selected-color)';
      if (loading) return 'var(--app-icon-color)';
      if (disabled) return 'var(--app-icon-disabled-color)';
      return defaultIconColor;
    };

    const iconFillColor = getIconFillColor();

    const onClickWrapper = useCallback(() => {
      if (disabled) return;
      return onClick?.(id);
    }, [onClick, disabled, id]);

    const IconSource = useMemo(
      () => iconSource || getApplicationIcon(id, disabled),
      [disabled, iconSource, id],
    );

    const tooltipPlacement =
      hubPosition.horizontal === UI.HorizontalHubPosition.Left ? 'right' : 'left';

    const tooltipContent =
      tooltip && tooltip.length > MAX_TOOLTIP_LENGTH
        ? `${tooltip.slice(0, MAX_TOOLTIP_LENGTH)}...`
        : tooltip;

    const iconClasses = classNames('icon', { loading: !selected && loading });

    return (
      <Tooltip
        pillStyle
        hideArrow
        bgColor="buttonColor"
        placement={tooltipPlacement}
        tooltipContent={tooltipContent}
        ref={toolTipRef}
        className="app-menu-icon-tooltip"
      >
        <ClickableDiv
          className={classNames(
            'app-menu-icon-wrap',
            `app_id_${id}`,
            { selected },
            { disabled },
            { loading },
            { white: whiteBackground },
          )}
          onClick={onClickWrapper}
        >
          {notifications !== undefined && !clearNotificationsIndicator ? (
            <NotificationsIndicator value={notifications} />
          ) : null}
          {iconUrl || !IconSource ? (
            <ApplicationSvgUrlIcon
              iconUrl={iconUrl}
              alt={iconName ?? id}
              fillColor={themeStyle}
              className={iconClasses}
            />
          ) : (
            <IconSource className={iconClasses} fill={iconFillColor} />
          )}
        </ClickableDiv>
      </Tooltip>
    );
  },
);

AppMenuIcon.displayName = 'AppMenuIcon';

export default AppMenuIcon;
