import { WebConnectors } from '../../..';
import { WebConnectorMessageTypes } from '../../enum';
import { RemoveXhrListenerInput } from '../../types/removeXhrListenerInput';

export async function removeXhrListener(
  this: WebConnectors,
  { listenerId }: RemoveXhrListenerInput,
) {
  return await this.messaging.sendMessage({
    type: WebConnectorMessageTypes.removeXhrListener,
    payload: { listenerId },
  });
}
