import { shallowEqual } from 'react-redux';
import { useAppSelector } from '../hooks';

export const useSelectedApplication = () => {
  return useAppSelector((state) => state.applicationsState.selectedApplication, shallowEqual);
};

export const useSelectedApplicationObject = () => {
  return useAppSelector(
    (state) =>
      (state.applicationsState.selectedApplication.selectedAppId ?? '') in
      state.applicationsState.applications
        ? state.applicationsState.applications[
            state.applicationsState.selectedApplication.selectedAppId!
          ]
        : undefined,
    shallowEqual,
  );
};
