import { createAsyncThunk } from '@reduxjs/toolkit';
import { Container } from 'inversify';
import { RootState, ThunkExtra } from '..';
import { ApplicationAuthManager } from '@/application-auth-manager';
import { CreateAppLaunchInput } from '@/internal-api/application-auth/types';

export const resetAppForRerender = createAsyncThunk<
  void,
  CreateAppLaunchInput & { oAuthMode?: boolean; retry?: boolean; retryReason?: string },
  { state: RootState; extra: ThunkExtra }
>(
  'applications/resetAppForRerender',
  async (
    input: CreateAppLaunchInput & { oAuthMode?: boolean; retry?: boolean; retryReason?: string },
    api,
  ) => {
    const { oAuthMode, appId, version, tokenEndpoint } = input;
    if (oAuthMode) {
      await api.dispatch(createAppLaunchId({ appId, version, tokenEndpoint }));
    }
  },
);

export const createAppLaunchId = createAsyncThunk<
  { launchId: string },
  CreateAppLaunchInput,
  {
    state: RootState;
    extra: ThunkExtra;
  }
>('applications/createLaunchId', async (input: CreateAppLaunchInput, api) => {
  const container = api.extra.container as Container;
  const authManager = container.get(ApplicationAuthManager);

  return await authManager.createAppLaunchId(input);
});
