import { inject, injectable } from 'inversify';
import { OsState } from '../store';
import { WriteBackPayload } from '../deprecated/types/events';
import { WriteBackEvent } from './types/events';
import { InternalApi } from '../internal-api';
import { AffectedEntity, ActingUser } from '../internal-api/writeback-audit-logger/types';
import { getAffectedFields } from '@/adapter-manager/utils';
interface WritebackAuditLogger {
  logAndSendAudit(eventData: object);
}
@injectable()
export class VimOsWritebackAuditLogger implements WritebackAuditLogger {
  constructor(
    @inject(OsState) private osState: OsState,
    @inject(InternalApi) private internalApi: InternalApi,
  ) {}

  private getAffectedProblemList(writeback: WriteBackPayload): AffectedEntity {
    const affectedFields: Array<string> = getAffectedFields(writeback.data);

    const state = this.osState.store.getState();
    return {
      id: {
        technicalId: state.ehrState.patient?.vimPatientId,
      },
      organization: state.userState.organization?.id
        ? state.userState.organization?.id.toString()
        : undefined,
      type: 'Patient',
      affectedFields: affectedFields,
    };
  }
  private getAffectedEncounter(writeback: WriteBackPayload): AffectedEntity {
    const affectedFields: Array<string> = getAffectedFields(writeback.data);

    const state = this.osState.store.getState();
    return {
      id: {
        technicalId: state.ehrState.encounter?.encounterId,
      },
      organization: state.userState.organization?.id
        ? state.userState.organization?.id.toString()
        : undefined,
      type: 'Encounter',
      affectedFields: affectedFields,
    };
  }

  private getAffectedReferral(writeback: WriteBackPayload): AffectedEntity {
    const affectedFields: Array<string> = getAffectedFields(writeback.data);

    const state = this.osState.store.getState();
    return {
      id: {
        technicalId: state.ehrState.referral?.referralId,
      },
      organization: state.userState.organization?.id
        ? state.userState.organization?.id.toString()
        : undefined,
      type: 'Referral',
      affectedFields: affectedFields,
    };
  }

  private getAffectedEntity(writeback: WriteBackPayload): AffectedEntity {
    switch (writeback.writeBack) {
      case 'updateReferral':
        return this.getAffectedReferral(writeback);
      case 'updateEncounter':
        return this.getAffectedEncounter(writeback);
      case 'updateProblemList':
        return this.getAffectedProblemList(writeback);
    }
  }

  private getActingUser(): ActingUser {
    const state = this.osState.store.getState();

    return {
      id: {
        technicalId: state.userState.user?.externalId,
        readableId: state.userState.user?.linkedEhrUser || state.userState.user?.email,
      },
      organization: state.userState.organization?.id
        ? state.userState.organization?.id.toString()
        : undefined,
    };
  }

  private validateAffectedEntity(affectedEntity: AffectedEntity): boolean {
    if (!affectedEntity.id.technicalId) {
      console.warn('Cannot send audit log, missing technical id', affectedEntity);
      return false;
    }
    return true;
  }

  async logAndSendAudit(eventData: WriteBackEvent) {
    const { error, writeBackPayload, appName } = eventData;
    const affectedEntity = this.getAffectedEntity(writeBackPayload);
    if (!this.validateAffectedEntity(affectedEntity)) {
      return;
    }

    const actingUser = this.getActingUser();
    const payload = {
      actionResult: error ? 'failed' : 'success',
      requester: appName,
      actingUser: actingUser,
      affectedEntity: affectedEntity,
      actionName: `Writeback-${writeBackPayload.writeBack}`,
      appName: 'vim-os',
    };
    await this.internalApi.writebackAuditLogger.log(payload);
  }
}
