import { WebConnectors } from '../../..';
import { WebConnectorMessageTypes } from '../../enum';
import { RemoveListenerInput } from './input';

export async function removeWebRequestListener(
  this: WebConnectors,
  { listenerId }: RemoveListenerInput,
) {
  return await this.messaging.sendMessage({
    type: WebConnectorMessageTypes.removeWebRequestListener,
    payload: { listenerId },
  });
}
