import { gql } from '@getvim-os/gql-client';

export const CREATE_APP_LAUNCH = gql`
  mutation CreateAppLaunch($input: CreateAppLaunchInput) {
    createAppLaunch(input: $input) {
      launchId
    }
  }
`;

export const VERIFY_APP_TOKEN = gql`
  mutation VerifyAppToken($input: VerifyAppTokenInput) {
    verifyAppToken(input: $input) {
      isValid
    }
  }
`;
