import { StandardError } from '@getvim-os/errors';

const WINDOW_NOT_FOUND_ERROR_TEXT = 'Window not found';
const PROCESS_NOT_FOUND_ERROR_TEXT = 'Process not found';

export class ProcessNotFoundError extends StandardError {
  constructor(message?: string) {
    if (message) {
      super(message);
    } else super(PROCESS_NOT_FOUND_ERROR_TEXT);
  }
}
export class WindowNotFoundError extends ProcessNotFoundError {
  constructor(message?: string) {
    if (message) {
      super(message);
    } else super(WINDOW_NOT_FOUND_ERROR_TEXT);
  }
}
