import { inject, injectable } from 'inversify';

import { DesktopConnectorMessageTypes } from './enum';

import { CommonConnectors } from '../common';
import { StorageKnownKey } from '../../storage';
import { WindowCriteriaMapping } from './types';
import {
  InjectDllToProcessElectronResponse,
  InjectDllToProcessInput,
} from './types/injectDllToProcess';
import { ProcessNotFoundError, WindowNotFoundError } from './types/errors';
import { WindowsAgent } from './windows-agent';
import { BareboneMessaging } from '../../infra/messaging';
import { v4 as uuid } from 'uuid';
import { NodeFetchParams } from './types/nodeFetchParams';
import { NodeFetchBase64Response, NodeFetchResponse } from './types/nodeFetchResponse';
const lastThreeDigitVersion = [1, 1, 122];

@injectable()
export class DesktopConnectors extends CommonConnectors {
  constructor(
    @inject(WindowsAgent) public windowsAgent: WindowsAgent,
    @inject(BareboneMessaging) protected messaging: BareboneMessaging,
  ) {
    super(messaging);
  }

  private isVersionLargerOrEqual(versionA: number[], versionB: number[]): boolean {
    if (versionB.length > versionA.length)
      throw new Error('Invalid input, versionB is longer than versionA');

    // check is larger with significance to digit location (e.g. 1.1.1 is larger than 1.1.0, but not larger than 2.0.0)
    for (const [i, element] of versionB.entries()) {
      if (versionA[i] !== element) return versionA[i] > element!;
    }

    return true;
  }

  public async msiVersion(): Promise<string | undefined> {
    const msiVersion = await this.messaging.sendMessage<string | undefined>({
      type: DesktopConnectorMessageTypes.getEnvironmentVariable,
      payload: {
        key: StorageKnownKey.MSI_VERSION,
      },
    });

    if (!msiVersion) {
      return undefined;
    }

    let detectedVersion = msiVersion.split('.').map((x) => Number.parseInt(x, 10));
    const isFourDigitVersion = detectedVersion.length === 4;
    // In versions prior to 1.1.132, we only had three digits, and in some of those versions, we had a bug where the number in current-version is incorrect
    // In this case, if the current version only has 3 digits, we will assume it is at most 1.1.122
    if (
      !isFourDigitVersion &&
      this.isVersionLargerOrEqual(detectedVersion, lastThreeDigitVersion)
    ) {
      detectedVersion = lastThreeDigitVersion;
    }
    return detectedVersion.join('.');
  }

  // region: DMR

  public async listenToDataFromEhr({ handler }: { handler: (data) => void }) {
    await this.messaging.sendMessageWithHandler({
      type: 'listenToDataFromEhr',
      handler,
    });
  }

  // region: WindowsAgent
  public async listenToWindows(payload: WindowCriteriaMapping) {
    return await this.messaging.sendAsyncMessage({
      type: 'listenToWindows',
      payload,
    });
  }

  async doesProcessExists(pid: number): Promise<boolean> {
    try {
      return await this.messaging.sendMessage<boolean>({
        type: 'isProcessAlive',
        payload: { pid },
      });
    } catch (error) {
      return this.wrappedErrorIfIsOfTypeWinNotFound(error as string);
    }
  }

  // use Desktop.InjectDLL from @getvim/vim-connect-adapter to access this function, protects from invalid msi versions
  async injectDllToProcessElectorn(
    payload: InjectDllToProcessInput,
  ): Promise<InjectDllToProcessElectronResponse> {
    return await this.messaging.sendMessage({
      type: 'injectDLL',
      payload,
    });
  }

  public async getEhrVersion(): Promise<string | undefined> {
    return await this.messaging.sendMessage<string>({
      type: 'getEhrVersion',
      timeout: 7500, // make a quick timeout to avoid mismatch between DMR version and adapter version. Quick win, not a long term solution.
    });
  }

  public async listenToDataFromIpc({
    channel,
    listenerIdOverride,
    encoding,
    rawBuffer,
    useDuplex,
    handler,
  }: {
    channel: string;
    listenerIdOverride?: string;
    encoding?: string;
    rawBuffer?: boolean;
    useDuplex?: boolean;
    handler: (data) => void;
  }) {
    const listenerId = listenerIdOverride ?? uuid();
    await this.messaging.sendMessageWithHandler({
      type: 'listenToDataFromIpc',
      payload: { channel, listenerId: listenerId, encoding, rawBuffer, useDuplex },
      handler,
    });
    return listenerId;
  }

  private wrappedErrorIfIsOfTypeWinNotFound = (err: any): never => {
    if (typeof err === 'string') {
      if (
        (err.startsWith('Process with PID=') && err.endsWith(' not found!')) ||
        err.includes('pywinauto.application.ProcessNotFoundError')
      ) {
        throw new ProcessNotFoundError();
      } else if (
        err.includes('Invalid handle') ||
        err.includes('is not a valid window handle') ||
        err.includes('is not a vaild window handle') ||
        err.includes('An event was unable to invoke any of the subscribers') ||
        err.includes("Couldn't find a valid process for given window handle")
      ) {
        throw new WindowNotFoundError();
      }
    }
    throw err;
  };

  public async sendMessageToIpcAsync(payload: SendMessageToIpcType) {
    // always use DesktopConnector.versionSupportsNewAntiProxyDll() when using this
    return await this.messaging.sendMessage({
      type: 'sendMessageToIpcAsync',
      payload,
    });
  }

  public async getEhrProcessPath(): Promise<string | undefined> {
    return await this.messaging.sendMessage<string>({
      type: 'getEhrProcessPath',
      timeout: 2000, // make a quick timeout to avoid mismatch between DMR version and adapter version. Quick win, not a long term solution.
    });
  }

  public async getPid(): Promise<string> {
    return await this.messaging.sendMessage({
      type: 'getActiveWindow',
    });
  }

  public async readHostFile(payload: ReadFileParameters): Promise<string> {
    return await this.messaging.sendMessage({
      type: 'readHostFile',
      payload,
    });
  }

  public async getFileInfoByPID(pid: string): Promise<GetFileInfoByPIDResponse> {
    return await this.messaging.sendMessage({
      type: 'getFileVersionByPID',
      payload: { pid },
      timeout: 60_000, // 60 seconds -  sometimes timeout around this specific function in prod
    });
  }

  public async fetchThroughNode(payload: NodeFetchParams): Promise<NodeFetchResponse> {
    return await this.messaging.sendMessage({
      type: 'fetchThroughNode',
      payload: {
        resultType: 'text',
        ...payload,
      },
    });
  }

  // available from Sept 11 2023, version 1.65.0 DesktopConnector.versionSupportsBase64NodeFetch
  public async fetchBase64ThroughNode(payload: NodeFetchParams): Promise<NodeFetchBase64Response> {
    return await this.messaging.sendMessage({
      type: 'fetchThroughNode',
      payload: {
        resultType: 'base64',
        ...payload,
      },
    });
  }
}

interface SendMessageToIpcType {
  channel: string;
  messageToSend: any;
}

export interface GetFileInfoByPIDResponse {
  fileVersion: string;
  fileName: string;
}

export interface SendMessageToAntiProxyType {
  messageToSend: any;
}

export type ReadFileParameters = {
  path: string;
  encoding?: BufferEncoding | undefined | null;
};
