import { PromiseTimeoutError } from '@getvim-os/errors';

const rejectOnTimeout = <T>(ms: number) =>
  new Promise<T>((_, reject) =>
    setTimeout(() => reject(new PromiseTimeoutError(`Timed out after ${ms / 1000} seconds!`)), ms),
  );

export function wrapMethodWithTimeout<T, ARGS>(func: (...args: ARGS[]) => Promise<T>, ms: number) {
  return (...args: ARGS[]): Promise<T> => {
    return Promise.race<T>([rejectOnTimeout<T>(ms), func(...args)]);
  };
}
