import { inject, injectable } from 'inversify';
import { compareVersions } from 'compare-versions';
import { WebConnectors } from '../../connectors/web';

@injectable()
export class WebVersioning {
  public extensionVersion: string | null = null;

  constructor(@inject(WebConnectors) private webConnector: WebConnectors) {}

  public async initialize(): Promise<void> {
    const extensionVersion = await this.webConnector.extensionVersion();

    this.extensionVersion = extensionVersion;
  }

  public isExtensionVersionGreaterOrEqual = (
    minStandardExtensionVersion: string,
    minNativeExtensionVersion: string,
  ) => {
    if (this.extensionVersion === null) {
      throw new Error('Extension version is not initialized yet...');
    }
    return (
      this.extensionVersion.startsWith('0') /* Test extension is in this version */ ||
      (this.extensionVersion.startsWith('2') &&
        compareVersions(this.extensionVersion, minStandardExtensionVersion) >= 0) ||
      (this.extensionVersion.startsWith('1') &&
        compareVersions(this.extensionVersion, minNativeExtensionVersion) >= 0)
    );
  };
}
