import { Events } from '@deprecated/types';
import { UpdateReferralWritebackInput } from '@getvim-os/types';
import {
  UpdatableEhrState,
  UpdatableReferral,
  EHRResource,
  WRITEBACK_TIMEOUT,
} from '@getvim/vim-os-api';
import { inject, injectable } from 'inversify';
import { OsMessageChannelManager } from '../deprecated/runtime-wrapper/osMessageChannelManager';
import { VimOsWritebackAuditLogger } from './auditLogger';
import { RuntimeMessageHooks, VIM_OS_EVENTS } from '../deprecated/runtime-wrapper/messageHooks';
import { WriteBackEvents } from '@/deprecated/types/events';
import { AnalyticsManager } from '@/analytics-manager';
import { WritebackStatus } from '@/analytics-manager/events';
import { VimSpan } from '@getvim-os/telemetry';
import {
  InternalError,
  InvalidInputError,
  WritebackError,
  writebackErrorCodesEnum,
} from '@getvim-os/errors';
import { createPromisedChannel } from 'msg-channel-shake/core';
import { OsState } from '@/store';

type Payload =
  | {
      resource: typeof EHRResource.patient;
      dataToUpdate: UpdatableEhrState<typeof EHRResource.patient>;
    }
  | {
      resource: typeof EHRResource.referral;
      dataToUpdate: UpdatableEhrState<typeof EHRResource.referral>;
      dataToAdd: UpdatableEhrState<typeof EHRResource.referral>;
    }
  | {
      resource: typeof EHRResource.encounter;
      dataToUpdate: UpdatableEhrState<typeof EHRResource.encounter>;
      dataToAdd: UpdatableEhrState<typeof EHRResource.encounter>;
    }
  | { resource: typeof EHRResource.orders };

const convertUpdateToWriteBack = (payload: Payload): Events.WriteBackPayload | undefined => {
  const { resource } = payload;
  switch (resource) {
    case EHRResource.patient: {
      return {
        writeBack: 'updateProblemList',
        data: {
          conditionsToAdd: payload.dataToUpdate.conditionsToAdd || [],
          conditionsToDismiss: payload.dataToUpdate.conditionsToDismiss || [],
        },
        config: undefined,
      };
    }
    case EHRResource.encounter: {
      return {
        writeBack: 'updateEncounter',
        data: {
          addedDiagnoses: payload.dataToUpdate.assessments?.diagnosisCodes ?? [],
          addedProcedures: payload.dataToUpdate.plans?.procedureCodes ?? [],
          subjective: payload.dataToUpdate.subjective,
          objective: payload.dataToUpdate.objective,
          assessments: payload.dataToUpdate.assessments,
          plans: payload.dataToUpdate.plans,
          patientInstructions: payload.dataToUpdate.patientInstructions,
        },
        config: undefined,
      };
    }
    case EHRResource.referral: {
      const conflictingKeys = Object.keys(payload.dataToAdd).filter(
        (key) => key in payload.dataToUpdate,
      );
      if (conflictingKeys.length > 0) {
        throw new Error(
          `Error: using 'set' and 'append' for the same field "${conflictingKeys.join(
            ', ',
          )}" is not allowed`,
        );
      }
      const data = {
        ...payload.dataToUpdate,
        ...payload.dataToAdd,
      } as UpdateReferralWritebackInput;

      return {
        writeBack: 'updateReferral',
        data,
        config: {
          overrideEhrFields: Object.keys(payload.dataToUpdate) as (keyof UpdatableReferral)[],
        },
      };
    }
    default:
      return undefined;
  }
};

@injectable()
export class ResourceUpdateManager {
  private useNewAdapter = false;

  constructor(
    @inject(OsState) private osState: OsState,
    @inject(OsMessageChannelManager) private osMessageChannelManager: OsMessageChannelManager,
    @inject(VimOsWritebackAuditLogger) private osWritebackAuditLogger: VimOsWritebackAuditLogger,
    @inject(RuntimeMessageHooks) private messageHooks: RuntimeMessageHooks,
    @inject(AnalyticsManager) private analyticsManager: AnalyticsManager,
  ) {
    this.backwardsCompatabilityWritebackListener();
  }
  private backwardsCompatabilityWritebackListener() {
    this.messageHooks.addOSMessageHandler(VIM_OS_EVENTS.WRITEBACK_EVENT, async (event) => {
      if (event.type === WriteBackEvents.WRITE_BACK) {
        await this.osWritebackAuditLogger.logAndSendAudit(event.payload);
      }
    });
  }

  public setUseNewAdapter(shouldUseNewAdapter: boolean) {
    this.useNewAdapter = shouldUseNewAdapter;
  }

  public async updateResource<T extends EHRResource>(
    resource: T,
    dataToUpdate: UpdatableEhrState<T> = {},
    dataToAdd: UpdatableEhrState<T> = {},
    appId: string,
    span?: VimSpan,
  ) {
    performance.mark('write-back-start');
    if (this.useNewAdapter) {
      // TODO: implement resource update in new adapters
      throw new InternalError('New adapter not supported yet..');
    }

    const writeBack = convertUpdateToWriteBack({ resource, dataToUpdate, dataToAdd });
    if (!writeBack) {
      throw new InvalidInputError('dataToUpdate could not be converted to WriteBack');
    }

    const [portToForward, promise] = createPromisedChannel(WRITEBACK_TIMEOUT);

    this.osMessageChannelManager.sendMessageToRuntime(
      {
        type: Events.OsToRuntimeEvents.EXECUTE_WRITE_BACK,
        payload: {
          ...writeBack,
          config: { ...(writeBack.config ?? {}), displayLoadingWidget: true, appName: appId },
        },
      },
      [portToForward],
    );

    let hadError = false;

    try {
      const res = await (span
        ? span?.startAutoChildSpan('execute-write-back', {}, () => promise)
        : promise);
      if (res && typeof res === 'object' && Object.values(res).includes(false)) {
        throw new WritebackError('Some fields failed to update', {
          errorCode: writebackErrorCodesEnum.SOME_FIELDS_FAILED_WRITEBACK,
          error: 'Some fields failed to update',
          cause: { updateResult: res },
          noPHI: true,
        });
      }
      return res;
    } catch (error) {
      hadError = true;
      throw error;
    } finally {
      span?.startAutoChildSpan('audit writeback', {}, () =>
        this.osWritebackAuditLogger.logAndSendAudit({
          error: hadError,
          appName: appId,
          writeBackPayload: writeBack,
        }),
      );
      const application = this.osState.store.getState().applicationsState.applications[appId];
      this.analyticsManager.trackWriteback({
        resource,
        writebackData: writeBack.data,
        appId,
        application,
        status: hadError ? WritebackStatus.FAILED : WritebackStatus.SUCCESS,
        duration: Number.parseInt(
          performance.measure('write-back-duration', 'write-back-start').duration.toFixed(0),
        ),
      });
    }
  }
}
