import { inject, injectable } from 'inversify';
import { CommonConnectors } from '../connectors';

@injectable()
export class BareboneStorage {
  constructor(@inject(CommonConnectors) private commonConnectors: CommonConnectors) {}

  async get<T = unknown>(key: string): Promise<T> {
    const value = await this.commonConnectors.storageGet({ key });
    return value?.[key];
  }

  set(key: string, value: unknown): Promise<void> {
    return this.commonConnectors.storageSet({
      data: { [key]: value },
    });
  }

  async safeGet<T>(key: string, logger?): Promise<Record<string, T>> {
    let value: Record<string, T> = {};
    try {
      const res = await this.get<Record<string, T>>(key);
      if (res != undefined && res != null) {
        value = res;
      }
    } catch (error) {
      (logger || console).error('error when fetching overrides from storage', {
        errorMessage: (error as Error).message,
      });
    }
    return value;
  }
}
