import { Container, multiInject, inject, injectable } from 'inversify';
import { Logger, LoggerFactory } from '@getvim-os/logger';
import {
  PayloadAction as ReduxPayloadAction,
  configureStore,
  ListenerMiddlewareInstance,
  ThunkDispatch,
  AnyAction,
  Action,
  TypedAddListener,
  TypedRemoveListener,
  addListener as reduxAddListener,
  removeListener as reduxRemoveListener,
} from '@reduxjs/toolkit';
import ehrState from './ehr/ehrState.slice';
import pendingEhrState from './ehr/pendingEhrState.slice';
import vaultState from './ehr/vault.slice';
import userState from './user/userState.slice';
import hubState from './hub/hubState.slice';
import applicationsState from './applications/applicationsState.slice';
import { ToolkitStore } from '@reduxjs/toolkit/dist/configureStore';
import { REDUX_LISTENER } from '../inversifyTokens';

export type PayloadAction<T> = ReduxPayloadAction<T> & {
  logger?: Logger;
};

const callActionWithLogger = (logger) => (next) => (action) => {
  action.logger = logger;
  const result = next(action);
  return result;
};

const loggerMiddleware =
  (logger = console) =>
  (_store) =>
    callActionWithLogger(logger);

export interface ThunkExtra {
  container: Container;
}

export const setupStore = (
  logger?,
  listenersMiddlewares?: ListenerMiddlewareInstance<
    any,
    ThunkDispatch<any, any, Action<any>>
  >['middleware'][],
  preloadedState?,
  inversifyContainer?,
) =>
  configureStore({
    reducer: {
      pendingEhrState,
      ehrState,
      vaultState,
      userState,
      hubState,
      applicationsState,
    },
    preloadedState,
    middleware: (getDefaultMiddleware) =>
      getDefaultMiddleware({
        serializableCheck: {
          ignoredActionPaths: ['logger'],
        },
        thunk: {
          extraArgument: {
            container: inversifyContainer,
          } satisfies ThunkExtra,
        },
      })
        .prepend(...(listenersMiddlewares ?? []))
        .concat(loggerMiddleware(logger)),
  });

type OSStore = ReturnType<typeof setupStore>;
export type RootState = ReturnType<OSStore['getState']>;
export type AppStore = ReturnType<typeof setupStore>;

export type Store = ToolkitStore<RootState> & {
  dispatch: ThunkDispatch<RootState, ThunkExtra, AnyAction>;
};

export type AppDispatch = Store['dispatch'];

export interface ReduxListener {
  listenerMiddleware: ListenerMiddlewareInstance<
    RootState,
    Store['dispatch'],
    ThunkExtra
  >['middleware'];
}

export const addListener = reduxAddListener as TypedAddListener<RootState, AppDispatch, ThunkExtra>;
export const removeListener = reduxRemoveListener as TypedRemoveListener<RootState, AppDispatch>;
export type ListenerArgs = Parameters<typeof addListener>[0];

@injectable()
export class OsState {
  private logger: Logger;
  public store: Store;

  constructor(
    @inject(Container) container: Container,
    @inject(LoggerFactory) loggerFactory: LoggerFactory,
    @multiInject(REDUX_LISTENER) reduxListeners: ReduxListener[],
  ) {
    this.logger = loggerFactory.create('OsState');
    this.store = setupStore(
      this.logger,
      reduxListeners.map((reduxListener) => reduxListener.listenerMiddleware),
      {},
      container,
    );
  }
}
