import { createSlice } from '@reduxjs/toolkit';
import { v4 as uuid } from 'uuid';
import { PayloadAction } from '..';
import { EhrUser, Organization, User } from '@getvim-os/types';

export type LoginUser = {
  ehrUser: EhrUser & {
    organization: Organization;
  };
};

export const initialState: User = {
  userSessionId: uuid(),
};

export const slice = createSlice({
  name: 'user',
  initialState,
  reducers: {
    login: (state, action: PayloadAction<Partial<LoginUser>>) => {
      if (action.payload.ehrUser) {
        const {
          ehrUser: { organization, ...user },
        } = action.payload;
        state.organization = organization;
        state.user = user;
      }
    },
    updateEhrUser: (state, action: PayloadAction<Partial<EhrUser>>) => {
      const { email, ...updatableFields } = action.payload;
      if (state.user) {
        state.user = {
          ...state.user,
          ...updatableFields,
        };
      }
    },
    setAccessToken: (state, action: PayloadAction<string>) => {
      state.accessToken = action.payload;
    },
    setHostname: (state, action: PayloadAction<string>) => {
      state.hostname = action.payload;
    },
    logout: () => {
      return initialState;
    },
  },
});

export const actions = slice.actions;

export default slice.reducer;
