import { createAsyncThunk, unwrapResult } from '@reduxjs/toolkit';

type CreateAsyncThunkParameters<Returned = unknown, ThunkArg = void> = Parameters<
  typeof createAsyncThunk<Returned, ThunkArg>
>;
type CreateAsyncThunkReturnValue<Returned = unknown, ThunkArg = void> = ReturnType<
  typeof createAsyncThunk<Returned, ThunkArg>
>;

/**
 * Inspired By https://stackoverflow.com/a/64697111
 */
export function createNonConcurrentAsyncThunk<Returned = unknown, ThunkArg = void>(
  ...args: CreateAsyncThunkParameters<Returned, ThunkArg>
): CreateAsyncThunkReturnValue<Returned, ThunkArg> {
  const [typePrefix, payloadCreator, options] = args;
  const pending: {
    actionAbort?: () => void;
  } = {};

  const internalThunk = createAsyncThunk<Returned, ThunkArg>(
    typePrefix + '-protected',
    payloadCreator,
  );

  return createAsyncThunk<Returned, ThunkArg>(
    typePrefix,
    async (arg, thunkAPI) => {
      if (pending.actionAbort) {
        pending.actionAbort();
      }
      const internalPromise = thunkAPI.dispatch(internalThunk(arg));
      const abort = internalPromise.abort;
      pending.actionAbort = abort;
      return await internalPromise.then(unwrapResult).finally(() => {
        if (pending.actionAbort === abort) {
          pending.actionAbort = undefined;
        }
      });
    },
    options,
  );
}
