/* eslint-disable unicorn/better-regex */
/* eslint-disable no-useless-escape */
import { z } from 'zod';

const forbiddenValues = ['&amp', '&lt', '&gt', '&quot', '&apos', '&#', ']]'];
const validEHRText = /^[a-zA-Z 0-9~`!@#$%^&*()-_+={[}\]|\\;:'",<>.\/?\n\t\r]+$/;

export const SafeTextSchema = z
  .string()
  .max(60_000)
  .refine(
    (value) =>
      value === '' || !forbiddenValues.some((forbiddenValue) => value.includes(forbiddenValue)),
    {
      message: 'Text contains Non supported characters',
    },
  )
  .refine((value) => value === '' || validEHRText.test(value), {
    message: 'Text contains non English characters',
  });
