export const PARENT_COMMUNICATOR = Symbol.for('PARENT_COMMUNICATOR');

type EHR_INSTANCE_ID = number;

export interface ParentMessage<T = unknown> {
  data: T;
  source: FrameSource;
}

export type FrameSource = WindowProxy | EHR_INSTANCE_ID | null;

export interface ParentCommunicator {
  setup(ehrInstanceId?: number): Promise<void>;
  onParentMessage(callback: (parentMessage: ParentMessage) => void): void;
  sendRawMessage(message: unknown, ports?: ReadonlyArray<MessagePort>): void;
  sendAsyncMessage(message: AsyncMessageToParentPayload): void;
  sendMessage<T = unknown>(message: MessageToParentPayload, timeout?: number): Promise<T>;
  sendMessageWithHandler<T>(
    message: MessageToParentPayload,
    handler: (data: T, isError: boolean) => void,
    timeout?: number,
  ): Promise<string>;
}

export interface SendMessageInput {
  type: string;
  payload?: unknown;
  timeout?: number;
  ehrInstanceId?: number;
}

export type AsyncMessageToParentPayload = Omit<MessageToParentPayload, 'type'> & { type?: string };

export interface MessageToParentPayload {
  type: string;
  payload?: unknown;
  messageId: string;
  ehrInstanceId?: number;
}

declare global {
  interface Window {
    $barebonePort?: MessagePort;
  }
}
