import { inject, injectable } from 'inversify';
import { OtelTracerProvider } from './otelTracerProvider';
import { VimTracer, VimTracerFactory } from './vimTracer';

@injectable()
export class VimTracerProvider {
  constructor(
    @inject(OtelTracerProvider) private otelTracerProvider: OtelTracerProvider,
    @inject(VimTracerFactory) private vimTracerFactory: VimTracerFactory,
  ) {}

  getTracer(...args: Parameters<OtelTracerProvider['getTracer']>): VimTracer {
    const originalTracer = this.otelTracerProvider.getTracer(...args);

    return this.vimTracerFactory(originalTracer);
  }
}
