import { WebConnectors } from '../..';
import { WebConnectorMessageTypes } from '../../enum';
import { UpdateElementInput } from './input';

export async function updateElement(
  this: WebConnectors,
  { selector, frameSelector, shadowSelector, attributes, style }: UpdateElementInput,
) {
  return await this.messaging.sendMessage({
    type: WebConnectorMessageTypes.updateElement,
    payload: { selector, frameSelector, shadowSelector, attributes, style },
  });
}
