import { WebConnectors } from '../..';
import { WebConnectorMessageTypes } from '../../enum';
import { WebRequestListener } from '../../types/web-request-listeners';
import { WebRequestResult } from '../../types/web-request-result';

export async function addWebRequestListener(
  this: WebConnectors,
  { urlPattern, handler }: WebRequestListener,
) {
  const listenerId = await this.messaging.sendMessageWithHandler<WebRequestResult>({
    type: WebConnectorMessageTypes.addWebRequestListener,
    payload: { urlPattern },
    handler,
  });
  return {
    remove: () => this.removeWebRequestListener({ listenerId: listenerId as string }),
    listenerId,
  };
}
